import telemetry from '@/modules/telemetry/telemetry'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import CurrencyService from '@/services/currency/CurrencyService'
import I18n from '@/modules/i18n'
import * as LABEL_KEYS from './constants/i18n'
import { Printer } from '@/services/printer/Printer'
import { TicketHelper } from './TicketHelper'
import { Line } from '@/services/printer/elements'
import TicketPreferences from '@/services/ticket/TicketPreferences'
import Order from 'models/Order'

export default class GlovoTicket {
  constructor(
    private order: OrderDetails,
    private preferences: TicketPreferences
  ) {}

  printTicket(copies = 1) {
    for (let i = 0; i < copies; i++) {
      this.print()
    }
  }

  private *generate() {
    const elements = this.generateCustomTicket()
    let previousElement = null
    for (const currentElement of elements) {
      const previousElementIsLine = previousElement instanceof Line
      const currentElementIsLine = currentElement instanceof Line
      const ignoreItIsDoubleLine = previousElementIsLine && currentElementIsLine
      if (!ignoreItIsDoubleLine) {
        yield currentElement
      }
      previousElement = currentElement
    }
  }

  private print() {
    try {
      const ticket = Array.from(this.generate())
      const response = Printer.printCustomTicket(ticket)
      if (!response.ok) {
        this.logPrintError(
          'print_custom_ticket_bad_response',
          response.message || 'custom print failed'
        )
        this.legacyPrintTicket()
      }
    } catch (e) {
      this.logPrintError('print_custom_ticket_error', e.message)
      this.legacyPrintTicket()
    }
  }

  private legacyPrintTicket() {
    const serializedLabels = JSON.stringify(this.getPrintLabels())
    const response = Printer.legacyPrintTicket(
      this.getSerializedPrintData(),
      serializedLabels
    )
    if (!response.ok) {
      this.logPrintError(
        'legacy_print_ticket_bad_response',
        response.message || 'printTicket failed'
      )
      this.legacyFallbackTicketPrint()
    }
  }

  private legacyFallbackTicketPrint() {
    const response = Printer.legacyPrint(
      this.getSerializedPrintData(),
      this.translate(LABEL_KEYS.SUMMARY_TOTAL) as string,
      this.translate(LABEL_KEYS.CUSTOMER_WANT_INVOICE) as string,
      this.preferences.currency
    )

    if (!response.ok) {
      this.logPrintError(
        'legacy_fallback_ticket_print_bad_response',
        response.message || 'fallback legacy print failed'
      )
    }
  }

  private generateCustomTicket() {
    const ticket = new TicketHelper(
      this.order,
      this.getPrintLabels(),
      this.preferences
    )
    if (this.order.orderInformation.isMarketplace) {
      return this.marketplaceTicket(ticket)
    }

    return this.nonMarketplaceOrderTicket(ticket)
  }

  private *marketplaceTicket(ticket: TicketHelper) {
    yield* ticket.logo()
    yield* ticket.longOrderCode()
    yield* ticket.paymentMethod()
    yield* ticket.customerInfo()
    yield* ticket.companyInfo()
    yield* ticket.newClient()
    yield* ticket.allergyInfo()
    yield* ticket.products()
    yield* ticket.cutlery()
    yield* ticket.priceBreakdown()
    yield* ticket.priceTotal()
    yield* ticket.customerCashAmount()
    yield* ticket.timestamp()
  }

  private *nonMarketplaceOrderTicket(ticket: TicketHelper) {
    yield* ticket.logo()
    yield* ticket.shortOrderCode()
    yield* ticket.paymentMethod()
    yield* ticket.estimation()
    yield* ticket.companyInfo()
    yield* ticket.newClient()
    yield* ticket.allergyInfo()
    yield* ticket.products()
    yield* ticket.cutlery()
    yield* ticket.priceTotal()
    yield* ticket.customerCashAmount()
    yield* ticket.timestamp()
  }

  private getSerializedPrintData() {
    const clone = JSON.parse(JSON.stringify(this.order))
    if (clone.customer && !clone.marketplace) {
      clone.customer.phoneNumber = ''
    }
    return JSON.stringify(clone)
  }

  private translate(key: string, values?: Record<string, any>) {
    return I18n.i18n.t(key, values) as string
  }

  private getPrintLabels() {
    return {
      currency: this.preferences.currency,
      ticketOwner: 'Glovo',
      customerName: this.translate(LABEL_KEYS.CUSTOMER_NAME),
      customerPhone: this.translate(LABEL_KEYS.CUSTOMER_PHONE),
      deliveryAddress: this.translate(LABEL_KEYS.DELIVERY_ADDRESS),
      customerWantInvoice: this.translate(LABEL_KEYS.CUSTOMER_WANT_INVOICE),
      companyName: this.translate(LABEL_KEYS.COMPANY_NAME),
      companyAddress: this.translate(LABEL_KEYS.COMPANY_ADDRESS),
      companyTaxId: this.translate(LABEL_KEYS.COMPANY_TAX_ID),
      productPrice: this.translate(LABEL_KEYS.PRODUCT_PRICE),
      minBasketSurcharge: this.translate(LABEL_KEYS.MIN_BASKET_SURCHARGE),
      deliveryFee: this.translate(LABEL_KEYS.DELIVERY_FEE),
      payByCash: this.getPayByCashLabel(),
      total: this.translate(LABEL_KEYS.SUMMARY_TOTAL),
      discounts: this.translate(LABEL_KEYS.SUMMARY_DISCOUNTS),
      customerCashAmount: this.getCustomerCashAmountLabel(),
      allergyInfo: this.translate(LABEL_KEYS.ALLERGY_INFO),
      clientName: this.translate(LABEL_KEYS.TICKET_CLIENT_NAME),
      newClient: this.translate(LABEL_KEYS.TICKET_CLIENT_NEW),
      pickupCode: this.translate(LABEL_KEYS.TICKET_PICKUP_CODE),
      orderCode: this.translate(LABEL_KEYS.TICKET_ORDER_CODE),
      pickupEstimation: this.translate(LABEL_KEYS.TICKET_PICKUP_ESTIMATION),
      pickupEstimationNotYetAvailable: this.translate(
        LABEL_KEYS.PICKUP_ESTIMATION_NOT_YET_AVAILABLE
      ),
      cutleryRequested: this.translate(LABEL_KEYS.CUTLERY_REQUEST_YES),
      cutleryNotRequested: this.translate(LABEL_KEYS.CUTLERY_REQUEST_NO)
    }
  }

  private getCustomerCashAmountLabel() {
    return this.translate(LABEL_KEYS.CUSTOMER_CASH_AMOUNT, {
      amount: CurrencyService.format(
        Order.paymentMethodIsCash(this.order)
          ? this.order.paymentDetails.customerCashPaymentAmount ?? 0
          : 0
      )
    }) as string
  }

  private getPayByCashLabel() {
    return this.translate(
      this.order.orderInformation.isMarketplace
        ? LABEL_KEYS.MP_PAY_BY_CASH
        : LABEL_KEYS.PAY_BY_CASH
    )
  }

  private logPrintError(eventName: string, errorMessage: string) {
    try {
      const options = {
        attr: {
          android: JSON.stringify(window.Android),
          order: this.order.id.toString()
        }
      }
      telemetry.Log.error(eventName, `error:\n${errorMessage}`, options)
    } catch (e) {
      console.error(e)
    }
  }
}
