import { Vue as _Vue } from 'vue/types/vue'
import { GlvModule, GlvModuleOptions } from 'glovo'
import { installErrorTrackerPlugin } from './plugins'
import { errorWhitelist } from './errors'
import AbstractErrorTracker from './errors/abstract-error-tracker'

export interface ObservabilityModuleOptions extends GlvModuleOptions {
  sentryDSN?: string
}

const glvModule: GlvModule<ObservabilityModuleOptions> = {
  install(Vue: typeof _Vue, options?: ObservabilityModuleOptions) {
    if (!options) {
      throw new Error('Options are required to install Observability module')
    } else {
      installErrorTrackerPlugin({
        sentryDSN: options.sentryDSN,
        errorWhitelist
      })
    }
  }
}

export { AbstractErrorTracker }
export default glvModule
