
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'

import { GlovoModal } from 'common/ui/modal'
import Icon from 'common/Icon.vue'

@Component({
  name: 'SelectProductModal',
  components: {
    GlovoModal,
    Icon
  }
})
export default class SelectProductModal extends Vue {
  @Prop() isVisible!: boolean
  @Prop() productName?: string
  @Prop() productPrice?: string

  get isModalDisplayed() {
    return this.isVisible
  }
  set isModalDisplayed(isDisplayed: boolean) {
    if (!isDisplayed) {
      this.closeModal()
    }
  }

  closeModal() {
    this.$emit('close')
  }
}
