import {
  ADD_CLOSED_STORE_ADDRESSES,
  AddClosedStoreAddresses,
  HIDE_STORE_CLOSING_MODAL,
  OPEN_STORE_ADDRESSES,
  OpenStoreAddressesSuccessPayload,
  RESET_STATE,
  SET_CLOSING_DAYS,
  SET_STATUS,
  SetClosingDaysPayload,
  SetStatusPayload,
  SHOW_STORE_CLOSING_MODAL,
  UPDATE_DISMISS_CHECK_IN_FAILURE,
  UPDATE_IS_CHECK_IN_LOADING,
  UPDATE_IS_CHECK_IN_PENDING,
  UpdateDismissCheckInFailurePayload,
  UpdateIsCheckInLoadingPayload,
  UpdateIsCheckInPendingPayload
} from './action-types'
import { State } from './state'
import { StoreAddressId } from '@/modules/store/data/resources'
import { IntervalWithReason } from '@/models/Interval'
import { DayDTO } from '@/api/user'

export const mutations = {
  [RESET_STATE](state: State) {
    state.isOpen = true
    state.closingDays = {}
    state.closedStoreAddressIds = []
    state.dataState.checkIn.isAllowedToChooseClosingType = false
    state.dataState.checkIn.shouldPlayNotification = false
    state.dataState.checkIn.isCheckInPending = false
    state.dataState.checkIn.isStartOfShift = false
    state.uiState.hasDismissingCheckInFailed = false
    state.uiState.shouldShowStoreClosingModal = false
  },
  [OPEN_STORE_ADDRESSES](
    state: State,
    payload: OpenStoreAddressesSuccessPayload
  ) {
    state.isOpen = true
    state.closedStoreAddressIds = openStoreAddressesMutation(
      state.closedStoreAddressIds,
      payload
    )
  },
  [ADD_CLOSED_STORE_ADDRESSES](state: State, payload: AddClosedStoreAddresses) {
    state.isOpen = false
    state.closedStoreAddressIds = closeStoreAddressesMutation(
      state.closedStoreAddressIds,
      payload
    )
  },
  [SET_CLOSING_DAYS](state: State, payload: SetClosingDaysPayload) {
    state.closingDays = setClosingDaysMutation(
      payload.storeAddressIds,
      payload.interval,
      state.closingDays
    )
  },
  [UPDATE_IS_CHECK_IN_LOADING](
    { uiState }: State,
    payload: UpdateIsCheckInLoadingPayload
  ) {
    uiState.isCheckinConfirmationLoading = payload
  },
  [UPDATE_IS_CHECK_IN_PENDING](
    state: State,
    payload: UpdateIsCheckInPendingPayload
  ) {
    state.dataState.checkIn.isCheckInPending = payload
  },
  [SET_STATUS](state: State, payload: SetStatusPayload) {
    state.isOpen = payload.open
    state.dataState.checkIn = payload.checkIn
  },
  [UPDATE_DISMISS_CHECK_IN_FAILURE](
    { uiState }: State,
    payload: UpdateDismissCheckInFailurePayload
  ) {
    uiState.hasDismissingCheckInFailed = payload
  },
  [SHOW_STORE_CLOSING_MODAL]({ uiState }: State) {
    uiState.shouldShowStoreClosingModal = true
  },
  [HIDE_STORE_CLOSING_MODAL]({ uiState }: State) {
    uiState.shouldShowStoreClosingModal = false
  }
}

export function setClosingDaysMutation(
  storeAddressIds: StoreAddressId[],
  interval: IntervalWithReason,
  closingDays: Record<StoreAddressId, DayDTO[]>
): Record<StoreAddressId, DayDTO[]> {
  const newState: Record<StoreAddressId, DayDTO[]> = { ...closingDays }
  storeAddressIds.forEach(id => {
    if (newState[id]) {
      newState[id].push(interval)
    } else {
      newState[id] = [interval]
    }
  })
  return newState
}

export function openStoreAddressesMutation(
  storeAddressIds: StoreAddressId[],
  payload: StoreAddressId[]
) {
  return storeAddressIds.filter(
    storeAddressId => !payload.includes(storeAddressId)
  )
}

export function closeStoreAddressesMutation(
  storeAddressIds: StoreAddressId[],
  payload: StoreAddressId[]
) {
  const newState = storeAddressIds.concat(payload)
  const newStateWithoutDuplicates = new Set(newState)
  return [...newStateWithoutDuplicates]
}
