import { GlvModuleOptions } from 'glovo'

export default function (
  options: GlvModuleOptions | undefined,
  requiredDeps: string[]
) {
  if (!options || !options.deps) {
    throw new Error(
      `Dependencies: ${requiredDeps.join(',')} are required for module`
    )
  }
  const deps = options.deps
  requiredDeps.forEach(dep => {
    if (!Object.keys(deps).includes(dep)) {
      throw new Error(`Dependency: ${dep} is required for module`)
    }
  })
}
