
import Vue from 'vue'
import { Getter } from 'vuex-class'
import { Prop, Component } from 'vue-property-decorator'
import {
  GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER,
  GET_CURRENT_STORE_ADMIN_GETTER
} from '@/modules/authentication'
import { authentication } from '@/modules/authentication/store/namespace'
import { StoreAddressId, StoreAdmin } from '@/modules/store/data/resources'
import Order from 'models/Order'
import { orderManagement } from '@/modules/orders/store/namespace'
import { GET_ORDERS_GETTER } from '@/modules/store/data/getters'
import { GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER } from '@/modules/orders/store/getters'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import User from 'models/User'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import { OrderTypes } from '@/modules/store/data/state'
import { OrderId } from '@/modules/store'
import GlovoBadge from 'common/ui/GlovoBadge.vue'
import Pair from 'common/Pair.vue'
import Countdown from '@/modules/orders/components/common/Countdown.vue'
import OrderReadyButton from '@/modules/orders/components/common/OrderReadyButton.vue'
import { OrderStatus } from '@/modules/orders/constants'
import BundleInfo from '../../common/BundleInfo.vue'
import {
  GET_ORDER_DETAILS,
  GetOrderDetailsPayload
} from '@/modules/orders/store'
import { StoreAction } from '@/modules/store/types/store'

@Component({
  components: {
    BundleInfo,
    GlovoBadge,
    Pair,
    Countdown,
    OrderReadyButton
  }
})
export default class OrderDetailsInformation extends Vue {
  @Prop() orderDetails!: OrderDetails

  @authentication.Getter(GET_AVAILABLE_STORE_ADDRESS_IDS_GETTER)
  availableStoreAddresses!: StoreAddressId[]
  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin!: StoreAdmin | null

  @orderManagement.Getter(GET_IS_KITCHEN_CODE_STORE_ADDRESS_GETTER)
  isKitchenCodeStoreAddress!: (id: StoreAddressId) => boolean

  @Getter(GET_ORDERS_GETTER)
  allOrders!: Record<OrderId, OrderTypes>

  @Getter(GET_TOGGLES_GETTER)
  toggles!: Record<FeatureToggle, boolean>

  @orderManagement.Action(GET_ORDER_DETAILS)
  getOrderDetails!: StoreAction<GetOrderDetailsPayload>

  async goToOrder(bundledOrder: OrderDetails) {
    if (!bundledOrder) {
      return
    }
    const { id } = bundledOrder
    const { storeAddressId } = this.allOrders[id]

    this.$router.push({
      name: 'details',
      params: {
        id: `${id}`,
        storeAddressId: `${storeAddressId}`
      }
    })
    this.getOrderDetails({
      orderId: id,
      storeAddressId
    })
  }

  get user(): User {
    return User.parse(this.storeAdmin || {})
  }

  get shouldShowCustomerAndStoreInformation() {
    return !this.user.isMCD() && this.orderDetails.customerDetails
  }

  get hasEstimatedTimeOfPickup() {
    return Order.hasEstimatedTimeOfPickup(this.orderDetails)
  }

  get shouldShowOrderReadyButton() {
    return (
      this.toggles.FE_PARTNERS_ORDER_READY_BUTTON_ENABLED &&
      this.isAccepted &&
      this.orderHasNotBeenDelivered &&
      !this.orderDoesNotRequireACourier
    )
  }

  get orderDoesNotRequireACourier() {
    return this.isMarketplace || this.isPickedUpByCustomer
  }

  get orderHasNotBeenDelivered() {
    return (
      this.orderDetails.statusDetails.currentStatus !=
      OrderStatus.DELIVERED_STATUS
    )
  }

  get isMarketplace() {
    return this.orderDetails.orderInformation.isMarketplace
  }

  get isAccepted() {
    return Order.isAccepted(this.orderDetails)
  }

  get displayPickupCode() {
    return Order.getShortCode({
      code: this.orderDetails.orderInformation.code,
      pickupCode: this.orderDetails.orderInformation.pickupCode,
      storeTrackingCode: this.orderDetails.orderInformation.storeTrackingCode,
      usesKitchenCode: this.isKitchenCodeStoreAddress(
        this.orderDetails.storeDetails.storeAddressId
      )
    })
  }

  get displayOriginalPickupTime() {
    return (
      !!this.orderDetails.statusDetails.originalPickupTimeRange &&
      this.toggles.FE_PARTNERS_ORIGINAL_PICK_UP_RANGE_ENABLED
    )
  }

  get formatOriginalPickupTime() {
    const range = this.orderDetails.statusDetails.originalPickupTimeRange
    return range ? `${range.start} - ${range.end}` : null
  }

  get isPickedUpByCustomer() {
    return this.orderDetails.orderInformation.isPickedUpByCustomer
  }

  get isPendingAndCourierNearby() {
    return (
      Order.isPending(this.orderDetails) &&
      Order.isCollectorNearby(this.orderDetails) &&
      !this.isMarketplace &&
      !this.isPickedUpByCustomer
    )
  }

  get isAcceptedAndCourierNearby() {
    return (
      Order.isAccepted(this.orderDetails) &&
      Order.isCollectorNearby(this.orderDetails) &&
      !this.isMarketplace &&
      !this.isPickedUpByCustomer
    )
  }

  get isOrderPaymentMethodCash() {
    return Order.paymentMethodIsCash(this.orderDetails)
  }

  get isBundled() {
    return Order.isBundled(this.orderDetails)
  }

  get bundleOrder() {
    if (this.isBundled) {
      return this.allOrders[
        this.orderDetails.orderInformation.bundledOrders[0].orderId
      ]
    }
  }

  get isAndroidInterfaceEnabled() {
    return window.Android
  }

  get orderFinished() {
    return Order.isFinished(this.orderDetails)
  }
}
