
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import {
  SectionMenuItem,
  CollectionMenuItem,
  CollectionGroupMenuItem,
  MenuItemType,
  UndefinableMenuItemType,
  CollectionList
} from './collection-list-types'

@Component({
  name: 'CollectionDialog'
})
export default class CollectionDialog extends Vue {
  @Prop({ default: () => [] }) collectionList!: CollectionList
  @Prop({ default: false }) isVisible!: boolean
  @Prop({ default: '0' }) selectedItemId!: string

  closeDialog() {
    this.$emit('close')
  }

  itemClicked(item: UndefinableMenuItemType) {
    if (item.type === MenuItemType.SECTION) {
      this.emitSectionClick(item)
    } else if (item.type === MenuItemType.COLLECTION) {
      this.emitCollectionClick(item)
    } else {
      this.emitCollectionGroupClick(item)
    }
  }

  emitSectionClick(item: SectionMenuItem) {
    this.$emit('update', MenuItemType.SECTION, item.name, item.id.toString())
  }

  emitCollectionClick(item: CollectionMenuItem) {
    this.$emit('update', MenuItemType.COLLECTION, item.name, item.id.toString())
  }

  emitCollectionGroupClick(item: CollectionGroupMenuItem) {
    this.$emit(
      'update',
      MenuItemType.COLLECTION_GROUP,
      item.title,
      item.id.toString()
    )
  }
}
