<template>
  <div
    :class="['glv-icon', isLarge ? 'glv-icon-large' : '']"
    :style="{ backgroundImage: source }"
  ></div>
</template>
<script>
export default {
  name: 'Icon',
  props: {
    name: {
      type: String,
      default: ''
    },
    extension: {
      type: String,
      default: 'svg'
    },
    size: {
      type: String,
      default: ''
    }
  },
  computed: {
    source() {
      const img = require(`@/assets/images/${this.name}.${this.extension}`)
      return `url(${img})`
    },
    isLarge() {
      return this.size === 'large'
    }
  }
}
</script>
<style lang="scss">
@import '~styles';

.glv-icon {
  @include xs-icon-size;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  &-large {
    @include m-icon-size;
  }
}
</style>
