import { Howl, Howler } from 'howler'
import telemetry from '@/modules/telemetry/telemetry'
import { ONE_MINUTE_IN_MILLIS } from '@/modules/core/helpers'

const AUDIO_ENGINE_MONITORING_DELAY_IN_MILLIS = ONE_MINUTE_IN_MILLIS

export enum Sounds {
  pending = '/static/audio/pending-orders-notif-demonstrative.mp3',
  courier_nearby = '/static/audio/courier-nearby-notif.mp3',
  cancelled = '/static/audio/cancelled-orders-notification.mp3',
  checkin = '/static/audio/checkin.mp3'
}

export default class AudioService {
  private constructor() {
    /* singleton pattern */
  }

  private static soundCollection: { [key in Sounds]: Howl }
  private static isInitialised = false

  private static initializeSound(sound: Sounds, loop = false): Howl {
    return new Howl({
      src: [sound],
      loop: loop
    })
  }

  private static monitorAudioState(): void {
    if (window.hasOwnProperty('AudioContext')) {
      const audio = new AudioContext()
      const state = audio.state
      telemetry.Metric.count('audio_state', 1, {
        tags: {
          state
        }
      })
      if ('close' in audio) {
        audio.close()
      }
    } else {
      const audioState = (Howler as any).state || 'unknown'
      const audioEngine = Howler.usingWebAudio ? 'webaudio' : 'html5'
      telemetry.Metric.count('audio_state_fallback', 1, {
        tags: {
          state: audioState,
          engine: audioEngine
        }
      })
    }
  }

  public static start(): void {
    if (this.isInitialised) return
    else this.isInitialised = true
    setTimeout(this.monitorAudioState, AUDIO_ENGINE_MONITORING_DELAY_IN_MILLIS)
    this.soundCollection = {
      [Sounds.pending]: this.initializeSound(Sounds.pending),
      [Sounds.courier_nearby]: this.initializeSound(Sounds.courier_nearby),
      [Sounds.cancelled]: this.initializeSound(Sounds.cancelled),
      [Sounds.checkin]: this.initializeSound(Sounds.checkin, true)
    }
  }

  public static stop(): void {
    this.stopAllSounds()
    for (const sound in this.soundCollection) {
      this.soundCollection[sound as Sounds].unload()
      delete this.soundCollection[sound as Sounds]
    }
    this.isInitialised = false
  }

  public static playSound(sound: Sounds): void {
    this.soundCollection[sound].stop()
    this.soundCollection[sound].play()
  }

  public static playSoundForSeconds(sound: Sounds, seconds: number) {
    this.soundCollection[sound].stop()
    this.soundCollection[sound].play()
    const secondsToMillis = seconds * 1000
    window.setInterval(
      () => this.soundCollection[sound].stop(),
      secondsToMillis
    )
  }

  public static stopSound(sound: Sounds): void {
    if (!this.soundCollection || !this.soundCollection[sound]) return
    this.soundCollection[sound].stop()
  }

  public static stopAllSounds(): void {
    for (const sound in this.soundCollection) {
      this.soundCollection[sound as Sounds].stop()
    }
  }

  public static isPlaying(sound: Sounds) {
    return this.soundCollection[sound as Sounds].playing()
  }
}
