
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import Order from 'models/Order'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import OrderDetailsMessages from './OrderDetailsMessages.vue'

@Component({
  name: 'OrderDetailsDescription',
  components: {
    OrderDetailsMessages
  }
})
export default class OrderDetailsDescription extends Vue {
  @Prop() details!: OrderDetails

  get hasMessage(): boolean {
    return (
      this.hasCutleryRequestInfo ||
      this.isCashPaymentMethod ||
      this.hasAllergyInfo
    )
  }

  get hasCutleryRequestInfo(): boolean {
    return this.details.orderInformation.isCutleryRequested != null
  }

  get isCashPaymentMethod(): boolean {
    return !!Order.paymentMethodIsCash(this.details)
  }

  get hasAllergyInfo(): boolean {
    return !!this.details.orderInformation.allergyInfo
  }
}
