import { StoreProductDTO } from '../api/dtos'
import { ProductId, StoreId } from '@/modules/store/data/resources'

interface IStoreProduct extends Omit<StoreProductDTO, 'productExternalId'> {
  id: ProductId
}

export class StoreProductVM implements IStoreProduct {
  constructor(private readonly product: StoreProductDTO) {
    this.product = product || {}
  }

  get id() {
    return this.product.productExternalId
  }

  get name() {
    return this.product.name
  }

  get topSeller() {
    return this.product.topSeller
  }
}

export type StoreProductsList = StoreProductVM[]
export type ProductsMapByStoreId = Record<StoreId, StoreProductsList>
export type ProductsMapById = Record<ProductId, StoreProductVM>
