import { StoreAddressId } from './store-address'

export enum RequestPriceUpdateType {
  PRODUCT = 'PRODUCT',
  ATTRIBUTE = 'ATTRIBUTE'
}

export enum RequestPriceUpdateStatus {
  PENDING = 'PENDING',
  APPROVED = 'APPROVED',
  DECLINED = 'DECLINED'
}

export type RequestPriceUpdateId = number

export interface RequestPriceUpdate {
  id: RequestPriceUpdateId
  storeAddressId: StoreAddressId
  itemId: number
  name: string
  description: string
  actualPrice: number
  newPrice: number
  type: RequestPriceUpdateType
  creationDate: number
  statusDate: number
  status: RequestPriceUpdateStatus
}
