import {
  UPDATE_ONLINE_STATUS,
  UpdateOnlineStatusPayload,
  Action,
  UPDATE_IS_OFFLINE_BANNER_VISIBLE,
  LOG_REQUEST,
  UpdateIsOfflineBannerVisible,
  LoggedRequest,
  CLEAR_LOGGED_REQUESTS,
  ENABLE_SCROLL,
  DISABLE_SCROLL,
  UPDATE_REFRESHING_INTERVAL,
  SET_IS_SEARCHING,
  SetIsSearchingPayload
} from '@/modules/core/store/action-types'
import { ActionContext } from 'glovo'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FEATURE_TOGGLE_POLLING_INTERVAL_IN_MILLIS } from '@/modules/feature-toggles/store/actions'
import {
  millisToSeconds,
  minutesToMillis,
  ONE_MINUTE_IN_MILLIS
} from '@/modules/core/helpers'
import {
  updateMetaRefreshContent,
  DEFAULT_APP_REFRESHING_INTERVAL,
  readMetaRefreshInterval
} from '@/modules/shared'
import { ORDERS_ROUTE } from '@/modules/orders/orders-routes'

const FT_INTERVAL_GUARD_IN_MILLIS =
  FEATURE_TOGGLE_POLLING_INTERVAL_IN_MILLIS + ONE_MINUTE_IN_MILLIS

export function actionsFactory() {
  return {
    [UPDATE_ONLINE_STATUS](
      { commit }: ActionContext<Action>,
      payload: UpdateOnlineStatusPayload
    ) {
      commit(UPDATE_ONLINE_STATUS, payload)
    },
    [UPDATE_IS_OFFLINE_BANNER_VISIBLE](
      { commit }: ActionContext<Action>,
      payload: UpdateIsOfflineBannerVisible
    ) {
      commit(UPDATE_IS_OFFLINE_BANNER_VISIBLE, payload)
    },
    [LOG_REQUEST]({ commit }: ActionContext<Action>, payload: LoggedRequest) {
      commit(LOG_REQUEST, payload)
    },
    [CLEAR_LOGGED_REQUESTS]({ commit }: ActionContext<Action>) {
      commit(CLEAR_LOGGED_REQUESTS)
    },
    [ENABLE_SCROLL]({ commit }: ActionContext<Action>) {
      commit(ENABLE_SCROLL)
    },
    [DISABLE_SCROLL]({ commit }: ActionContext<Action>) {
      commit(DISABLE_SCROLL)
    },
    [SET_IS_SEARCHING](
      { commit }: ActionContext<Action>,
      payload: SetIsSearchingPayload
    ) {
      commit(SET_IS_SEARCHING, payload)
    },
    [UPDATE_REFRESHING_INTERVAL]({ rootGetters }: ActionContext<Action>) {
      const appRefreshingInterval =
        rootGetters[GET_TOGGLES_GETTER] &&
        (rootGetters[GET_TOGGLES_GETTER]
          .FE_PARTNERS_APP_REFRESHING_INTERVAL_IN_MINUTES as number)
      if (!appRefreshingInterval) return

      const appRefreshingIntervalInMillis = minutesToMillis(
        appRefreshingInterval
      )

      if (appRefreshingIntervalInMillis > FT_INTERVAL_GUARD_IN_MILLIS) {
        updateRefreshingTagIfNecessary(appRefreshingIntervalInMillis)
      } else {
        updateRefreshingTagIfNecessary(FT_INTERVAL_GUARD_IN_MILLIS)
      }
    }
  }
}

function updateRefreshingTagIfNecessary(desiredIntervalInMillis: number) {
  const currentRefreshingInterval = readMetaRefreshInterval()
  if (!currentRefreshingInterval) {
    updateMetaRefreshContent(
      `${DEFAULT_APP_REFRESHING_INTERVAL};url=/${ORDERS_ROUTE}`
    )
  }

  const desiredInterval = millisToSeconds(desiredIntervalInMillis)
  if (desiredInterval !== currentRefreshingInterval) {
    updateMetaRefreshContent(`${desiredInterval};url=/${ORDERS_ROUTE}`)
  }
}
