
import Component from 'vue-class-component'
import { Prop, Mixins, Watch } from 'vue-property-decorator'
import { WatchedModel } from '@/components/mixins'
import GlovoCheckbox from './GlovoCheckbox.vue'
import { Padding } from '../../../types'

type Option = Record<string, any> & { index: number }
type OptionMap = Record<string, Option>

@Component({ name: 'GlovoCheckboxGroup', components: { GlovoCheckbox } })
export default class GlovoCheckboxGroup extends Mixins(WatchedModel) {
  private optionsMap: OptionMap = {}

  @Prop() padding!: Padding
  @Prop({ required: true }) options!: Option[]
  @Prop({ default: 'id' }) valueProp!: string
  @Prop({ default: 'name' }) labelProp!: string
  @Prop({ default: 'common.select.all' }) selectAllLabel!: string
  @Prop({ default: true }) selectAllEnabled!: boolean

  get selectedOptionValues(): string[] {
    return this.internalValue
  }

  set selectedOptionValues(optionValues) {
    this.internalValue = this.sortSelectedValuesByOptionsOrder(optionValues)
  }

  get optionValues() {
    return this.options?.map(this.getOptionValue)
  }

  get selectedOptionsCount() {
    return this.selectedOptionValues?.length
  }

  get optionsCount() {
    return this.options?.length
  }

  get isAllChecked() {
    return this.selectedOptionsCount === this.optionsCount
  }

  get isIndeterminate() {
    return (
      this.selectedOptionsCount > 0 &&
      this.selectedOptionsCount < this.optionsCount
    )
  }

  getOptionIndexForValue(optionValue: string) {
    return this.optionsMap[optionValue]?.index ?? -1
  }

  getOptionValue = (option: Option): string => {
    return typeof option === 'object' ? option[this.valueProp] : option
  }

  sortSelectedValuesByOptionsOrder(selectedValues: string[]) {
    if (!Array.isArray(selectedValues)) {
      return []
    }

    return selectedValues.sort((value1, value2) => {
      const option1Index = this.getOptionIndexForValue(value1)
      const option2Index = this.getOptionIndexForValue(value2)
      return option1Index - option2Index
    })
  }

  handleCheckAllUpdate(event: { target: HTMLInputElement }) {
    this.selectedOptionValues = event?.target?.checked ? this.optionValues : []
  }

  @Watch('options', { immediate: true })
  updateOptions() {
    this.optionsMap = this.options.reduce((optionsMap, option, index) => {
      optionsMap[this.getOptionValue(option)] = { ...option, index }
      return optionsMap
    }, {} as OptionMap)
  }
}
