
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { GlovoModal } from 'common/ui/modal'
import Icon from 'common/Icon.vue'
import OrderProductEdit from './OrderProductEdit.vue'
import { orderManagement } from '@/modules/orders/store/namespace'
import { CLOSE_ORDER_PRODUCT_EDIT_MODAL } from '@/modules/orders/store/action-types'
import { GET_ORDER_PRODUCT_EDIT_MODAL_GETTER } from '@/modules/orders/store/getters'
import { OrderDetails } from '@/modules/store/data/resources/order-details'
import { StoreAction } from '@/modules/store/types/store'
import type { BoughtProductId } from '@/modules/store/data/resources/order-details'

@Component({
  name: 'OrderProductEditModal',
  components: {
    Icon,
    GlovoModal,
    OrderProductEdit
  }
})
export default class OrderProductEditModal extends Vue {
  @Prop() order!: OrderDetails

  @orderManagement.Getter(GET_ORDER_PRODUCT_EDIT_MODAL_GETTER)
  orderProductEditModal!: {
    isDisplayed: boolean
    productId: BoughtProductId | null
  }

  @orderManagement.Action(CLOSE_ORDER_PRODUCT_EDIT_MODAL)
  closeOrderProductEditModal!: StoreAction<void>

  get productId() {
    return this.orderProductEditModal.productId
  }

  get storeAddressId() {
    return this.order.storeDetails.storeAddressId
  }

  get product() {
    return this.order.products.filter(
      product => product.id === this.productId
    )[0]
  }

  get productName() {
    return this.product?.name
  }

  get isDisplayed() {
    return !!this.productId && this.orderProductEditModal.isDisplayed
  }

  set isDisplayed(value: boolean) {
    if (this.isDisplayed && value === false) {
      this.closeOrderProductEditModal()
    }
  }

  removeProductFromOrder() {
    this.$emit('remove', this.product)
    this.closeOrderProductEditModal()
  }

  editProductQuantity() {
    this.$emit('edit-quantity', this.product.id)
    this.closeOrderProductEditModal()
  }

  replaceProduct() {
    this.$emit('replace', this.product)
    this.closeOrderProductEditModal()
  }

  editProductAttributes() {
    this.$emit('edit-attributes', this.product.id)
    this.closeOrderProductEditModal()
  }
}
