import { Store } from 'vuex'
import { State } from '@/modules/store'
import { RequestInterceptor } from '@/modules/http'
import { JWTHelper } from '../helpers/jwt/JWTHelper'
import {
  getExpirationTime,
  isRefreshCall,
  isRefreshInProgress,
  refreshToken
} from '@/modules/authentication/interceptors/helpers'

export function oldTokenRefreshInterceptor(
  store: Store<State>
): RequestInterceptor {
  return async requestConfig => {
    const { url } = requestConfig
    const expirationTime = getExpirationTime(store)
    if (
      !isRefreshCall(url) &&
      expirationTime !== null &&
      JWTHelper.tokenIsGoingToExpire(expirationTime)
    ) {
      await handleRefresh(store)
    }
    return requestConfig
  }
}

async function handleRefresh(store: Store<State>) {
  if (!isRefreshInProgress(store)) {
    await refreshToken(store)
  } else {
    let refreshInProgress = isRefreshInProgress(store)
    // Queue incoming requests while refresh is in progress
    while (refreshInProgress) {
      await new Promise(resolve => setTimeout(resolve, 100))
      refreshInProgress = isRefreshInProgress(store)
    }
  }
}
