import cloneDeep from 'lodash/cloneDeep'
import {
  DISPLAY_REPORTS_HISTORY,
  DISPLAY_REPORTS_DASHBOARD,
  DISPLAY_REPORTS_INVOICES
} from '@/modules/store/services/display-types'
import { Category, INVOICES_SUBCATEGORY_NAME, ViewMode } from './Category'

interface CategoryFilters {
  viewMode?: ViewMode
}

export default class Categories {
  private categories: Category[] = cloneDeep(INITIAL_CATEGORIES)

  getEnabledCategories(categoryFilters?: CategoryFilters) {
    const { viewMode = undefined } = categoryFilters || {}
    let enabledCategories = this.categories.filter(
      category => !category.disabled
    )

    if (viewMode) {
      enabledCategories = filterCategoriesForViewMode(
        enabledCategories,
        viewMode
      )
    }

    return enabledCategories
  }

  getCategoryWithName(categoryName: Category['name']) {
    return this.categories.find(category => category.name === categoryName)
  }

  getDefaultCategory(): Category {
    return this.getCategoryWithName(DEFAULT_CATEGORY_NAME)!
  }

  enableCategoryWithName(categoryName: Category['name']) {
    const category = this.getCategoryWithName(categoryName)
    if (!category) {
      throw new Error(
        `[enableCategoryWithName]: Category ${categoryName} was not found`
      )
    }

    category.disabled = false
  }
}

const filterCategoriesForViewMode = (
  categories: Category[],
  viewMode: ViewMode
) => {
  return (categories || []).filter(category => {
    const { disabledViews } = category
    return !Array.isArray(disabledViews) || !disabledViews.includes(viewMode)
  })
}

