
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { OrderDetails } from '@/modules/store/data/resources/order-details'

@Component({
  name: 'OrderDetailsMap'
})
export default class OrderDetailsMap extends Vue {
  @Prop() orderDetails!: OrderDetails

  get deliveryAddressPosition() {
    if (this.orderDetails.deliveryAddressDetails) {
      return {
        lat: this.orderDetails.deliveryAddressDetails.latitude,
        lng: this.orderDetails.deliveryAddressDetails.longitude
      }
    }
    return null
  }

  get gmapOptions() {
    return {
      zoomControl: true,
      mapTypeControl: false,
      scaleControl: false,
      streetViewControl: false,
      rotateControl: false,
      fullscreenControl: false,
      disableDefaultUi: false
    }
  }

  get flagIcon() {
    return {
      url: require('@/assets/images/delivery_flag.svg')
    }
  }
}
