import {
  buildSmartUrl,
  CLOUDINARY_THUMBNAIL_SETTINGS,
  ImageUrlSources
} from './Images'
import { plainToClass } from 'class-transformer'
import {
  Product,
  ProductCustomization,
  Attribute,
  AttributeGroup
} from '@/modules/store/data/resources'

export enum OrderProductMode {
  default = 'MODE_DEFAULT',
  removed = 'MODE_REMOVED',
  replaced = 'MODE_REPLACED',
  pick = 'MODE_PICK'
}

export const PRODUCT_IMAGE_PLACEHOLDER = 'placeholder/placeholder_glovo.svg'

export default class OrderProduct implements Product {
  static parse(orderData = {}) {
    return plainToClass(OrderProduct, orderData)
  }
  name = ''
  description = ''
  quantity = 1
  id = -1
  imgUrl: string | null = ''
  imageUrl: string | null = ''
  price = ''
  productId = -1
  note = ''
  imageServiceId: string | null = ''
  mode: OrderProductMode = OrderProductMode.default
  attributeGroups: AttributeGroup[] | null = null
  originalCustomerProductRequest: Product | null = null
  customizations: ProductCustomization[] | null = null
  persoAttributes: Attribute[] | null = null

  parse(product: Product) {
    this.name = product.name
    this.description = product.description
    this.quantity = product.quantity != null ? product.quantity : 1
    this.id = product.id || -1
    this.price = product.price
    this.productId = product.productId
    this.imageServiceId = product.imageServiceId || null
    this.imageUrl = product.imageUrl || null
    this.imgUrl = product.imgUrl || null
    this.mode = product.mode || OrderProductMode.default
    this.customizations = product.customizations || null
    this.attributeGroups = product.attributeGroups || []
    this.originalCustomerProductRequest =
      product.originalCustomerProductRequest || null
    return this
  }

  clone() {
    return Object.assign(Object.create(Object.getPrototypeOf(this)), this)
  }

  setReference(product: Product) {
    this.originalCustomerProductRequest = product
  }

  hasReference() {
    return (
      this.originalCustomerProductRequest &&
      Object.keys(this.originalCustomerProductRequest).length > 0
    )
  }

  initialId() {
    const originalCustomerProductRequestId = this.originalCustomerProductRequest
      ? this.originalCustomerProductRequest.id
      : this.id
    return this.hasReference() ? originalCustomerProductRequestId : this.id
  }

  default() {
    return this.mode === OrderProductMode.default
  }

  toggleDefault() {
    this.mode = OrderProductMode.default
  }

  pickMode() {
    this.mode = OrderProductMode.pick
  }

  formatDescription(customizations: ProductCustomization[] | null) {
    if (!customizations) {
      return ''
    }
    const desc: string[] = []
    customizations.forEach(({ quantity, name }) => {
      if (quantity === 0) {
        return
      }
      let text = ''
      if (quantity > 1) {
        text += `${quantity}x `
      }
      text += name
      desc.push(text)
    })
    return desc.join(', ')
  }

  referenceName() {
    return this.originalCustomerProductRequest!.name
  }

  referencePrice() {
    return this.originalCustomerProductRequest!.price
  }

  referenceQuantity() {
    return this.originalCustomerProductRequest!.quantity
  }

  referenceDescription() {
    return (
      this.formatDescription(
        this.originalCustomerProductRequest!.customizations
      ) || this.originalCustomerProductRequest!.description
    )
  }

  referenceImg(isThumbnail: boolean) {
    if (this.originalCustomerProductRequest) {
      return this.generateProductImageSource(
        this.originalCustomerProductRequest,
        isThumbnail
      )
    }
    return ''
  }

  img(isThumbnail: boolean) {
    return this.generateProductImageSource(this, isThumbnail)
  }

  generateProductImageSource(item: Product, isThumbnail: boolean) {
    if (item.imageServiceId) {
      return buildSmartUrl(
        item.imageServiceId,
        ImageUrlSources.cloudinary,
        isThumbnail ? CLOUDINARY_THUMBNAIL_SETTINGS : ''
      )
    } else if (item.imageUrl) {
      return buildSmartUrl(item.imageUrl, ImageUrlSources.external, null)
    } else if (item.imgUrl) {
      return buildSmartUrl(item.imgUrl, ImageUrlSources.external, null)
    } else {
      return buildSmartUrl(
        PRODUCT_IMAGE_PLACEHOLDER,
        ImageUrlSources.local,
        null
      )
    }
  }

  isPlaceholder() {
    return this.needsAPlaceholder(this)
  }

  isReferencePlaceholder() {
    if (this.originalCustomerProductRequest) {
      return this.needsAPlaceholder(this.originalCustomerProductRequest)
    }
  }

  needsAPlaceholder(item: Product) {
    return !(item.imageServiceId || item.imgUrl || item.imageUrl)
  }

  picking() {
    return this.mode === OrderProductMode.pick
  }

  modifications() {
    const modificationList: string[] = []
    if (!this.originalCustomerProductRequest) {
      return modificationList
    }
    if (this.quantity !== this.originalCustomerProductRequest.quantity) {
      modificationList.push('QUANTITY')
    }
    if (this.productId !== this.originalCustomerProductRequest.productId) {
      modificationList.push('ID')
    }
    return modificationList
  }

  hasModifications() {
    const modifications = this.modifications()
    return modifications.length > 0
  }

  hasSpecificModification(modification: string) {
    const modifications = this.modifications()
    return modifications.includes(modification)
  }
}
