import { StoreProductsList } from '../../models'

export const filterProductsByQuery = (
  products: StoreProductsList,
  query: string
): StoreProductsList => {
  if (!Array.isArray(products) || typeof query !== 'string' || !query) {
    return products
  }

  const searchRegExp = new RegExp(query, 'i')
  return products.filter(product => product.name.match(searchRegExp))
}
