export const disableBodyScroll = () => {
  if (typeof document !== 'object' || typeof window !== 'object') {
    return
  }

  const scrollPosition = window.scrollY

  fixBodyAndHideOverflow()
  setScrollPosition(scrollPosition)
}

const fixBodyAndHideOverflow = () => {
  document.body.style.position = 'fixed'
  document.body.style.width = '100%'
  document.body.style.overflow = 'hidden'
}

const setScrollPosition = (position: number) => {
  document.body.style.top = `-${position}px`
}

export const enableBodyScroll = () => {
  if (typeof document !== 'object' || typeof window !== 'object') {
    return
  }

  const scrollPosition = document.body.style.top
  restoreBodyPositionAndOverflow()
  restoreScrollPosition(parseInt(scrollPosition || '0'))
}

const restoreBodyPositionAndOverflow = () => {
  document.body.style.position = ''
  document.body.style.width = ''
  document.body.style.overflow = ''
}

const restoreScrollPosition = (position: number) => {
  document.body.style.top = ''

  window.scrollTo(0, position * -1)
}
