import { StoreAddressId } from '@/modules/store/data/resources'
import { UndefinableError } from 'types/any'
import { IntervalWithReason } from '@/models/Interval'
import { NonEmptyArrayOf } from 'types/NonEmptyArrayOf'
import { Status } from '@/modules/store/data/resources/status'

export const RESET_STATE = 'RESET_STATE'

export const GET_STATUS = 'GET_STATUS'
export type GetStatusPayload = StoreAddressId | undefined

export const GET_STATUS_SUCCESS = 'GET_STATUS_SUCCESS'
export type GetStatusSuccessPayload = Status

export const GET_STATUS_FAILURE = 'GET_STATUS_FAILURE'
export type GetStatusFailurePayload = UndefinableError

export const SET_STATUS = 'SET_STATUS'
export type SetStatusPayload = Status

export const SETUP_STATUS_POLLING = 'SETUP_STATUS_POLLING'
export type SetupStatusPollingPayload = StoreAddressId

export const STOP_STATUS_POLLING = 'STOP_STATUS_POLLING'

export const UPDATE_IS_CHECK_IN_PENDING = 'UPDATE_IS_CHECK_IN_PENDING'
export type UpdateIsCheckInPendingPayload = boolean

export const CONFIRM_CHECK_IN = 'CONFIRM_CHECK_IN'
export type ConfirmCheckInPayload = StoreAddressId

export const CONFIRM_CHECK_IN_SUCCESS = 'CONFIRM_CHECK_IN_SUCCESS'
export type ConfirmCheckInSuccessPayload = StoreAddressId

export const CONFIRM_CHECK_IN_FAILURE = 'CONFIRM_CHECK_IN_FAILURE'
export type ConfirmCheckInFailurePayload = UndefinableError

export const UPDATE_IS_CHECK_IN_LOADING = 'UPDATE_IS_CHECK_IN_LOADING'
export type UpdateIsCheckInLoadingPayload = boolean

export const DISMISS_CHECK_IN = 'DISMISS_CHECK_IN'
export type DismissCheckInPayload = StoreAddressId

export const DISMISS_CHECK_IN_SUCCESS = 'DISMISS_CHECK_IN_SUCCESS'
export type DismissCheckInSuccessPayload = StoreAddressId

export const DISMISS_CHECK_IN_FAILURE = 'DISMISS_CHECK_IN_FAILURE'
export type DismissCheckInFailurePayload = UndefinableError

export const UPDATE_DISMISS_CHECK_IN_FAILURE = 'UPDATE_DISMISS_CHECK_IN_FAILURE'
export type UpdateDismissCheckInFailurePayload = boolean

export const SHOW_STORE_CLOSING_MODAL = 'SHOW_STORE_CLOSING_MODAL'

export const HIDE_STORE_CLOSING_MODAL = 'HIDE_STORE_CLOSING_MODAL'

export const OPEN_STORE_ADDRESSES = 'OPEN_STORE_ADDRESSES'
export const OPEN_STORE_ADDRESSES_NEW = 'OPEN_STORE_ADDRESSES_NEW'
export type OpenStoreAddressesPayload = NonEmptyArrayOf<StoreAddressId>

export const OPEN_STORE_ADDRESSES_SUCCESS = 'OPEN_STORE_ADDRESSES_SUCCESS'
export type OpenStoreAddressesSuccessPayload = NonEmptyArrayOf<StoreAddressId>

export const OPEN_STORE_ADDRESSES_FAILURE = 'OPEN_STORE_ADDRESSES_FAILURE'
export type OpenStoreAddressesFailurePayload = UndefinableError

export const SET_CLOSING_DAYS = 'SET_CLOSING_DAYS'
export interface SetClosingDaysPayload {
  interval: IntervalWithReason
  storeAddressIds: NonEmptyArrayOf<StoreAddressId>
}

export const SET_CLOSING_DAYS_SUCCESS = 'SET_CLOSING_DAYS_SUCCESS'
export type SetClosingDaysSuccessPayload = SetClosingDaysPayload

export const SET_CLOSING_DAYS_FAILURE = 'SET_CLOSING_DAYS_FAILURE'
export type SetClosingDaysFailurePayload = UndefinableError

export const CLOSE_STORE_ADDRESSES_TEMPORARILY =
  'CLOSE_STORE_ADDRESSES_TEMPORARILY'
export interface CloseStoreAddressesTemporarilyPayload {
  until: string
  reason: string
  storeAddressIds: NonEmptyArrayOf<StoreAddressId>
}

export const CLOSE_STORE_ADDRESSES_TEMPORARILY_SUCCESS =
  'CLOSE_STORE_ADDRESSES_TEMPORARILY_SUCCESS'
export type CloseStoreAddressesTemporarilySuccessPayload = NonEmptyArrayOf<
  StoreAddressId
>

export const CLOSE_STORE_ADDRESSES_TEMPORARILY_FAILURE =
  'CLOSE_STORE_ADDRESSES_TEMPORARILY_FAILURE'
export type CloseStoreAddressesTemporarilyFailurePayload = UndefinableError

export const ADD_CLOSED_STORE_ADDRESSES = 'ADD_CLOSED_STORE_ADDRESSES'
export type AddClosedStoreAddresses = StoreAddressId[]

export type Action =
  | {
      type: typeof RESET_STATE
    }
  | {
      type: typeof GET_STATUS
      payload: GetStatusPayload
    }
  | {
      type: typeof GET_STATUS_SUCCESS
      payload: GetStatusSuccessPayload
    }
  | {
      type: typeof GET_STATUS_FAILURE
      payload: GetStatusFailurePayload
    }
  | {
      type: typeof SET_STATUS
      payload: SetStatusPayload
    }
  | {
      type: typeof UPDATE_IS_CHECK_IN_PENDING
      payload: UpdateIsCheckInPendingPayload
    }
  | {
      type: typeof CONFIRM_CHECK_IN
      payload: ConfirmCheckInPayload
    }
  | {
      type: typeof CONFIRM_CHECK_IN_SUCCESS
      payload: ConfirmCheckInSuccessPayload
    }
  | {
      type: typeof CONFIRM_CHECK_IN_FAILURE
      payload: ConfirmCheckInFailurePayload
    }
  | {
      type: typeof UPDATE_IS_CHECK_IN_LOADING
      payload: UpdateIsCheckInLoadingPayload
    }
  | {
      type: typeof DISMISS_CHECK_IN
      payload: DismissCheckInPayload
    }
  | {
      type: typeof DISMISS_CHECK_IN_SUCCESS
      payload: DismissCheckInSuccessPayload
    }
  | {
      type: typeof DISMISS_CHECK_IN_FAILURE
      payload: DismissCheckInFailurePayload
    }
  | {
      type: typeof UPDATE_DISMISS_CHECK_IN_FAILURE
      payload: UpdateDismissCheckInFailurePayload
    }
  | {
      type: typeof SHOW_STORE_CLOSING_MODAL
    }
  | {
      type: typeof HIDE_STORE_CLOSING_MODAL
    }
  | {
      type: typeof SETUP_STATUS_POLLING
      payload: SetupStatusPollingPayload
    }
  | {
      type: typeof CLOSE_STORE_ADDRESSES_TEMPORARILY
      payload: CloseStoreAddressesTemporarilyPayload
    }
  | {
      type: typeof CLOSE_STORE_ADDRESSES_TEMPORARILY_SUCCESS
      payload: CloseStoreAddressesTemporarilySuccessPayload
    }
  | {
      type: typeof CLOSE_STORE_ADDRESSES_TEMPORARILY_FAILURE
      payload: CloseStoreAddressesTemporarilyFailurePayload
    }
  | {
      type: typeof ADD_CLOSED_STORE_ADDRESSES
      payload: AddClosedStoreAddresses
    }
  | {
      type: typeof SET_CLOSING_DAYS
      payload: SetClosingDaysPayload
    }
  | {
      type: typeof SET_CLOSING_DAYS_SUCCESS
      payload: SetClosingDaysSuccessPayload
    }
  | {
      type: typeof SET_CLOSING_DAYS_FAILURE
      payload: SetClosingDaysFailurePayload
    }
  | {
      type: typeof OPEN_STORE_ADDRESSES
      payload: OpenStoreAddressesPayload
    }
  | {
      type: typeof OPEN_STORE_ADDRESSES_NEW
      payload: OpenStoreAddressesPayload
    }
  | {
      type: typeof OPEN_STORE_ADDRESSES_SUCCESS
      payload: OpenStoreAddressesSuccessPayload
    }
  | {
      type: typeof OPEN_STORE_ADDRESSES_FAILURE
      payload: OpenStoreAddressesFailurePayload
    }
