export const FIFTY_MILLIS = 50
export const FOUR_HUNDRED_MILLIS = 400
export const HALF_A_SECOND_IN_MILLIS = 500
export const ONE_SECOND_IN_MILLIS = 1000
export const TWO_SECONDS_IN_MILLIS = 2 * ONE_SECOND_IN_MILLIS
export const THREE_SECONDS_IN_MILLIS = 3 * ONE_SECOND_IN_MILLIS
export const FIVE_SECONDS_IN_MILLIS = 5 * ONE_SECOND_IN_MILLIS
export const TEN_SECONDS_IN_MILLIS = 10 * ONE_SECOND_IN_MILLIS
export const FIFTEEN_SECONDS_IN_MILLIS = 15 * ONE_SECOND_IN_MILLIS
export const TWENTY_SECONDS_IN_MILLIS = 20 * ONE_SECOND_IN_MILLIS
export const ONE_MINUTE_IN_MILLIS = 60 * ONE_SECOND_IN_MILLIS
export const FIVE_MINUTES_IN_MILLIS = 5 * ONE_MINUTE_IN_MILLIS

export const FIVE_MINUTES_IN_SECONDS = 60 * 5

export const ONE_HOUR_IN_SECONDS = 60 * 60

export const SCROLLING_OFFSET_TOP_IN_PIXELS = 100

export const minutesToMillis = (minutes: number) => minutes * 60 * 1000
export const millisToMinutes = (millis: number) => millis / 60 / 1000
export const minutesToSeconds = (minutes: number) => minutes * 60
export const millisToSeconds = (millis: number) => millis / 1000
