import telemetry from '@/modules/telemetry/telemetry'
import * as Sentry from '@sentry/browser'

// Class for unknown errors
class UnknownRuntimeError extends Error {
  constructor(error: string) {
    super(error)
  }
  public readonly name = 'UnknownRuntimeError'
}

export function datadogErrorTracking(hint: Sentry.EventHint | undefined) {
  // Ignore Events that don't have an exception attached
  if (!hint || !hint.originalException) return

  // If event exception does not contain an error, but another value instead
  // (eg. promise rejected with a non-error value), create a UnknownRuntimeError
  const error: Error =
    hint.originalException instanceof Error
      ? hint.originalException
      : new UnknownRuntimeError(hint.originalException)

  // extract Error metadata
  const errorType = error.name || 'unknown'
  const errorMessage = error.message || '[MISSING_ERROR_MESSAGE]'
  const errorStack = error.stack || '[MISSING_ERROR_STACK_TRACE]'
  const errorDetails =
    typeof (error as any).toJSON === 'function' ? (error as any).toJSON() : {}

  // create metrics metadata objects
  const tags = { 'error-type': errorType }
  const attr = {
    'error-type': errorType,
    'error-message': errorMessage,
    'error-stack': errorStack,
    'error-details': errorDetails
  }

  // emit runtime error metrics to datadog
  telemetry.Metric.count('runtime_error', 1, { tags })
  telemetry.Log.error('runtime_error', errorMessage, { tags, attr })
}
