import { Element } from '@/services/printer/elements'

export interface PrintResponse {
  ok: boolean
  message?: string
}

export class Printer {
  public static printCustomTicket(ticket: Element[]): PrintResponse {
    try {
      const serializedTicket = JSON.stringify(ticket)
      const printResponse = window.Android!.printCustomTicket(serializedTicket)
      const response: PrintResponse = JSON.parse(printResponse)
      return response
    } catch (e) {
      return { ok: false, message: e.message || e }
    }
  }

  public static legacyPrintTicket(
    printData: string,
    serializedLabels: string
  ): PrintResponse {
    try {
      window.Android!.printTicket(printData, serializedLabels)
      return { ok: true }
    } catch (e) {
      return { ok: false, message: e.message || e }
    }
  }

  public static legacyPrint(
    printData: string,
    totalTitle: string,
    customerWantInvoiceTitle: string,
    currency: string
  ) {
    try {
      window.Android!.print(
        printData,
        totalTitle,
        customerWantInvoiceTitle,
        currency
      )
      return { ok: true }
    } catch (e) {
      return { ok: false, message: e.message || e }
    }
  }
}
