import * as types from '@/modules/store/services/mutation-types'

// initial state
const state = {
  modals: {
    ORDER: {
      name: 'order',
      displayed: false
    },
    CLOSE: {
      name: 'close',
      displayed: false
    },
    FILTER: {
      name: 'filter',
      displayed: false
    },
    STOCK_PRODUCT: {
      name: 'stock_product',
      displayed: false
    },
    STOCK_ATTRIBUTE: {
      name: 'stock_attribute',
      displayed: false
    },
    BAGS: {
      name: 'bags',
      displayed: false
    },
    ADMIN: {
      name: 'admin',
      displayed: false
    },
    PERMISSIONS: {
      name: 'permissions',
      displayed: false
    },
    REPLACE: {
      name: 'replace',
      displayed: false
    },
    ADD_NEW_PRODUCT: {
      name: 'add_product',
      displayed: false
    },
    HELP: {
      name: 'help',
      displayed: false
    }
  }
}

// getters
const getters = {
  anyModalDisplayed: state =>
    Object.values(state.modals).find(modal => modal.displayed === true) !==
    undefined,
  orderModalDisplayed: state => state.modals.ORDER.displayed,
  closeModalDisplayed: state => state.modals.CLOSE.displayed,
  filterModalDisplayed: state => state.modals.FILTER.displayed,
  stockoutAttributeModalDisplayed: state =>
    state.modals.STOCK_ATTRIBUTE.displayed,
  stockoutProductModalDisplayed: state => state.modals.STOCK_PRODUCT.displayed,
  confirmBagsModalDisplayed: state => state.modals.BAGS.displayed,
  adminModalDisplayed: state => state.modals.ADMIN.displayed,
  permissionsModalDisplayed: state => state.modals.PERMISSIONS.displayed,
  replaceModalDisplayed: state => state.modals.REPLACE.displayed,
  addNewProductModalDisplayed: state => state.modals.ADD_NEW_PRODUCT.displayed,
  helpModalDisplayed: state => state.modals.HELP.displayed
}

// actions
const actions = {
  changeModalDisplay({ commit }, { key, display }) {
    if (!key) {
      return
    }
    const mutationType = display
      ? types.MODAL_DISPLAYED
      : types.MODAL_NOT_DISPLAYED
    commit(mutationType, key.toUpperCase())
  }
}

// mutations
const mutations = {
  [types.MODAL_DISPLAYED](state, key) {
    state.modals[key].displayed = true
  },

  [types.MODAL_NOT_DISPLAYED](state, key) {
    state.modals[key].displayed = false
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}
