import { Observable, Subscription } from 'npm/rxjs'
import { Store } from 'vuex'
import { State } from '@/modules/store'
import { FIFTEEN_SECONDS_IN_MILLIS } from '@/modules/core/helpers'
import { OrdersList } from '@/modules/store/data/resources/orders-list/orders-list'
import { PollingService } from '@/modules/shared'
import { AbstractLiveOrdersService } from '@/modules/orders/services/live-orders'
import { OrdersResponse } from '@/modules/orders/services/live-orders/abstract-live-orders/orders-response'
import { PollingOrdersResponse } from '@/modules/orders/services/live-orders/polling-live-orders/polling-orders-response'
import { PollingOrdersRequest } from '@/modules/orders/services/live-orders/polling-live-orders/polling-orders-request'
import { GET_ORDERS } from '@/modules/orders/store'
import { MparticlePollingEventsService } from './mparticle-polling-events-service'

export class PollingLiveOrdersService extends AbstractLiveOrdersService {
  ordersPolling$: Observable<OrdersList>

  liveOrdersPolling$?: Observable<PollingOrdersResponse>

  notify?: (orders: OrdersResponse) => void

  pollingSubscription: Subscription | null = null

  constructor(
    protected requests: PollingOrdersRequest,
    store: Store<State>,
    {
      interval = FIFTEEN_SECONDS_IN_MILLIS,
      attempts = Number.POSITIVE_INFINITY
    } = {}
  ) {
    super(requests)

    const mparticleService = new MparticlePollingEventsService(store)

    const ordersPolling = new PollingService({
      interval,
      attempts
    })

    this.ordersPolling$ = ordersPolling.createPolling(
      this.requests.getOrders,
      mparticleService.beforePollingRequest(),
      mparticleService.afterPollingResponse(),
      mparticleService.onPollingError()
    )
  }

  start(): void {
    if (!this.notify) {
      throw new Error('Cannot start live orders service without subscription')
    }
    this.stop()
    this.liveOrdersPolling$ = this.createPollingObservable()
    this.pollingSubscription = this.liveOrdersPolling$.subscribe(this.notify)
  }

  stop(): void {
    if (this.pollingSubscription && !this.pollingSubscription.closed) {
      this.pollingSubscription.unsubscribe()
    }
  }

  pause(): void {
    this.stop()
  }

  resume(): void {
    if (!this.liveOrdersPolling$) {
      throw new Error('Cannot resume non-existent live orders service stream')
    }
    if (!this.notify) {
      throw new Error('Cannot resume live orders service without subscription')
    }
    this.stop()
    this.pollingSubscription = this.liveOrdersPolling$.subscribe(this.notify)
  }

  subscribe(subscriptionFn: (orders: OrdersResponse) => void): void {
    this.notify = subscriptionFn
  }

  createPollingObservable(): Observable<PollingOrdersResponse> {
    return this.ordersPolling$
  }

  isPolling() {
    return Boolean(this.pollingSubscription && !this.pollingSubscription.closed)
  }
}

export function factory(store: Store<State>): PollingLiveOrdersService {
  return new PollingLiveOrdersService(
    {
      getOrders: () => store.dispatch(`orderManagement/${GET_ORDERS}`)
    },
    store
  )
}
