import {
  PromotionQualityTiersListDTO,
  PromotionQualityDTO,
  PromotionQualityTier
} from '../api/dtos'
import { PromotionQualityTiersListVM } from '../models'

export const mapPromotionQualityTiers = (
  dto: PromotionQualityTiersListDTO
): PromotionQualityTiersListVM => {
  return new PromotionQualityTiersListVM(dto)
}

export const mapPromotionQuality = (
  dto: PromotionQualityDTO
): PromotionQualityTier => {
  return dto?.qualityTier
}
