import Vuex, { Store } from 'vuex'
import modals from '../modules/modals'
import display from '../modules/display'
import categories from '../modules/categories'
import ordersFactory from '../modules/orders'
import stockFactory from '../modules/stock'
import reportsFactory from '../modules/reports'
import userFactory from '../modules/user'
import { State } from '../state'
import { data } from '../data'
import { AbstractFetchService } from '@/modules/http'

function modulesFactory(fetchService: AbstractFetchService) {
  return {
    modals,
    user: userFactory(fetchService),
    categories,
    orders: ordersFactory(fetchService),
    stock: stockFactory(fetchService),
    display,
    reports: reportsFactory(fetchService),
    data
  }
}

export default function factory(
  fetchService: AbstractFetchService
): Store<State> {
  return new Vuex.Store({
    state: {} as State,
    mutations: {},
    actions: {},
    getters: {},
    modules: modulesFactory(fetchService),
    strict: process.env.VUE_APP_MODE !== 'production'
  })
}
