import { ActionContext } from 'glovo'
import {
  ADD_STORE_ADMINS,
  ADD_PRODUCTS,
  AddProductsPayload,
  Action,
  ADD_STORE_CONTENT,
  AddStoreContentPayload,
  ADD_REQUEST_PRICE_UPDATES,
  AddRequestPriceUpdatesPayload,
  ADD_ATTRIBUTES,
  AddAttributesPayload,
  ADD_STORE_ADMIN_DTOS,
  AddStoreAdminDTOsPayload,
  ADD_CITIES,
  ADD_STORES,
  ADD_STORE_ADDRESSES,
  AddStoreAddressesPayload,
  MarkOrderAsViewedPayload,
  MARK_ORDER_AS_VIEWED,
  MarkOrderAsLoadedPayload,
  MARK_ORDER_AS_LOADED,
  UPDATE_ATTRIBUTES,
  UpdateAttributesPayload,
  MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED,
  MarkCancelledOrderAsAcknowledged,
  MARK_COURIER_NEARBY_AS_NOTIFIED,
  MarkCourierNearbyAsNotifiedPayload,
  MARK_IS_GROCERIES_CHECKBOX_CHECKED,
  MarkIsGroceriesCheckboxCheckedPayload,
  MARK_ORDER_IS_READY_BUTTON_AS_VIEWED,
  MarkOrderIsReadyButtonAsViewedPayload,
  AddOrdersPayload,
  ADD_ORDERS
} from './action-types'
import {
  City,
  Store,
  StoreAddress,
  StoreAddressDTO,
  StoreAdminDTO,
  StoreDTO
} from '@/modules/store/data/resources'
import { CityDTO } from '@/modules/store/data/resources/city'

export const actions = {
  [ADD_STORE_ADDRESSES](
    { commit }: ActionContext<Action>,
    payload: AddStoreAddressesPayload
  ) {
    commit(ADD_STORE_ADDRESSES, payload)
  },
  [ADD_ORDERS]({ commit }: ActionContext<Action>, payload: AddOrdersPayload) {
    commit(ADD_ORDERS, payload)
  },
  [MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED](
    { commit }: ActionContext<Action>,
    payload: MarkCancelledOrderAsAcknowledged
  ) {
    commit(MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED, payload)
  },
  [MARK_ORDER_AS_VIEWED](
    { commit }: ActionContext<Action>,
    payload: MarkOrderAsViewedPayload
  ) {
    commit(MARK_ORDER_AS_VIEWED, payload)
  },
  [MARK_ORDER_AS_LOADED](
    { commit }: ActionContext<Action>,
    payload: MarkOrderAsLoadedPayload
  ) {
    commit(MARK_ORDER_AS_LOADED, payload)
  },
  [MARK_ORDER_IS_READY_BUTTON_AS_VIEWED](
    { commit }: ActionContext<Action>,
    payload: MarkOrderIsReadyButtonAsViewedPayload
  ) {
    commit(MARK_ORDER_IS_READY_BUTTON_AS_VIEWED, payload)
  },
  [ADD_STORE_ADMIN_DTOS](
    { commit }: ActionContext<Action>,
    payload: AddStoreAdminDTOsPayload
  ) {
    commit(ADD_STORE_ADMINS, mapStoreAdmins(payload))
    commit(ADD_CITIES, normaliseCities(payload))
    commit(ADD_STORES, normaliseStores(payload))
    commit(ADD_STORE_ADDRESSES, normaliseStoreAddresses(payload))
  },
  [ADD_PRODUCTS](
    { commit }: ActionContext<Action>,
    payload: AddProductsPayload
  ) {
    commit(ADD_PRODUCTS, payload)
  },
  [ADD_STORE_CONTENT](
    { commit }: ActionContext<Action>,
    payload: AddStoreContentPayload
  ) {
    commit(ADD_STORE_CONTENT, payload)
  },
  [ADD_REQUEST_PRICE_UPDATES](
    { commit }: ActionContext<Action>,
    payload: AddRequestPriceUpdatesPayload
  ) {
    commit(ADD_REQUEST_PRICE_UPDATES, payload)
  },
  [ADD_ATTRIBUTES](
    { commit }: ActionContext<Action>,
    payload: AddAttributesPayload
  ) {
    commit(ADD_ATTRIBUTES, payload)
  },
  [UPDATE_ATTRIBUTES](
    { commit }: ActionContext<Action>,
    payload: UpdateAttributesPayload
  ) {
    commit(UPDATE_ATTRIBUTES, payload)
  },
  [MARK_COURIER_NEARBY_AS_NOTIFIED](
    { commit }: ActionContext<Action>,
    payload: MarkCourierNearbyAsNotifiedPayload
  ) {
    commit(MARK_COURIER_NEARBY_AS_NOTIFIED, payload)
  },
  [MARK_IS_GROCERIES_CHECKBOX_CHECKED](
    { commit }: ActionContext<Action>,
    payload: MarkIsGroceriesCheckboxCheckedPayload
  ) {
    commit(MARK_IS_GROCERIES_CHECKBOX_CHECKED, payload)
  }
}

function mapStoreAdmins(payload: AddStoreAdminDTOsPayload) {
  return payload.map(storeAdminDTO => ({
    ...storeAdminDTO,
    cityCodes: storeAdminDTO.cities.map(({ code }) => code)
  }))
}

function normaliseCities(payload: AddStoreAdminDTOsPayload) {
  return payload.reduce(
    (acc: City[], storeAdminDTO: StoreAdminDTO) => [
      ...acc,
      ...storeAdminDTO.cities.reduce(
        (acc: City[], cityDTO: CityDTO) => [
          ...acc,
          {
            ...cityDTO,
            storeIds: cityDTO.stores.map(({ id }) => id)
          }
        ],
        []
      )
    ],
    []
  )
}

function normaliseStores(payload: AddStoreAdminDTOsPayload) {
  return payload.reduce<Store[]>(
    (acc: Store[], storeAdminDTO: StoreAdminDTO) => [
      ...acc,
      ...storeAdminDTO.cities.reduce(
        (acc: Store[], cityDTO: CityDTO) => [
          ...acc,
          ...cityDTO.stores.reduce(
            (acc: Store[], storeDTO: StoreDTO) => [
              ...acc,
              {
                ...storeDTO,
                storeAddressIds: storeDTO.storeAddresses.map(({ id }) => id),
                cityCode: cityDTO.code
              }
            ],
            []
          )
        ],
        []
      )
    ],
    []
  )
}

function normaliseStoreAddresses(payload: AddStoreAdminDTOsPayload) {
  return payload.reduce(
    (acc: StoreAddress[], storeAdminDTO: StoreAdminDTO) => [
      ...acc,
      ...storeAdminDTO.cities.reduce(
        (acc: StoreAddress[], cityDTO: CityDTO) => [
          ...acc,
          ...cityDTO.stores.reduce(
            (acc: StoreAddress[], storeDTO: StoreDTO) => [
              ...acc,
              ...storeDTO.storeAddresses.reduce(
                (acc: StoreAddress[], storeAddressDTO: StoreAddressDTO) => [
                  ...acc,
                  {
                    ...storeAddressDTO,
                    parentStoreId: storeDTO.id,
                    cityCode: cityDTO.code
                  }
                ],
                []
              )
            ],
            []
          )
        ],
        []
      )
    ],
    []
  )
}
