import { Store } from 'vuex'
import { State } from '@/modules/store'
import mParticleInstance from '@/modules/mparticle'
import { EventTracker } from '@/modules/mparticle'
import Storage from 'models/Storage'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { getNetworkType, getConnectionType } from '@/modules/shared/helpers'
import telemetryInstance, { Telemetry } from '@/modules/telemetry/telemetry'
import { BusinessEventNames } from 'models/BusinessEvents'

class ConnectivityService {
  private store: Store<State>
  private readonly mParticle: EventTracker
  private telemetry: Telemetry

  constructor(
    store: Store<State>,
    mParticle: EventTracker = mParticleInstance,
    telemetry: Telemetry = telemetryInstance
  ) {
    this.store = store
    this.mParticle = mParticle
    this.telemetry = telemetry
    this.onConnectionStateChange = this.onConnectionStateChange.bind(this)
    this.onConnectionPropertiesChange = this.onConnectionPropertiesChange.bind(
      this
    )
  }

  start() {
    window.addEventListener('online', this.onConnectionStateChange)
    window.addEventListener('offline', this.onConnectionStateChange)
    if (navigator.connection && navigator.connection.addEventListener) {
      navigator.connection.addEventListener(
        'change',
        this.onConnectionPropertiesChange
      )
    }
  }

  stop() {
    window.removeEventListener('online', this.onConnectionStateChange)
    window.removeEventListener('offline', this.onConnectionStateChange)
    if (navigator.connection && navigator.connection.removeEventListener) {
      navigator.connection.removeEventListener(
        'change',
        this.onConnectionPropertiesChange
      )
    }
  }

  private onConnectionPropertiesChange() {
    this.telemetry.setDefaultTag('device-connection-speed', getNetworkType())
    this.telemetry.setDefaultTag('device-connection-type', getConnectionType())
  }

  private onConnectionStateChange() {
    const { logEvent, EventType } = this.mParticle

    logEvent(BusinessEventNames.connectivityStatus, EventType.UserContent, {
      deviceId: this.getDeviceId(),
      storeAdminId: this.getStoreAdminId(),
      status: this.getStatus()
    })
    const tags = { 'connection-status': this.getStatus() }
    this.telemetry.Metric.count(`connection_status_change`, 1, { tags })
  }

  private getDeviceId() {
    const device = Storage.get(Storage.items.DEVICE)
    return (device || {}).id || ''
  }

  private getStoreAdminId() {
    const admin = this.store.getters[
      `authentication/${GET_CURRENT_STORE_ADMIN_GETTER}`
    ]
    return (admin && admin.id) || ''
  }

  private getStatus() {
    return navigator.onLine ? 'ONLINE' : 'OFFLINE'
  }
}

export default ConnectivityService
