import * as types from '@/modules/store/services/mutation-types'
import OrderProduct from 'models/OrderProduct'
import ordersApiFactory from '@/api/orders'

// initial state
const state = {
  historyOrderDetails: {},
  reportedOrders: [],
  ordersLoading: false
}

const getters = {
  historyOrderDetails: state => state.historyOrderDetails,
  reportedOrders: state => state.reportedOrders,
  ordersLoading: state => state.ordersLoading
}

export function actionsFactory({ reportCourier, servedProducts }) {
  return {
    async reportCourier({ commit }, { storeAddressId, orderId, data }) {
      // to be migrated in PAR-142
      await reportCourier(storeAddressId, orderId, data)
      commit('COURIER_REPORTED', { orderId })
    },
    async setOrderDetails({ dispatch }, order) {
      const storeAddressId = order.storeAddressId
      const response = await servedProducts(storeAddressId, order.id)
      dispatch('setOrderProducts', { response, order })
    },
    setOrderProducts({ commit }, { response, order }) {
      const orderWithProducts = Object.assign({}, order)
      const products = responseToOrderProducts(response)
      orderWithProducts.products = products
      orderWithProducts.productsTotal = products.total
      commit('SET_ORDER_DETAILS', orderWithProducts)
    }
  }
}

export const responseToOrderProducts = function (response) {
  response.servedProducts = response.servedProducts.map(product => {
    return new OrderProduct().parse(product)
  })
  return response
}

const mutations = {
  [types.SET_ORDER_DETAILS](state, order) {
    state.historyOrderDetails = order
  },
  [types.COURIER_REPORTED](state, { orderId }) {
    state.reportedOrders.push(orderId)
  },
  [types.SHOW_GET_ORDERS_LOADING](state, data) {
    state.ordersLoading = data
  }
}

export default function (fetchService) {
  return {
    state,
    getters,
    actions: actionsFactory(ordersApiFactory(fetchService)),
    mutations
  }
}
