<script>
import { buildSmartUrl } from 'models/Images'

export default {
  name: 'CollectionList',
  props: {
    currentCollectionId: {
      type: Number,
      default: 0
    },
    content: {
      type: Object,
      default: () => {}
    }
  },
  computed: {
    hasOneGroup() {
      return this.hasCollections && this.content.collections.length === 1
    },
    hasMultipleGroups() {
      return this.hasCollections && this.content.collections.length > 0
    },
    hasCollections() {
      return this.content && this.content.collections
    },
    openedEntry() {
      const openedEntry = {}
      if (!this.hasCollections) return openedEntry

      this.content.collections.forEach(group => {
        group.collections.forEach(collection => {
          if (collection.id === this.currentCollectionId) {
            openedEntry.group = group.id
            openedEntry.collection = collection.id
          }
        })
      })
      return openedEntry
    }
  },
  methods: {
    groupImg(group) {
      return buildSmartUrl(group.icon, 'cloudinary', 'h_25,w_25,c_fill')
    },
    isSelectedCollection(collectionId) {
      return this.openedEntry.collection === collectionId
    }
  }
}
</script>
<template>
  <div id="collections-list">
    <template v-if="hasOneGroup">
      <div
        v-for="collection in content.collections[0].collections"
        :key="collection.id"
        :data-qa="`collection-list-entry-${collection.id}`"
        class="collections-list_entry"
        :class="{ bold: isSelectedCollection(collection.id) }"
        @click="$emit('picked', collection.id)"
      >
        {{ collection.name }}
      </div>
    </template>
    <template v-else>
      <el-collapse
        v-if="hasMultipleGroups"
        accordion
        :value="openedEntry.group"
      >
        <el-collapse-item
          v-for="group in content.collections"
          :key="group.id"
          :name="group.id"
        >
          <template slot="title">
            <div class="collections-list_title_group">
              <img :src="groupImg(group)" alt="" />
              <span class="collections-list_title">
                {{ group.title }}
              </span>
            </div>
          </template>
          <div
            v-for="collection in group.collections"
            :key="collection.id"
            :data-qa="`collection-list-entry-${collection.id}`"
            class="collections-list_entry"
            :class="{ bold: isSelectedCollection(collection.id) }"
            @click="$emit('picked', collection.id)"
          >
            {{ collection.name }}
          </div>
        </el-collapse-item>
      </el-collapse>
    </template>
  </div>
</template>
<style lang="scss" scoped>
.collections-list {
  &_title {
    font-size: 20px;
    &_group {
      display: flex;
      align-items: center;
      & img {
        margin-right: 10px;
      }
    }
  }
  &_entry {
    font-size: 20px;
    border-bottom: 0.5px solid lightgray;
    cursor: pointer;
    height: 40px;
    line-height: 40px;
    &:hover {
      background-color: rgba(0, 0, 0, 0.02);
    }
  }
}
</style>
