import { State } from '@/modules/benchmarking/store/state'

export const GET_IS_IN_BENCHMARKING_MODE_GETTER =
  'GET_IS_IN_BENCHMARKING_MODE_GETTER'

export const GET_NUMBER_OF_ORDERS_REQUESTED = 'GET_NUMBER_OF_ORDERS_REQUESTED'

export const getters = {
  [GET_IS_IN_BENCHMARKING_MODE_GETTER](state: State) {
    return state.isInBenchmarkingMode
  },
  [GET_NUMBER_OF_ORDERS_REQUESTED](state: State) {
    return state.numberOfOrdersRequested
  }
}
