import { DisplayType } from '@/modules/store/services/display-types'

export type CategoryName =
  | 'ORDERS'
  | 'REPORTS'
  | 'STOCK'
  | 'PROFILE'
  | 'PROMOTIONS'
export type SubCategoryName = 'DASHBOARD' | 'HISTORY' | 'INVOICES'
export type ViewMode = 'DESKTOP' | 'TABLET'

export const DASHBOARD_SUBCATEGORY_NAME: SubCategoryName = 'DASHBOARD'
export const INVOICES_SUBCATEGORY_NAME: SubCategoryName = 'INVOICES'

export interface Category {
  name: CategoryName
  label: string
  icon: string
  iconActive: string
  route: string
  subCategories?: SubCategory[]
  disabledViews?: ViewMode[]
  disabled?: boolean
}

export interface SubCategory {
  name: SubCategoryName
  label: string
  route: string
  display: DisplayType
}
