import { ActionContext, TypedDispatch } from 'glovo'
import featureTogglesFactory, {
  RetrieveFeatureTogglesResponse,
  RetrieveFeatureTogglesPayload
} from '../api/feature-toggles'
import {
  RETRIEVE_ACTIVE_TOGGLES,
  Action,
  RETRIEVE_ACTIVE_TOGGLES_SUCCESS,
  RetrieveActiveTogglesSuccess,
  RETRIEVE_ACTIVE_TOGGLES_FAILURE,
  RetrieveActiveTogglesFailure,
  RESET_STATE
} from './action-types'
import { Getters, GET_TOGGLES_GETTER } from './getters'
import { State } from './state'
import { PollingService } from '@/modules/shared'
import { START_LIVE_ORDERS } from '@/modules/orders/store'
import { AnotherModuleAction } from 'types/any'
import { FIVE_MINUTES_IN_MILLIS } from '@/modules/core/helpers'
import { UPDATE_REFRESHING_INTERVAL } from '@/modules/core/store/action-types'

export const FEATURE_TOGGLE_POLLING_INTERVAL_IN_MILLIS = FIVE_MINUTES_IN_MILLIS

export function actionsFactory({
  getActiveToggles
}: ReturnType<typeof featureTogglesFactory>) {
  return {
    [RESET_STATE]({ commit }: ActionContext<Action>) {
      commit(RESET_STATE)
    },
    async [RETRIEVE_ACTIVE_TOGGLES]({
      dispatch,
      getters
    }: ActionContext<Action, State, Getters>) {
      try {
        await setupFeatureTogglePolling(getters, dispatch, getActiveToggles)
      } catch (err) {
        dispatch(RETRIEVE_ACTIVE_TOGGLES_FAILURE, err)
      }
    },
    [RETRIEVE_ACTIVE_TOGGLES_SUCCESS](
      {
        commit,
        dispatch,
        state
      }: ActionContext<Action | AnotherModuleAction, State, Getters>,
      payload: RetrieveActiveTogglesSuccess
    ) {
      if (state.loaded) {
        // i.e. only after first time, this prevents a bug where the polling doesn't trigger on app load, since the storeAddressIds haven't been fetched from me() yet and the polling has already been "started"
        dispatch(`orderManagement/${START_LIVE_ORDERS}`)
      }
      commit(RETRIEVE_ACTIVE_TOGGLES_SUCCESS, payload)
      dispatch(`core/${UPDATE_REFRESHING_INTERVAL}`)
    },
    [RETRIEVE_ACTIVE_TOGGLES_FAILURE](
      _ctx: ActionContext<Action>,
      payload: RetrieveActiveTogglesFailure
    ) {
      console.error(payload)
    }
  }
}

async function setupFeatureTogglePolling(
  getters: Getters,
  dispatch: TypedDispatch<Action>,
  getActiveTogglesFn: (
    payload: RetrieveFeatureTogglesPayload
  ) => Promise<RetrieveFeatureTogglesResponse>
): Promise<void> {
  return new Promise((resolve, reject) => {
    const featureTogglePolling$ = new PollingService({
      interval: FEATURE_TOGGLE_POLLING_INTERVAL_IN_MILLIS,
      attempts: 0
    }).createPolling(() => {
      const toggles = Object.keys(getters[GET_TOGGLES_GETTER])
      if (toggles.length > 0) {
        return getActiveTogglesFn({ toggles })
      } else {
        return Promise.resolve({ toggles: [] })
      }
    })

    featureTogglePolling$.subscribe(
      response => {
        if (response) {
          dispatch(RETRIEVE_ACTIVE_TOGGLES_SUCCESS, response)
        }
        resolve()
      },
      error => {
        dispatch(RETRIEVE_ACTIVE_TOGGLES_FAILURE, error)
        reject()
      }
    )
  })
}
