import { State, FeatureToggle, initialState } from './state'
import {
  RESET_STATE,
  RETRIEVE_ACTIVE_TOGGLES_SUCCESS,
  RetrieveActiveTogglesSuccess
} from './action-types'
import { FeatureToggleValue } from 'models/FeatureToggles'

export const mutations = {
  [RESET_STATE](state: State) {
    state.loaded = false
    state.toggles = initialState.toggles
  },
  [RETRIEVE_ACTIVE_TOGGLES_SUCCESS](
    state: State,
    payload: RetrieveActiveTogglesSuccess
  ) {
    const toggles = parseToggles(payload.toggles)
    state.toggles = {
      ...state.toggles,
      ...toggles
    }
    state.loaded = true
  }
}

function parseToggles(
  toggles: { name: FeatureToggle; value: FeatureToggleValue }[]
): State['toggles'] {
  return toggles.reduce((acc, { name, value }) => {
    acc[name] = value
    return acc
  }, {} as State['toggles'])
}
