import { GlvModule } from 'glovo'
import { Vue as _Vue } from 'vue/types/vue'
import analyticsService from './analytics-service'

export class AnalyticsModule implements GlvModule {
  constructor(private vue: typeof _Vue) {}
  install() {
    this.vue.prototype.$analytics =
      this.vue.prototype.$analytics || analyticsService.initiate()
  }
}
