
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import SelectProductModal from '@/components/main/popups/SelectProductModal.vue'
import GlovoWarning from 'common/ui/GlovoWarning.vue'
import { Product } from '@/modules/store/data/resources'
import { ProductDetails } from '@/modules/store/data/resources/order-details'
import ProductsSelector from '@/modules/orders/components/details/product/ProductsSelector.vue'

@Component({
  name: 'ReplaceProductNew',
  components: {
    GlovoWarning,
    ProductsSelector,
    SelectProductModal
  }
})
export default class ReplaceProductNew extends Vue {
  @Prop() product!: ProductDetails
  @Prop() storeAddressId!: number
  @Prop() isVisible!: boolean

  selectedProduct: Product | null = null

  onProductSelected(product: Product) {
    this.selectedProduct = product
  }

  handleClose() {
    if (!this.selectedProduct) {
      this.$emit('close')
    } else {
      this.selectedProduct = null
    }
  }
  get productName() {
    return this.product.name
  }

  get productPrice() {
    return this.product.price
  }

  get warningMessage() {
    return this.$t('order.products.warning')
  }
}
