
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import GlovoConfirmation from 'common/ui/GlovoConfirmation.vue'
import { ProductDetails } from '@/modules/store/data/resources/order-details'
import { OrderId } from '@/modules/store'
import { StoreAddressId } from '@/modules/store/data/resources'

import Stockout from '@/components/main/popups/Stockout.vue'

@Component({
  name: 'DisableProduct',
  components: {
    GlovoConfirmation,
    Stockout
  }
})
export default class DisableProduct extends Vue {
  @Prop() orderId!: OrderId
  @Prop() storeAddressId!: StoreAddressId
  @Prop() product!: ProductDetails
  shouldShowStockout: boolean = false

  cancel() {
    this.$emit('cancel')
  }

  confirm() {
    this.$emit('confirm')
  }

  openStockoutView() {
    this.shouldShowStockout = true
  }
}
