import {
  stockOutEvents,
  checkInEvents,
  orderListEvents,
  orderDetailsEvents,
  storeClosureEvents
} from './events'
import { AnalyticsServiceFactory } from './analytics-library'
import MParticleDelegate from './mparticle/mparticle-delegate'

const isDevelopmentMode = process.env.VUE_APP_MODE === 'development'

// TD Task added to fix any types to proper typings - https://glovoapp.atlassian.net/browse/PAR-2254

const events: any = {
  ...stockOutEvents,
  ...checkInEvents,
  ...orderListEvents,
  ...orderDetailsEvents,
  ...storeClosureEvents
}

export interface AnalyticsService {
  initiate(): analyticsModule
}

interface analyticsModule {
  events: typeof events
  trackMParticleEvent(event: any): void
}

class EventsAnalyticsService implements AnalyticsService {
  initiate() {
    const analytics = AnalyticsServiceFactory.create(new MParticleDelegate())
    const analyticsModule = {
      ...events,

      trackMParticleEvent(event: any): void {
        analytics.track(event)
      }
    }
    return analyticsModule
  }
}

class NullAnalyticsService implements AnalyticsService {
  initiate() {
    const analyticsModule = {
      ...events,

      trackMParticleEvent(): void {}
    }
    return analyticsModule
  }
}

export default isDevelopmentMode
  ? new NullAnalyticsService()
  : new EventsAnalyticsService()
