import { GlvModule } from 'glovo'
import { Vue as _Vue } from 'vue/types/vue'
import telemetry from './telemetry'

export class TelemetryModule implements GlvModule {
  constructor(private vue: typeof _Vue) {}
  install() {
    this.vue.prototype.$telemetry = this.vue.prototype.$telemetry || telemetry
  }
}
