
import Vue from 'vue'
import Component from 'vue-class-component'
import { Getter } from 'vuex-class'
import { Prop } from 'vue-property-decorator'
import CurrencyService from '@/services/currency/CurrencyService'
import {
  GET_PRODUCTS_GETTER,
  GET_STORE_COLLECTION_GROUPS_GETTER,
  GET_STORE_COLLECTION_SECTIONS_GETTER,
  GET_STORE_COLLECTIONS_GETTER
} from '@/modules/store/data/getters'
import {
  Product,
  ProductId,
  StoreCollection,
  StoreCollectionGroup,
  StoreCollectionGroupId,
  StoreCollectionId,
  StoreCollectionSection,
  StoreCollectionSectionId
} from '@/modules/store/data/resources'
import { CollectionGroup, SelectedCollectionOrGroup } from './replace/types'
import ProductsFilter from './replace/ProductsFilter.vue'
import { MenuItemType } from '@/components/common/ui/collection-list-types'

@Component({
  name: 'products-selector',
  components: {
    ProductsFilter
  }
})
export default class ProductsSelector extends Vue {
  @Prop() storeAddressId!: number

  @Getter(GET_PRODUCTS_GETTER)
  storeProducts!: Record<ProductId, Product>

  @Getter(GET_STORE_COLLECTION_GROUPS_GETTER) storeCollectionGroups!: Record<
    StoreCollectionGroupId,
    StoreCollectionGroup
  >

  @Getter(GET_STORE_COLLECTIONS_GETTER) storeCollections!: Record<
    StoreCollectionId,
    StoreCollection
  >

  @Getter(GET_STORE_COLLECTION_SECTIONS_GETTER)
  storeCollectionSections!: Record<
    StoreCollectionSectionId,
    StoreCollectionSection
  >

  searchWord: string = ''

  selectedCollectionOrGroup: SelectedCollectionOrGroup = {
    id: null,
    type: null
  }

  onSearchWordChanged(newSearchWord: string) {
    this.searchWord = newSearchWord
  }

  onSelectedCollectionOrGroupChanged(
    newSelectedCollectionOrGroup: SelectedCollectionOrGroup
  ) {
    this.selectedCollectionOrGroup = newSelectedCollectionOrGroup
  }

  get isFilteringApplied(): boolean {
    return !!this.searchWord || !!this.selectedCollectionOrGroup.type
  }

  get collectionGroups(): CollectionGroup[] {
    return Object.keys(this.storeCollectionGroups).map(this.mapCollectionGroup)
  }

  mapCollectionGroup(groupId: StoreCollectionGroupId | string | null) {
    if (groupId != null) {
      const collectionGroup = this.storeCollectionGroups[+groupId]
      const collections = collectionGroup.collections.map(collectionId => {
        const collection = this.storeCollections[collectionId]
        const sections = collection.sections.map(sectionId => {
          const section = this.storeCollectionSections[sectionId]
          const products = section.products
            .filter(productId => this.storeProducts[productId].enabled)
            .map(productId => this.storeProducts[productId])
          return { ...section, products }
        })
        return { ...collection, sections }
      })
      return { ...collectionGroup, collections }
    }
    return { collections: [] }
  }

  get filteredProductList(): Product[] {
    return this.getFlatFilteredProductList()
  }

  // FIXME: use only this method within PAR-2813 and remove mapCollectionGroup method
  getFlatFilteredProductList(): Product[] {
    const productsList: Product[] = []

    const {
      type: selectedType,
      id: selectedId
    } = this.selectedCollectionOrGroup

    const isCollectionGroupFiltered = (id: number) =>
      selectedType === MenuItemType.COLLECTION_GROUP && selectedId !== id

    const isCollectionFiltered = (id: number) =>
      selectedType === MenuItemType.COLLECTION && selectedId !== id

    const isSectionFiltered = (id: number) =>
      selectedType === MenuItemType.SECTION && selectedId !== id

    const productNameContainsSearchWord = (productName: string) =>
      productName.toLowerCase().includes(this.searchWord.toLowerCase())

    for (let groupId of Object.keys(this.storeCollectionGroups)) {
      if (isCollectionGroupFiltered(+groupId)) continue
      const collectionGroup = this.storeCollectionGroups[+groupId]
      for (let collectionId of collectionGroup.collections) {
        const collection = this.storeCollections[collectionId]
        if (isCollectionFiltered(collectionId)) continue
        for (let sectionId of collection.sections) {
          const section = this.storeCollectionSections[sectionId]
          if (isSectionFiltered(sectionId)) continue
          for (let productId of section.products) {
            const product = this.storeProducts[productId]
            const noSearchWordOrSearchWordMatched =
              !this.searchWord || productNameContainsSearchWord(product.name)
            if (product?.enabled && noSearchWordOrSearchWordMatched) {
              productsList.push(product)
            }
          }
        }
      }
    }

    return productsList
  }

  get currency() {
    return CurrencyService.getCurrencySymbol()
  }

  get searchResultsTitle() {
    if (this.searchWord) {
      return `${this.filteredProductList.length} ${this.$tc(
        'common.search.results.title',
        this.filteredProductList.length
      )}`
    }
    return `${this.filteredProductList.length} ${(this.$t(
      'order.products.title'
    ) as string).toLowerCase()}`
  }

  handleSelectProduct(product: Product) {
    this.$emit('selected', product)
  }
}
