import { analyticsEvents } from '../analytics-library'
import { StoreAddressId, StoreAdminId } from '@/modules/store/data/resources'
import { OrderId } from '@/modules/store'

// TD Task added to fix ts-ignore - https://glovoapp.atlassian.net/browse/PAR-2254

export const orderListEvents = {
  onMarkedAsPickedUpPressed(
    orderId: OrderId,
    storeAddressId: StoreAddressId,
    storeAdminId: StoreAdminId
  ): void {
    const { OrderMarkedAsPickedUpPressed } = analyticsEvents
    // @ts-ignore
    this.trackMParticleEvent(
      new OrderMarkedAsPickedUpPressed(orderId, storeAddressId, storeAdminId)
    )
  },
  onDispatchedToCourierPressed(
    orderId: OrderId,
    storeAddressId: StoreAddressId,
    storeAdminId: StoreAdminId
  ): void {
    const { OrderDispatchedToCourierPressed } = analyticsEvents
    // @ts-ignore
    this.trackMParticleEvent(
      new OrderDispatchedToCourierPressed(orderId, storeAddressId, storeAdminId)
    )
  },
  onViewOrderDetails(
    orderId: OrderId,
    storeAddressId: StoreAddressId,
    storeAdminId: StoreAdminId
  ): void {
    const { OrderDetailsPageSeen } = analyticsEvents
    // @ts-ignore
    this.trackMParticleEvent(
      new OrderDetailsPageSeen(orderId, storeAddressId, storeAdminId)
    )
  }
}
