import { cmsService } from '@/modules/cms'
import merge from 'lodash/merge'

export type LegalDocumentType = 'terms'
export type FeatureName = 'promos'

export interface LegalDocumentOptions {
  documentType?: LegalDocumentType
  featureName: FeatureName
  language: string
}

const DEFAULT_LEGAL_DOC_OPTIONS = Object.freeze({
  documentType: 'terms',
  language: 'en'
})

export const loadLegalDocument = async (options: LegalDocumentOptions) => {
  const document = await getLegalDocument(options)
  if (document) {
    return document
  }

  const defaultLang = DEFAULT_LEGAL_DOC_OPTIONS.language
  if (options.language !== defaultLang) {
    return getLegalDocument({ ...options, language: defaultLang })
  }

  return null
}

const getLegalDocument = (options: LegalDocumentOptions) => {
  const documentSlug = convertDocumentOptionsToSlug(options)
  if (!documentSlug) {
    return Promise.resolve(null)
  }

  return cmsService.page(documentSlug)
}

const convertDocumentOptionsToSlug = (
  options: LegalDocumentOptions
): string => {
  const { documentType, language, featureName } = merge(
    {},
    DEFAULT_LEGAL_DOC_OPTIONS,
    options
  )

  const lowerCaseLanguage = language?.toLowerCase() ?? ''
  return `partners-${documentType}-${featureName}-${lowerCaseLanguage}`
}
