import telemetry from '@/modules/telemetry/telemetry'

type TranslationKey = string
type isTranslationErrorLogged = boolean

class MissingTranslationLogger {
  public logMissingTranslationOnce(
    translationKey: TranslationKey,
    message: string
  ): isTranslationErrorLogged {
    if (this.loggedTranslationKeys.has(translationKey)) {
      return false
    }
    this.loggedTranslationKeys.add(translationKey)
    telemetry.Log.error('TRANSLATION_MISSING', message)
    return true
  }

  private loggedTranslationKeys: Set<TranslationKey> = new Set()
}

export const missingTranslationLogger = new MissingTranslationLogger()
