<template>
  <div id="perso-product">
    <div class="perso-product-header">
      {{ $t('store.perso.title', { product: parentProduct.name }) }}
    </div>

    <div id="perso-product-body" class="perso-product-body">
      <div
        v-for="attributeGroup in parentProduct.attributeGroups"
        :id="hash(attributeGroup.name)"
        :key="attributeGroup.name"
        class="perso-product-attributegroup"
        :class="{
          'perso-product-attributegroup-required':
            showErrors && !attributeGroup.isValid()
        }"
        :track="attributeGroup.name"
      >
        <div class="perso-product-attributegroup-title">
          {{ attributeGroup.name }}
        </div>

        <div class="perso-product-attributegroup-content">
          <div class="perso-product-attributegroup-subtitle">
            {{
              $tc(
                attributeGroup.getSubtitle().subtitle,
                attributeGroup.getSubtitle().params.minNumber,
                attributeGroup.getSubtitle().params
              )
            }}
          </div>
          <div
            v-if="!attributeGroup.isOptional()"
            class="perso-product-attributegroup-subtitle-required"
          >
            {{ $t('store.perso.required') }}
          </div>

          <div class="perso-product-attributegroup-choices">
            <div
              v-for="attribute in attributeGroup.attributes"
              :key="attribute.id"
              class="perso-product-attributegroup-attribute"
              :track="attribute.name"
              :class="{
                'perso-product-attributegroup-attribute-disabled':
                  attributeGroup.isFull() &&
                  !attribute.selected &&
                  attributeGroup.getType() !== 'SINGLE'
              }"
              @click="toggleAttribute(attribute, attributeGroup)"
            >
              <template
                v-if="
                  attributeGroup.getType() === 'SINGLE' &&
                  !attributeGroup.isOptional()
                "
              >
                <icon v-show="attribute.selected" name="circle-selected"></icon>
                <icon
                  v-show="!attribute.selected"
                  name="circle-not-selected"
                ></icon>
                <div class="perso-product-attributegroup-attribute-text">
                  <div>
                    {{ attribute.name }}
                    <span
                      v-show="
                        attribute.priceImpact && attribute.priceImpact !== 0
                      "
                      class="perso-product-attributegroup-attribute-priceimpact"
                      >(+{{ attribute.priceImpact | currency }})</span
                    >
                  </div>
                </div>
              </template>
              <template v-else>
                <icon v-show="attribute.selected" name="check-selected"></icon>
                <icon
                  v-show="!attribute.selected && !attributeGroup.isFull()"
                  name="check-not-selected"
                ></icon>
                <icon
                  v-show="!attribute.selected && attributeGroup.isFull()"
                  name="check-disabled"
                ></icon>
                <div class="perso-product-attributegroup-attribute-text">
                  <div>
                    {{ attribute.name }}
                    <span
                      v-show="
                        attribute.priceImpact && attribute.priceImpact !== 0
                      "
                      class="perso-product-attributegroup-attribute-priceimpact"
                      >(+{{ attribute.priceImpact | currency }})</span
                    >
                  </div>
                </div>
                <div class="perso-product-attributegroup-attribute-quantity">
                  <template
                    v-if="
                      attributeGroup.multipleSelection &&
                      attributeGroup.getType() !== 'SINGLE' &&
                      attribute.quantity > 0
                    "
                  >
                    <div
                      @click.stop.prevent="
                        updateAttributeQuantity(
                          'increment',
                          attribute,
                          attributeGroup
                        )
                      "
                    >
                      <icon
                        v-show="attributeGroup.isFull()"
                        name="plus-disabled"
                      />
                      <icon v-show="!attributeGroup.isFull()" name="plus" />
                    </div>
                    <span>{{ attribute.quantity }}</span>
                    <div
                      @click.stop.prevent="
                        updateAttributeQuantity(
                          'decrement',
                          attribute,
                          attributeGroup
                        )
                      "
                    >
                      <icon name="minus" />
                    </div>
                  </template>
                </div>
              </template>
            </div>
          </div>
        </div>
      </div>
      <div v-if="!detailsDisabled" class="perso-product-attributegroup">
        <div class="perso-product-attributegroup-title">
          {{ $t('store.perso.details') }}
        </div>
        <div class="perso-product-details_text">
          <input
            v-model="details"
            class="glovoInput"
            type="text"
            :placeholder="$t('store.perso.details.placeholder')"
          />
        </div>
      </div>
      <div :class="{ button: productHelper.validSelection(parentProduct) }">
        <glovo-btn
          style="width: 200px"
          :uppercase="true"
          :class="{
            'btn-ready': validSelection,
            'btn-not-ready': !validSelection
          }"
          :text="$t('store.perso.add')"
          type="green"
          @clack="addPersoProduct()"
        ></glovo-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { PersoProduct, Attribute } from 'models/Product'
import GlovoBtn from 'common/GlovoBtn'
import forEach from 'lodash/forEach'
import hash from 'string-hash'
import productHelper from './productHelper'
import Icon from 'common/Icon'

export default {
  name: 'PersoProduct',
  components: {
    Icon,
    GlovoBtn
  },
  props: {
    product: {
      type: Object,
      required: true
    },
    detailsDisabled: {
      type: Boolean
    }
  },
  data() {
    return {
      hash: hash,
      isLoading: false,
      parentProduct: this.product.clone(),
      persoProduct: this.product
        ? new PersoProduct(this.product.id)
        : new PersoProduct(),
      productHelper: productHelper,
      details: this.product ? this.product.note : '',
      showErrors: false
    }
  },
  computed: {
    validSelection() {
      let valid = true
      this.parentProduct.attributeGroups.forEach(group => {
        const isValid = group.isValid()
        if (valid === true && isValid === false && this.showErrors === true) {
          $('.perso-product-wrapper').animate(
            {
              scrollTop:
                document.getElementById(hash(group.name)).offsetTop - 30
            },
            500
          )
          $('.perso-product-container').animate(
            {
              scrollTop:
                document.getElementById(hash(group.name)).offsetTop - 60
            },
            500
          )
        }
        valid = valid && isValid
      })
      return valid
    }
  },
  methods: {
    toggleAttribute(attribute, group) {
      group.toggleAttribute(attribute)
    },
    updateAttributeQuantity(operator, attribute, group) {
      group.updateAttributeQuantity(operator, attribute)
    },
    addPersoProduct() {
      this.showErrors = false

      if (!this.validSelection) {
        this.showErrors = true
        return
      }

      const attributes = []
      forEach(this.parentProduct.attributeGroups, function (group) {
        forEach(group.attributes, function (attr) {
          if (attr.selected) {
            const _attribute = new Attribute(
              attr.id,
              attr.name,
              attr.priceImpact,
              attr.quantity
            )
            attributes.push(_attribute)
          }
        })
      })
      const data = {
        name: this.parentProduct.name,
        product: this.parentProduct,
        persoAttributes: attributes,
        note: this.details
      }

      this.persoProduct.parse(data)
      this.$emit('success', this.persoProduct)
    }
  }
}
</script>

