import moment from 'moment-timezone'
import isEmpty from 'lodash/isEmpty'
import {
  StoresAddressesMap,
  StoreId,
  StoreAddressId
} from '@/modules/store/data/resources'
import { CreatePromotionDTO } from '../api/dtos'
import { CreatePromotion } from '../models'
import { Instant } from 'types/dates'
import { isProductPromotion } from '../helpers'

export const mapCreatePromotionDTO = (
  promotionData: CreatePromotion,
  storeAddressMap: StoresAddressesMap
): CreatePromotionDTO => {
  const {
    storeAddressIds = [],
    storeId,
    storeProductIds,
    allProducts,
    termsAccepted,
    ...createPromoDTO
  } = promotionData || {}
  const promotionDTO: CreatePromotionDTO = {
    ...(createPromoDTO || {}),
    stores: []
  }

  if (
    isEmpty(createPromoDTO) ||
    isEmpty(storeAddressMap) ||
    isEmpty(storeAddressIds) ||
    !Array.isArray(storeAddressIds)
  ) {
    return promotionDTO
  }

  const { name, endDate, stores, ...restPromotionDTO } = promotionDTO
  let mappedStores = mapStores(storeAddressIds, storeAddressMap)

  if (isProductPromotion(promotionData.type)) {
    mappedStores = mapStoreProducts(
      mappedStores,
      storeId,
      storeProductIds,
      allProducts
    )
  }

  return {
    ...restPromotionDTO,
    name: name.trim(),
    endDate: mapEndDate(promotionDTO.endDate),
    stores: mappedStores,
    budget: mapBudget(promotionDTO.budget)
  }
}

const mapBudget = (budget: unknown) => {
  return (budget === '' ? undefined : budget) as number | undefined
}

const mapEndDate = (endDate: Instant) => {
  return (moment(endDate)
    .endOf('day')
    .subtract(1, 'minutes')
    .toDate() as any) as Instant
}

const mapStores = (
  storeAddressIds: CreatePromotion['storeAddressIds'],
  storeAddressMap: StoresAddressesMap
): CreatePromotionDTO['stores'] => {
  const storesMap: Record<StoreId, Set<StoreAddressId>> = {}
  storeAddressIds.forEach(addressId => {
    const { parentStoreId } = storeAddressMap[addressId]
    const addressSet = storesMap[parentStoreId] || new Set()
    addressSet.add(addressId)
    storesMap[parentStoreId] = addressSet
  })

  return Object.keys(storesMap).map(storeId => {
    const id = Number(storeId)
    return { id, addresses: [...storesMap[id]] }
  })
}

const mapStoreProducts = (
  stores: CreatePromotionDTO['stores'],
  storeId: CreatePromotion['storeId'],
  storeProductIds: CreatePromotion['storeProductIds'],
  allProducts: CreatePromotion['allProducts']
): CreatePromotionDTO['stores'] => {
  const selectedStoreIndex = stores.findIndex(store => store.id === storeId)
  if (selectedStoreIndex > -1) {
    stores[selectedStoreIndex].productExternalIds = allProducts
      ? []
      : storeProductIds
  }
  return stores
}
