import { TokenInformation } from './state'
import { StoreAdmin, StoreAddressId } from '@/modules/store/data/resources'

export const INIT_STORE = 'INIT_STORE'
export const REFRESH_TOKEN = 'REFRESH_TOKEN'

export const REFRESH_TOKEN_SUCCESS = 'REFRESH_TOKEN_SUCCESS'
export type RefreshTokenSuccessPayload = TokenInformation

export const REFRESH_TOKEN_BEFORE = 'REFRESH_TOKEN_BEFORE'
export const REFRESH_TOKEN_AFTER = 'REFRESH_TOKEN_AFTER'

export const SET_TOKEN = 'SET_TOKEN'
export interface SetTokenPayload {
  tokenInformation: TokenInformation
  tokenRequestTimestamp: number
}
export const SET_TOKEN_SUCCESS = 'SET_TOKEN_SUCCESS'
export interface SetTokenSuccessPayload {
  tokenInformation: TokenInformation
  tokenExpirationTime: number
}

export const LOG_IN = 'LOG_IN'
export interface LogInPayload {
  username: string
  password: string
}
export const LOG_OUT = 'LOG_OUT' // Update if used further https://glovoapp.atlassian.net/wiki/spaces/TECH/pages/1181417582/Log+out+events
export const INIT_DEVICE_INFO = 'INIT_DEVICE_INFO'
export const INIT_BUSINESS_EVENTS_LOG_COUNTERS =
  'INIT_BUSINESS_EVENTS_LOG_COUNTERS'

export const GET_CURRENT_STORE_ADMIN = 'GET_CURRENT_STORE_ADMIN'

export const SET_CURRENT_STORE_ADMIN = 'ADD_CURRENT_STORE_ADMIN'
export type SetCurrentStoreAdminPayload = StoreAdmin

export const TRACK_CURRENT_STORE_ADMIN = 'TRACK_CURRENT_STORE_ADMIN'
export type TrackCurrentStoreAdminPayload = StoreAdmin

export const SET_CURRENCY = 'SET_CURRENCY'
export interface SetCurrencyPayload {
  currency: string
  languageCode: string
  timeZone: string
}

export const SET_LANGUAGE = 'SET_LANGUAGE'
export type SetLanguagePayload = string

export const SET_ADMIN_MODE = 'SET_ADMIN_MODE'
export type SetAdminModePayload = boolean

export const SET_SELECTED_STORE_ADDRESS_IDS = 'SET_SELECTED_STORE_ADDRESS_IDS'
export type SetSelectedStoreAddressIdsPayload = StoreAddressId[]

export const RESTORE_SELECTED_STORE_ADDRESS_IDS =
  'RESTORE_SELECTED_STORE_ADDRESS_IDS'

export const RESTORE_SESSION = 'RESTORE_SESSION'
export const RESTORE_SESSION_SUCCESS = 'RESTORE_SESSION_SUCCESS'
export interface RestoreSessionSuccessPayload {
  tokenInformation: TokenInformation
  tokenExpirationTime: number
}

export const STORE_ADMIN_LOADED_SUCCESS = 'STORE_ADMIN_LOADED_SUCCESS'
export const STORE_ADMIN_LOADED_FAILURE = 'STORE_ADMIN_LOADED_FAILURE'

export type Action =
  | { type: typeof INIT_STORE }
  | { type: typeof REFRESH_TOKEN }
  | {
      type: typeof REFRESH_TOKEN_SUCCESS
      payload: RefreshTokenSuccessPayload
    }
  | { type: typeof REFRESH_TOKEN_BEFORE }
  | { type: typeof REFRESH_TOKEN_AFTER }
  | { type: typeof SET_TOKEN; payload: SetTokenPayload }
  | { type: typeof SET_TOKEN_SUCCESS; payload: SetTokenSuccessPayload }
  | { type: typeof LOG_IN; payload: LogInPayload }
  | { type: typeof LOG_OUT }
  | { type: typeof INIT_DEVICE_INFO }
  | { type: typeof INIT_BUSINESS_EVENTS_LOG_COUNTERS }
  | { type: typeof GET_CURRENT_STORE_ADMIN }
  | {
      type: typeof SET_CURRENT_STORE_ADMIN
      payload: SetCurrentStoreAdminPayload
    }
  | {
      type: typeof TRACK_CURRENT_STORE_ADMIN
      payload: TrackCurrentStoreAdminPayload
    }
  | { type: typeof SET_CURRENCY; payload: SetCurrencyPayload }
  | { type: typeof SET_LANGUAGE; payload: SetLanguagePayload }
  | { type: typeof SET_ADMIN_MODE; payload: SetAdminModePayload }
  | {
      type: typeof SET_SELECTED_STORE_ADDRESS_IDS
      payload: SetSelectedStoreAddressIdsPayload
    }
  | { type: typeof RESTORE_SELECTED_STORE_ADDRESS_IDS }
  | { type: typeof RESTORE_SESSION }
  | {
      type: typeof RESTORE_SESSION_SUCCESS
      payload: RestoreSessionSuccessPayload
    }
  | { type: typeof STORE_ADMIN_LOADED_SUCCESS }
  | { type: typeof STORE_ADMIN_LOADED_FAILURE }
