
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import Pair from 'common/Pair.vue'
import Order from 'models/Order'
import OrderDetailsCutlery from './OrderDetailsCutlery.vue'
import { green } from '@/modules/core/styles/export.scss'
import { OrderDetails } from '@/modules/store/data/resources/order-details'

@Component({
  name: 'OrderDetailsMessages',
  components: { Pair, OrderDetailsCutlery }
})
export default class OrderDetailsMessages extends Vue {
  private color = green
  @Prop() details!: OrderDetails

  get showCutleryRequest(): boolean {
    return !!(
      this.details &&
      this.details.orderInformation &&
      this.details.orderInformation.isCutleryRequested != null
    )
  }

  get isCashPaymentMethod(): boolean {
    return !!Order.paymentMethodIsCash(this.details)
  }

  get hasAllergyInfo(): boolean {
    return this.details && !!this.details.orderInformation.allergyInfo
  }

  get cashMessage(): string {
    return this.details.orderInformation.isMarketplace
      ? 'order.messages.customer.payment.CASH'
      : 'order.messages.payment.CASH'
  }
}
