/* eslint-disable @typescript-eslint/no-unused-vars */
import Vue from 'vue'
import * as Sentry from '@sentry/browser'
import * as Integrations from '@sentry/integrations'
import { datadogErrorTracking } from './datadog-error-tracking'
import AbstractErrorTracker from './abstract-error-tracker'
import ErrorTrackerOptions from './error-tracker-options'
import GlvError from './glv-error'
import GlvEvent from './glv-event'
import { version } from '@/../version'

const sentryProjectName =
  process.env.VUE_APP_MODE === 'production'
    ? 'partners-webapp'
    : 'partners-webapp-stage'

export const sentryAppReleaseVersion = `${sentryProjectName}@${version}`

/**
 * This class has the responsability to report anything that may help
 * debugging and improving our webapp statiblity.
 */
export default class ErrorTracker extends AbstractErrorTracker<
  ErrorTrackerOptions
> {
  private sentryDSN?: string
  private errorWhitelist?: string[]

  constructor({ sentryDSN, errorWhitelist }: ErrorTrackerOptions = {}) {
    super({ sentryDSN, errorWhitelist })
    this.sentryDSN = sentryDSN
    this.errorWhitelist =
      !!errorWhitelist && Array.isArray(errorWhitelist) ? errorWhitelist : []
  }

  captureUncaughtErrors() {
    try {
      Sentry.init({
        release: sentryAppReleaseVersion,
        dsn: this.sentryDSN,
        integrations: [
          new Integrations.Vue({
            Vue,
            attachProps: true
          })
        ],
        beforeSend: (event, hint) => this.beforeSend(event, hint)
      })
    } catch (err) {
      const errMessage = err instanceof Error ? err.message : `${err}`
      // tslint:disable-next-line: no-console
      console.warn(`There was a problem initializing Sentry SDK`, errMessage)
    }
  }

  captureError(error: GlvError, context?: Record<string, any>) {
    this._assertGlvError(error)
    if (context && this._assertErrorContext(context)) {
      const { tags } = context
      Sentry.withScope(scope => {
        if (tags) {
          scope.setTags(tags)
        }
        Sentry.captureException(error)
      })
    } else {
      Sentry.captureException(error)
    }
  }

  captureEvent(event: GlvEvent) {
    // TODO: This should be implemented for debugging purposes
    throw new Error('Not implemented yet.')
  }

  setUser(user: Record<string, any>) {
    const { id, email, ipAddress } = user
    Sentry.configureScope(scope => {
      scope.setUser({ id, email, ip_address: ipAddress })
    })
  }
  unsetUser() {
    Sentry.configureScope(scope => {
      scope.setUser(null)
    })
  }

  setHyperLocal(locality: Record<string, any>) {
    const { cityCode, language } = locality
    Sentry.configureScope(scope => {
      scope.setExtra('cityCode', cityCode)
      // TODO: Add current language to state to be able to set in scope
      if (language) {
        scope.setExtra('language', language)
      }
    })
  }
  unsetHyperLocal() {
    Sentry.configureScope(scope => {
      scope.setExtra('cityCode', undefined)
      scope.setExtra('language', undefined)
    })
  }

  setContext(key: string, value: string) {
    Sentry.configureScope(scope => {
      scope.setExtra(key, value)
    })
  }
  unsetContext(key: string) {
    Sentry.configureScope(scope => {
      scope.setExtra(key, undefined)
    })
  }

  setTag(key: string, value: string) {
    Sentry.configureScope(scope => {
      scope.setTag(key, value)
    })
  }

  /**
   * Method to be called before sending event to Sentry
   * @param event
   */
  beforeSend(event: Sentry.Event, hint: Sentry.EventHint | undefined) {
    const isWhitelisted = this.isEventWhitelisted(event)
    if (!isWhitelisted) {
      datadogErrorTracking(hint)
    }
    return isWhitelisted ? null : event
  }

  /**
   * Checks whether event is whitelisted
   * @param event to check
   */
  isEventWhitelisted(event: Sentry.Event) {
    const errors = this.getErrorStrings(event)
    return errors.reduce(
      (acc: boolean, current: string | undefined) =>
        acc || this.isErrorWhitelisted(current),
      false
    )
  }

  /**
   * Gets an array of error strings from a Sentry event
   * @param event to get the error string from
   */
  getErrorStrings(event: Sentry.Event) {
    const exceptions =
      event.exception && event.exception.values ? event.exception.values : []
    return exceptions.map(value => value.value)
  }

  isErrorWhitelisted(errorStr: string | undefined) {
    if (!errorStr || !this.errorWhitelist) {
      return false
    }
    return this.errorWhitelist.includes(errorStr)
  }

  _assertErrorContext(context: Record<string, any>) {
    const attrs = ['tags']
    return !!context && attrs.some((attr: string) => attr in context)
  }
}
