import { AbstractFetchService } from '@/modules/http'
import { Module } from 'vuex'
import { State as RootState } from '@/modules/store'
import stockApiFactory from '@/api/stock'
import { actionsFactory } from './actions'
import { initialState, State } from './state'
import {
  GET_CONTENT,
  GET_ATTRIBUTES,
  GET_REQUESTED_PRICE_UPDATES
} from './action-types'
import { getters } from './getters'
import { mutations } from './mutations'

export function storeFactory(
  fetchService: AbstractFetchService
): Module<State, RootState> {
  const stockApi = stockApiFactory(fetchService)
  return {
    namespaced: true,
    state: initialState,
    actions: actionsFactory(stockApi),
    getters,
    mutations
  }
}

export { GET_CONTENT, GET_ATTRIBUTES, GET_REQUESTED_PRICE_UPDATES }
