import { host } from './localConfig'
import { AbstractFetchService } from '@/modules/http'
import { StoreContent } from '@/modules/store/data/resources/store-content'
import { RequestPriceUpdate } from '@/modules/store/data/resources/request-price-update'
import {
  Attribute,
  ProductId,
  AttributeId,
  StoreAddressId
} from '@/modules/store/data/resources'
import { ClosingOption } from '@/modules/store-management/types'
import { StockoutOption } from '@/modules/store/data/resources'
import { NonEmptyArrayOf } from 'types/NonEmptyArrayOf'

export interface ProductStockoutRemovalPayload {
  productIds: NonEmptyArrayOf<ProductId>
}

export interface StockoutResponse {
  expirationTime: number
}

export interface AttributeStockoutRemovalPayload {
  attributeIds: NonEmptyArrayOf<AttributeId>
}

export type ClearStockout = null

export interface TemporaryClosingOptionsResponse {
  untilOptions: ClosingOption[]
}

export interface ProductStockoutPayload {
  productIds: ProductId[]
  until: StockoutOption
}

export interface AddCustomProductStockoutRequestPayload {
  productIds: NonEmptyArrayOf<ProductId>
  expirationTime: StockoutOption
}

export interface AddCustomAttributeStockoutRequestPayload {
  attributeIds: NonEmptyArrayOf<AttributeId>
  expirationTime: StockoutOption
}

export interface AttributeStockoutPayload {
  attributeIds: AttributeId[]
  until: StockoutOption
}

export default function (fetch: AbstractFetchService) {
  return {
    content: (storeAddressId: StoreAddressId) =>
      fetch.get<StoreContent>(
        `${host}/store_addresses/${storeAddressId}/content`,
        null,
        null,
        60000
      ),
    fetchAttributes: (storeAddressId: StoreAddressId) =>
      fetch.get<Attribute[]>(
        `${host}/store_addresses/${storeAddressId}/attributes`
      ),
    addProductStockout: (
      storeAddressId: StoreAddressId,
      payload: ProductStockoutPayload
    ) =>
      fetch.post<StockoutResponse>(
        `${host}/store_addresses/${storeAddressId}/products/stockout`,
        null,
        payload
      ),
    addCustomProductStockout: (
      storeAddressId: StoreAddressId,
      payload: AddCustomProductStockoutRequestPayload
    ) =>
      fetch.post<StockoutResponse>(
        `${host}/store_addresses/${storeAddressId}/products/stockout/custom`,
        null,
        payload
      ),
    removeProductStockout: (
      storeAddressId: StoreAddressId,
      payload: ProductStockoutRemovalPayload
    ) =>
      fetch.remove(
        `${host}/store_addresses/${storeAddressId}/products/stockout`,
        null,
        payload
      ),
    addAttributeStockout: (
      storeAddressId: StoreAddressId,
      payload: AttributeStockoutPayload
    ) =>
      fetch.post<StockoutResponse>(
        `${host}/store_addresses/${storeAddressId}/attributes/stockout`,
        null,
        payload
      ),
    addCustomAttributeStockout: (
      storeAddressId: StoreAddressId,
      payload: AddCustomAttributeStockoutRequestPayload
    ) =>
      fetch.post<StockoutResponse>(
        `${host}/store_addresses/${storeAddressId}/attributes/stockout/custom`,
        null,
        payload
      ),
    removeAttributeStockout: (
      storeAddressId: StoreAddressId,
      payload: AttributeStockoutRemovalPayload
    ) =>
      fetch.remove(
        `${host}/store_addresses/${storeAddressId}/attributes/stockout`,
        null,
        payload
      ),
    retrieveReplacements: (
      storeAddressId: StoreAddressId,
      collectionId: number
    ) =>
      fetch.get(
        `${host}/store_addresses/${storeAddressId}/collections/${collectionId}`
      ),
    getProductsDisablingOptions: () =>
      fetch.get<TemporaryClosingOptionsResponse>(
        `${host}/store_addresses/products/stockout/options`
      ),
    getAttributesDisablingOptions: () =>
      fetch.get<TemporaryClosingOptionsResponse>(
        `${host}/store_addresses/attributes/stockout/options`
      ),
    getRequestedPriceUpdates: (storeAddressId: StoreAddressId) =>
      fetch.get<RequestPriceUpdate[]>(
        `${host}/content/request_price_update/${storeAddressId}`
      ),
    requestAttributePriceUpdate: (
      storeAddressId: StoreAddressId,
      attributeId: number,
      price: number
    ) =>
      fetch.put(`${host}/content/request_price_update/attribute`, null, {
        storeAddressId,
        itemId: attributeId,
        newPrice: price
      }),
    requestProductPriceUpdate: (
      storeAddressId: StoreAddressId,
      productId: number,
      price: number
    ) =>
      fetch.put<RequestPriceUpdate>(
        `${host}/content/request_price_update/product`,
        null,
        {
          storeAddressId,
          itemId: productId,
          newPrice: price
        }
      ),
    removeRequestPriceUpdate: (id: number) =>
      fetch.remove(`${host}/content/request_price_update/${id}`)
  }
}
