import {
  StoreId,
  StoreAddressId,
  ProductId
} from '@/modules/store/data/resources'
import { PromotionType } from './promotions-dtos'

export enum PromotionQualityTier {
  FEATURED = 'FEATURED',
  WEAK = 'WEAK'
}

interface PromotionQualityDataCharacteristics {
  imageId: string
  text: string
}

interface PromotionQualityDataCriteria {
  definition: string
  criteriaList: string[]
}

export interface PromotionQualityData {
  imageId: string
  label: string
  fullLabel: string
  characteristics: PromotionQualityDataCharacteristics[]
  criteria?: PromotionQualityDataCriteria
}

export interface PromotionQualityTierDTO {
  type: PromotionQualityTier
  data: PromotionQualityData
}

export interface PromotionQualityTiersListDTO {
  tiers: PromotionQualityTierDTO[]
}

export interface PromotionQualityDTO {
  qualityTier: PromotionQualityTier
}

export interface PromotionQualityRequestDTO {
  promotionType: PromotionType
  storeId: StoreId
  storeAddressIds: StoreAddressId[]
  productExternalIds: ProductId[]
}
