import { OrderDetailsDTO } from '@/api/dtos/orders/OrderDetailsDTO'
import { OrderDetails } from '@/modules/store/data/resources/order-details'

export function mapOrderDetails(orderDetails: OrderDetailsDTO): OrderDetails {
  return {
    ...orderDetails,
    orderInformation: {
      ...orderDetails.orderInformation,
      code: orderDetails.orderInformation.orderCode
    }
  }
}
