import Storage from 'models/Storage'
import { BusinessEventsLogsCounters } from '@/modules/authentication/store/state'

export enum BusinessEventNames {
  testEvent = 'Test Event', // just used for testing

  // The values used here are intentional and match mParticle event types naming conventions
  ordersPolling = 'Orders Polling',
  orderIsReadyButtonClicked = 'Order is ready button clicked',
  orderIsReadyButtonViewed = 'Order is ready button viewed',
  connectivityStatus = 'Connectivity Status',
  applicationHidden = 'Application Hidden',
  applicationShown = 'Application Shown',
  notificationPixelViewed = 'Notification pixel viewed',
  notificationPixelLoaded = 'Notification pixel loaded',
  userLoggedOut = 'User Logged Out',
  bannerConnectivityShown = 'Banner Connectivity shown',
  usedMemoryAbsolute = 'Used Memory Absolute',
  usedMemoryRelative = 'Used Memory Relative',
  appUnresponsiveEvent = 'App Unresponsive Event',

  // Product
  productDisableAll = 'Product Disable All Pressed',
  productDisableSelected = 'Product Disable Selected Pressed',
  productDisableAccept = 'Product Disable Accept Pressed',
  productDisableCancel = 'Product Disable Cancel Pressed',

  // Attributes
  attributeDisableAll = 'Attribute Disable All Pressed',
  attributeDisableSelected = 'Attribute Disable Selected Pressed',
  attributeDisableAccept = 'Attribute Disable Accept Pressed',
  attributeDisableCancel = 'Attribute Disable Cancel Pressed'
}

const businessEventCountersNames: Record<
  BusinessEventNames,
  keyof BusinessEventsLogsCounters
> = {
  [BusinessEventNames.testEvent]: 'test_event',
  [BusinessEventNames.ordersPolling]: 'orders_polling_count',
  [BusinessEventNames.orderIsReadyButtonClicked]:
    'order_is_ready_button_clicked',
  [BusinessEventNames.orderIsReadyButtonViewed]: 'order_is_ready_button_viewed',
  [BusinessEventNames.connectivityStatus]: 'connectivity_status_count',
  [BusinessEventNames.applicationHidden]: 'application_hidden_count',
  [BusinessEventNames.applicationShown]: 'application_shown_count',
  [BusinessEventNames.notificationPixelViewed]: 'notification_pixel_viewed',
  [BusinessEventNames.notificationPixelLoaded]: 'notification_pixel_loaded',
  [BusinessEventNames.userLoggedOut]: 'user_logged_out',
  [BusinessEventNames.bannerConnectivityShown]: 'banner_connectivity_shown',
  [BusinessEventNames.usedMemoryAbsolute]: 'used_memory_absolute',
  [BusinessEventNames.usedMemoryRelative]: 'used_memory_relative',
  [BusinessEventNames.appUnresponsiveEvent]: 'app_unresponsive_event',

  // Product
  [BusinessEventNames.productDisableAll]: 'product_disable_all_pressed',
  [BusinessEventNames.productDisableSelected]:
    'product_disable_selected_pressed',
  [BusinessEventNames.productDisableAccept]: 'product_disable_accept_pressed',
  [BusinessEventNames.productDisableCancel]: 'product_disable_cancel_pressed',

  // Attributes
  [BusinessEventNames.attributeDisableAll]: 'attribute_disable_all_pressed',
  [BusinessEventNames.attributeDisableSelected]:
    'attribute_disable_selected_pressed',
  [BusinessEventNames.attributeDisableAccept]:
    'attribute_disable_accept_pressed',
  [BusinessEventNames.attributeDisableCancel]:
    'attribute_disable_cancel_pressed'
}

export function businessEventsCountersInitialValues() {
  return Object.values(businessEventCountersNames).reduce(
    (counters: Record<string, number>, currentCounterName: string) => {
      counters[currentCounterName] = 0
      return counters
    },
    {}
  )
}

export function getNextSequenceIndex(eventName: BusinessEventNames): number {
  const eventsCounters = Storage.get(Storage.items.BUSINESS_EVENTS_LOG_COUNTERS)
  let nextSequenceCounter = 0

  const counterName = businessEventCountersNames[eventName]

  if (eventsCounters && counterName) {
    nextSequenceCounter = eventsCounters[counterName]
    eventsCounters[counterName] += 1
    Storage.set(Storage.items.BUSINESS_EVENTS_LOG_COUNTERS, eventsCounters)
  }

  return nextSequenceCounter
}
