import { root } from '@/api/localConfig'
import { AbstractFetchService } from '@/modules/http'
import { FeatureToggle } from '../store/state'
import { FeatureToggleValue } from 'models/FeatureToggles'

export interface RetrieveFeatureTogglesPayload {
  toggles: FeatureToggle[]
}
export interface RetrieveFeatureTogglesResponse {
  toggles: { name: FeatureToggle; value: FeatureToggleValue }[]
}

export default function (fetch: AbstractFetchService) {
  return {
    getActiveToggles: (payload: RetrieveFeatureTogglesPayload) =>
      fetch.post<RetrieveFeatureTogglesResponse>(
        `${root}/features/query`,
        null,
        payload
      )
  }
}
