import { host } from './localConfig'
import { OrderId, State } from '@/modules/store'
import { AbstractFetchService } from '@/modules/http'
import { ProductCustomization } from '@/modules/store/data/resources/product'
import { ProductSummary, StoreAddressId } from '@/modules/store/data/resources'
import { Store } from 'vuex'
import _Store from '@/modules/store'
import { BENCHMARKING_MODULE_NAME } from '@/modules/benchmarking/benchmarking-module'
import { GET_IS_IN_BENCHMARKING_MODE_GETTER } from '@/modules/benchmarking/store/getters'
import { OrderDetailsDTO } from '@/api/dtos/orders/OrderDetailsDTO'
import { mapOrderDetails } from '@/api/mappers/order-details'
import { OrdersListDTO } from '@/api/dtos/orders/list/OrdersListDTO'
import { mapOrdersList } from '@/api/mappers/orders-list'
import { ProductDetails } from '@/modules/store/data/resources/order-details'
import { formatServedProductsPayload } from '@/modules/orders/helpers/helpers'

export interface AssignTrackingCodePayload {
  code: string
}

export interface ReplacePayload {
  servedProducts: ProductReplacement[]
}

export interface ProductReplacement {
  replacedBougthProductId: number
  servedProduct: {
    quantity: number
    id: number
    customizations?: ProductCustomization[] | null
  }
}

async function fakeFetchResponse<T = unknown>(response: T): Promise<T> {
  return response
}

export const ORDERS_POLLING_URL = '/store_addresses/orders?storeAddressIds='

export default function (
  fetch: AbstractFetchService,
  store: Store<State> = _Store.store
) {
  return {
    orderDetails: async (storeAddressId: StoreAddressId, orderId: OrderId) => {
      const orderDetails = await fetch.get<OrderDetailsDTO>(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}`
      )
      return mapOrderDetails(orderDetails)
    },
    orders: async (storeAddressIds: StoreAddressId[]) => {
      const orders = await fetch.get<OrdersListDTO>(
        `${host}${ORDERS_POLLING_URL}${storeAddressIds}`
      )
      return mapOrdersList(orders)
    },
    accept: (storeAddressId: StoreAddressId, orderId: OrderId) =>
      fetch.post(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}/accept`,
        null
      ),
    assignTrackingCode: (
      storeAddressId: StoreAddressId,
      orderId: OrderId,
      data: AssignTrackingCodePayload
    ) =>
      fetch.post(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}/assign_tracking_code`,
        null,
        data
      ),
    servedProducts: (storeAddressId: StoreAddressId, orderId: OrderId) =>
      fetch.get<ProductSummary>(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}/servedproducts`
      ),
    replace: (
      storeAddressId: StoreAddressId,
      orderId: OrderId,
      payload: ReplacePayload
    ) =>
      fetch.put<ProductSummary>(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}/servedproducts`,
        null,
        payload
      ),
    editProduct: (
      storeAddressId: StoreAddressId,
      orderId: OrderId,
      modifiedProduct: ProductDetails
    ) => {
      const servedProducts = formatServedProductsPayload(modifiedProduct)
      return fetch.put<ProductSummary>(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}/servedproducts`,
        null,
        servedProducts
      )
    },
    revert: (
      storeAddressId: StoreAddressId,
      orderId: OrderId,
      productId: number
    ) =>
      fetch.remove<ProductSummary>(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}/servedproducts/${productId}`
      ),
    markReadyForPickup: (storeAddressId: StoreAddressId, orderId: OrderId) =>
      fetch.post(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}/ready_for_pickup`
      ),
    reportCourier: (
      storeAddressId: StoreAddressId,
      orderId: OrderId,
      data: Record<string, any>
    ) =>
      fetch.post(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}/feedback`,
        null,
        data
      ),
    markActiveOrderAsShown: (orderId: number) => {
      if (isInBenchmarkingMode(store)) return fakeFetchResponse(undefined)
      return fetch.get(`${host}/orders/${orderId}/show`)
    },
    markCancelledOrderAsShown: (orderId: number) =>
      fetch.put(`${host}/orders/${orderId}/cancellation/notify`, null, null),
    dispatchOrder: (storeAddressId: StoreAddressId, orderId: OrderId) =>
      fetch.post<void>(
        `${host}/store_addresses/${storeAddressId}/orders/${orderId}/serve`
      )
  }
}

function isInBenchmarkingMode(store: Store<State>) {
  return store.getters[
    `${BENCHMARKING_MODULE_NAME}/${GET_IS_IN_BENCHMARKING_MODE_GETTER}`
  ]
}
