import { RetrieveFeatureTogglesResponse } from '../api/feature-toggles'

export const RESET_STATE = 'feature-toggles/RESET_STATE'

export const RETRIEVE_ACTIVE_TOGGLES = 'RETRIEVE_ACTIVE_TOGGLES'

export const RETRIEVE_ACTIVE_TOGGLES_SUCCESS = 'RETRIEVE_ACTIVE_TOGGLES_SUCCESS'
export type RetrieveActiveTogglesSuccess = RetrieveFeatureTogglesResponse

export const RETRIEVE_ACTIVE_TOGGLES_FAILURE = 'RETRIEVE_ACTIVE_TOGGLES_FAILURE'
export type RetrieveActiveTogglesFailure = any

export type Action =
  | { type: typeof RESET_STATE }
  | { type: typeof RETRIEVE_ACTIVE_TOGGLES }
  | {
      type: typeof RETRIEVE_ACTIVE_TOGGLES_SUCCESS
      payload: RetrieveActiveTogglesSuccess
    }
  | {
      type: typeof RETRIEVE_ACTIVE_TOGGLES_FAILURE
      payload: RetrieveActiveTogglesFailure
    }
