
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import GlovoBtn from 'common/GlovoBtn.vue'

@Component({
  name: 'QuantitySelector',
  components: {
    GlovoBtn
  }
})
export default class QuantitySelector extends Vue {
  @Prop({ default: -Infinity }) min!: number
  @Prop({ default: Infinity }) max!: number
  @Prop({ default: 0 }) quantity!: number

  get isDecreaseEnabled() {
    return this.quantity > this.min
  }

  get isIncreaseEnabled() {
    return this.quantity < this.max
  }
}
