import isEmpty from 'lodash/isEmpty'
import { stringify } from 'query-string'
import { AbstractFetchService } from '@/modules/http'
import { StoreAddressId } from '@/modules/store/data/resources'
import { host } from '@/api/localConfig'
import {
  PromotionId,
  UpdatePromotionRequestDTO,
  PromotionsListDTO,
  CreatePromotionDTO,
  PromotionDTO,
  PromotionQualityTiersListDTO,
  PromotionQualityDTO,
  PromotionQualityRequestDTO
} from './dtos'

export interface GetPromotionsPayload {
  storeAddressIds: StoreAddressId[]
}

export interface UpdatePromotionPayload extends UpdatePromotionRequestDTO {
  id: PromotionId
}

export const promotionsApiFactory = (fetch: AbstractFetchService) => {
  return {
    createPromotion: (createPromotionRequest: CreatePromotionDTO) => {
      return fetch.post<PromotionDTO>(
        `${host}/promotions`,
        null,
        createPromotionRequest
      )
    },
    getPromotionsForStoreAddresses: ({
      storeAddressIds
    }: GetPromotionsPayload) => {
      if (isEmpty(storeAddressIds)) {
        return null
      }

      return fetch.get<PromotionsListDTO>(
        `${host}/promotions?storeAddressIds=${storeAddressIds}`
      )
    },
    updatePromotion: (updatePromotionRequest: UpdatePromotionPayload) => {
      const { id, ...restUpdatePromotionRequest } = updatePromotionRequest || {}
      return fetch.put<UpdatePromotionRequestDTO>(
        `${host}/promotions/${id}`,
        null,
        restUpdatePromotionRequest
      )
    },
    getPromotionQualityTiers: () => {
      return fetch.get<PromotionQualityTiersListDTO>(
        `${host}/promotions/quality/tiers`
      )
    },
    getPromotionQuality: (dto: PromotionQualityRequestDTO) => {
      if (
        !dto ||
        !dto.promotionType ||
        !dto.storeId ||
        isEmpty(dto.storeAddressIds) ||
        isEmpty(dto.productExternalIds)
      ) {
        return null
      }

      const promoQualityQuery = stringify(dto, { arrayFormat: 'comma' })
      return fetch.get<PromotionQualityDTO>(
        `${host}/promotions/quality?${promoQualityQuery}`
      )
    }
  }
}
