
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import GlovoModal from './modal/GlovoModal.vue'
import GlovoBtn from '../GlovoBtn.vue'

@Component({
  name: 'GlovoConfirmation',
  components: {
    GlovoModal,
    GlovoBtn
  }
})
export default class GlovoConfirmation extends Vue {
  @Prop({ default: false }) open!: boolean
  @Prop() title!: string
  @Prop() content!: string
  @Prop() confirmButtonText!: string
  @Prop() cancelButtonText!: string

  confirm(e: Event) {
    this.$emit('confirm', e)
  }

  cancel() {
    this.$emit('cancel')
  }
}
