






import Vue from 'vue'
import ConnectivityService from '@/services/connectivity/ConnectivityService'
import Store from '@/modules/store'
import VisibilityService from '@/services/visibility/VisibilityService'
import { TOGGLES_LOADED_GETTER } from '@/modules/feature-toggles'
import telemetry from '@/modules/telemetry/telemetry'
import ResponsivenessMonitoringService from '@/services/responsiveness/ResponsivenessMonitoringService'
import MemoryMonitoringService from '@/services/memory/MemoryMonitoringService'
import {
  SetIsInBenchmarkingModePayload,
  SET_IS_IN_BENCHMARKING_MODE
} from '@/modules/benchmarking/store/action-types'
import { GET_IS_IN_BENCHMARKING_MODE_GETTER } from '@/modules/benchmarking/store/getters'
import { BENCHMARKING_ROUTE } from '@/modules/benchmarking/benchmarking-routes'
import { benchmarking } from '@/modules/benchmarking/store/namespace'
import { updateMetaRefreshContent } from '@/modules/shared/helpers'
import AudioService from '@/services/audio/AudioService'
import { THREE_SECONDS_IN_MILLIS } from '../helpers'
import { core } from '../store/namespace'
import {
  CLEAR_LOGGED_REQUESTS,
  UpdateIsOfflineBannerVisible,
  UPDATE_IS_OFFLINE_BANNER_VISIBLE
} from '../store/action-types'
import { StoreAction } from '@/modules/store/types/store'
import Component from 'vue-class-component'
import { Watch } from 'vue-property-decorator'
import { Action, Getter } from 'vuex-class'
import { ToBeMigrated } from 'types/any'
import moment from 'moment'
import { Route } from 'vue-router'
import { ORDERS_ROUTE } from '@/modules/orders/orders-routes'
import { DEFAULT_APP_REFRESHING_INTERVAL } from '@/modules/shared/helpers/meta-tag-content-update'

let connectivityService: ConnectivityService
let visibilityService: VisibilityService
let memoryMonitoringService: MemoryMonitoringService
let responsivenessMonitoringService: ResponsivenessMonitoringService

type LogEntry = {
  time: string
  state: LogState
}
type LogState = 'on' | 'off'

@Component({
  name: 'App',
  components: {}
})
export default class App extends Vue {
  offlineTimeout?: number

  @Getter('online') online!: boolean
  @Getter('anyModalDisplayed') anyModalDisplayed!: boolean
  @Getter(TOGGLES_LOADED_GETTER) areTogglesLoaded!: boolean
  @benchmarking.Action(SET_IS_IN_BENCHMARKING_MODE)
  setIsInBenchmarkingMode!: StoreAction<SetIsInBenchmarkingModePayload>
  @benchmarking.Getter(GET_IS_IN_BENCHMARKING_MODE_GETTER)
  isInBenchmarkingMode!: boolean

  @Action('updateOnlineStatus') updateOnlineStatus: ToBeMigrated
  @Action('sendStoreAddressTrackingLogs')
  sendStoreAddressTrackingLogs: ToBeMigrated
  @core.Action(UPDATE_IS_OFFLINE_BANNER_VISIBLE)
  updateOfflineBannerVisibility!: StoreAction<UpdateIsOfflineBannerVisible>
  @core.Action(CLEAR_LOGGED_REQUESTS)
  clearLoggedRequests!: StoreAction<void>

  @Watch('areTogglesLoaded') togglesLoadedChanged() {
    telemetry.Event.pageLoadingTime()
  }

  @Watch('$route') $routeChanged(to: Route) {
    const isExitingBenchmarking = !to.path.includes(BENCHMARKING_ROUTE)
    if (this.isInBenchmarkingMode && isExitingBenchmarking) {
      this.setIsInBenchmarkingMode(false)
      updateMetaRefreshContent(
        `${DEFAULT_APP_REFRESHING_INTERVAL};url=/${ORDERS_ROUTE}`
      )
    }
  }

  @Watch('online') onlineChanged() {
    if (!this.online) {
      this.offlineTimeout = window.setTimeout(() => {
        this.updateOfflineBannerVisibility(true)
        this.logOfflineState()
      }, THREE_SECONDS_IN_MILLIS)
    } else {
      clearTimeout(this.offlineTimeout)
      this.updateOfflineBannerVisibility(false)
      this.clearLoggedRequests()
      this.logOnlineState()
    }
  }

  mounted() {
    window.addEventListener('load', () => {
      this.changeOnlineStatus()
      window.addEventListener('online', this.changeOnlineStatus)
      window.addEventListener('offline', this.changeOnlineStatus)
    })
    const store = Store.store
    connectivityService = new ConnectivityService(store)
    visibilityService = new VisibilityService(store)
    memoryMonitoringService = new MemoryMonitoringService(store)
    responsivenessMonitoringService = new ResponsivenessMonitoringService(store)

    connectivityService.start()
    visibilityService.start()
    memoryMonitoringService.start()
    responsivenessMonitoringService.start()
    AudioService.start()
  }
  beforeDestroy() {
    // this hook does not reliably get called
    window.removeEventListener('online', this.changeOnlineStatus)
    window.removeEventListener('offline', this.changeOnlineStatus)
    connectivityService.stop()
    visibilityService.stop()
    memoryMonitoringService.stop()
    responsivenessMonitoringService.stop()
    AudioService.stop()
  }

  logOfflineState() {
    this.logState('off')
  }

  logState(state: LogState) {
    let offlineLog = this.$localStorage.get('offline_log')

    offlineLog = offlineLog ? JSON.parse(offlineLog) : []

    offlineLog.push({
      time: moment(new Date()).utc().valueOf(),
      state: state
    })

    this.$localStorage.set('offline_log', JSON.stringify(offlineLog))
  }

  async logOnlineState() {
    this.logState('on')
    await this.sendStoreAddressTrackingLogs(this.getlogs())
    this.resetLogs()
  }

  getlogs() {
    let offlineLog = this.$localStorage.get('offline_log')
    offlineLog = offlineLog ? JSON.parse(offlineLog) : []
    return offlineLog.map((log: LogEntry) => ({
      ...log,
      time: !isNaN(Date.parse(log.time))
        ? moment(Date.parse(log.time)).utc().valueOf()
        : log.time
    }))
  }

  resetLogs() {
    this.$localStorage.remove('offline_log')
  }

  changeOnlineStatus() {
    this.updateOnlineStatus(navigator.onLine)
  }
  setDir() {
    return this.$i18n.locale === 'ar' ? 'rtl' : 'lrt'
  }
}
