
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import GlovoBtn from 'common/GlovoBtn.vue'
import { GlovoModal } from 'common/ui/modal'
import QuantitySelector from 'common/QuantitySelector.vue'

@Component({
  name: 'QuantitySelectorModal',
  components: {
    GlovoBtn,
    GlovoModal,
    QuantitySelector
  }
})
export default class QuantitySelectorModal extends Vue {
  @Prop() isDisplayed!: boolean
  @Prop() quantity!: number
  @Prop() min?: number
  @Prop() max?: number

  get isModalDisplayed() {
    return this.isDisplayed
  }

  set isModalDisplayed(display: boolean) {
    if (!display) {
      this.$emit('close')
    }
  }
}
