import DateTimeService from '@/services/datetime/DateTimeService'
import { StoresMap } from '@/modules/store/data/resources'
import { PromotionDTO } from '../api/dtos'

export interface PromotionStoreInfo {
  storeId: number
  storeName: string
  storeAddresses: string[]
  products: string[]
}

export class PromotionInfo {
  private readonly _totalProductsCount: number
  private readonly _totalStoreAddressesCount: number

  constructor(
    private readonly _storesInPromotion: ReadonlyArray<PromotionStoreInfo>
  ) {
    this._storesInPromotion = _storesInPromotion || []
    this._totalProductsCount = this.calculateTotalProductsCount()
    this._totalStoreAddressesCount = this.calculateTotalStoreAddressesCount()
  }

  private calculateTotalProductsCount() {
    return this._storesInPromotion.reduce(
      (count, store) => count + store.products.length,
      0
    )
  }

  private calculateTotalStoreAddressesCount() {
    return this._storesInPromotion.reduce(
      (count, store) => count + store.storeAddresses.length,
      0
    )
  }

  get totalProductsCount() {
    return this._totalProductsCount
  }

  get totalStoreAddressesCount() {
    return this._totalStoreAddressesCount
  }

  get storesInPromotion(): ReadonlyArray<PromotionStoreInfo> {
    return this._storesInPromotion
  }
}

export class Promotion implements PromotionDTO {
  constructor(
    private readonly promotion: PromotionDTO,
    private readonly storesMap: StoresMap
  ) {
    this.promotion = promotion || {}
    this.storesMap = storesMap || {}
  }

  get id() {
    return this.promotion.id
  }

  get name() {
    return this.promotion.name
  }

  get type() {
    return this.promotion.type
  }

  get qualityTier() {
    return this.promotion.qualityTier
  }

  get cityCode() {
    return this.promotion.cityCode
  }

  get startDate() {
    return DateTimeService.formatAsDate(this.promotion.startDate)
  }

  get endDate() {
    return DateTimeService.formatAsDate(this.promotion.endDate)
  }

  get stores() {
    return this.promotion.stores || []
  }

  get budget() {
    return this.promotion.budget || {}
  }

  get percentage() {
    return this.promotion.percentage
  }

  get spendingSummary() {
    return this.promotion.spendingSummary || {}
  }

  get spendingPercentage(): string {
    const { currentSpend } = this.spendingSummary
    const { amount } = this.budget

    if (
      amount == null ||
      currentSpend == null ||
      isNaN(amount) ||
      isNaN(currentSpend) ||
      amount <= 0 ||
      currentSpend < 0
    ) {
      return ''
    }

    return `${Math.round((100 * currentSpend) / amount)}%`
  }

  get status() {
    return this.promotion.status
  }

  get getPromotionInfo(): PromotionInfo {
    return new PromotionInfo(this.getStoreInfo())
  }

  private getStoreInfo(): PromotionStoreInfo[] {
    return this.stores.map(store => ({
      storeId: store.id,
      storeName: this.storesMap[store.id].name,
      products: store.storeProducts.map(element => element.name),
      storeAddresses:
        this.storesMap[store.id]?.storeAddresses.map(
          storeAddress => storeAddress.address
        ) ?? []
    }))
  }
}
