import {
  Product,
  StoreAdmin,
  RequestPriceUpdate,
  StoreContent,
  Attribute,
  RequestPriceUpdateId,
  StoreAddressId,
  StoreAdminDTO,
  City,
  Store,
  StoreAddress,
  OrderId
} from './resources'
import { OrderTypes } from '@/modules/store/data/state'

export const ADD_STORE_ADMIN_DTOS = 'ADD_STORE_ADMIN_DTOS'
export type AddStoreAdminDTOsPayload = StoreAdminDTO[]

export const ADD_STORE_ADMINS = 'ADD_STORE_ADMINS'
export type AddStoreAdminsPayload = StoreAdmin[]

export const ADD_CITIES = 'ADD_CITIES'
export type AddCitiesPayload = City[]

export const ADD_STORES = 'ADD_STORES'
export type AddStoresPayload = Store[]

export const ADD_STORE_ADDRESSES = 'ADD_STORE_ADDRESSES'
export type AddStoreAddressesPayload = StoreAddress[]

export const ADD_ORDERS = 'ADD_ORDERS'
export type AddOrdersPayload = OrderTypes[]

export const MARK_ORDER_AS_VIEWED = 'MARK_ORDER_AS_VIEWED'
export type MarkOrderAsViewedPayload = OrderId

export const MARK_ORDER_AS_LOADED = 'MARK_ORDER_AS_LOADED'
export type MarkOrderAsLoadedPayload = OrderId

export const MARK_ORDER_IS_READY_BUTTON_AS_VIEWED =
  'MARK_ORDER_IS_READY_BUTTON_AS_VIEWED'
export type MarkOrderIsReadyButtonAsViewedPayload = OrderId

export const ADD_PRODUCTS = 'ADD_PRODUCTS'
export type AddProductsPayload = Product[]

export const ADD_STORE_CONTENT = 'ADD_STORE_CONTENT'
export interface AddStoreContentPayload {
  storeContent: StoreContent
  storeAddressId: StoreAddressId
}

export const ADD_REQUEST_PRICE_UPDATES = 'ADD_REQUEST_PRICE_UPDATES'
export type AddRequestPriceUpdatesPayload = RequestPriceUpdate[]

export const REMOVE_REQUEST_PRICE_UPDATE = 'REMOVE_REQUEST_PRICE_UPDATE'
export type RemoveRequestPriceUpdatesPayload = RequestPriceUpdateId

export const ADD_ATTRIBUTES = 'ADD_ATTRIBUTES'
export type AddAttributesPayload = {
  attributes: Attribute[]
  storeAddressId: StoreAddressId
}

export const UPDATE_ATTRIBUTES = 'UPDATE_ATTRIBUTES'
export type UpdateAttributesPayload = Attribute[]

export const MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED =
  'MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED'
export type MarkCancelledOrderAsAcknowledged = OrderId

export const MARK_COURIER_NEARBY_AS_NOTIFIED = 'MARK_COURIER_NEARBY_AS_NOTIFIED'
export type MarkCourierNearbyAsNotifiedPayload = OrderId

export const MARK_IS_GROCERIES_CHECKBOX_CHECKED =
  'MARK_IS_GROCERIES_CHECKBOX_CHECKED'
export type MarkIsGroceriesCheckboxCheckedPayload = {
  id: OrderId
  isChecked: boolean
}

export const SET_IS_ORDER_READY = 'SET_IS_ORDER_READY'
export type SetIsOrderReadyPayload = {
  storeAddressId: StoreAddressId
  orderId: OrderId
}

export type Action =
  | {
      type: typeof ADD_STORE_ADMIN_DTOS
      payload: AddStoreAdminDTOsPayload
    }
  | {
      type: typeof ADD_STORE_ADMINS
      payload: AddStoreAdminsPayload
    }
  | {
      type: typeof ADD_CITIES
      payload: AddCitiesPayload
    }
  | {
      type: typeof ADD_STORES
      payload: AddStoresPayload
    }
  | {
      type: typeof ADD_STORE_ADDRESSES
      payload: AddStoreAddressesPayload
    }
  | {
      type: typeof ADD_ORDERS
      payload: AddOrdersPayload
    }
  | {
      type: typeof MARK_ORDER_AS_VIEWED
      payload: MarkOrderAsViewedPayload
    }
  | {
      type: typeof MARK_ORDER_AS_LOADED
      payload: MarkOrderAsLoadedPayload
    }
  | {
      type: typeof MARK_ORDER_IS_READY_BUTTON_AS_VIEWED
      payload: MarkOrderIsReadyButtonAsViewedPayload
    }
  | {
      type: typeof ADD_PRODUCTS
      payload: AddProductsPayload
    }
  | {
      type: typeof ADD_STORE_CONTENT
      payload: AddStoreContentPayload
    }
  | {
      type: typeof ADD_REQUEST_PRICE_UPDATES
      payload: AddRequestPriceUpdatesPayload
    }
  | {
      type: typeof REMOVE_REQUEST_PRICE_UPDATE
      payload: RemoveRequestPriceUpdatesPayload
    }
  | {
      type: typeof ADD_ATTRIBUTES
      payload: AddAttributesPayload
    }
  | {
      type: typeof UPDATE_ATTRIBUTES
      payload: UpdateAttributesPayload
    }
  | {
      type: typeof MARK_CANCELLED_ORDER_AS_ACKNOWLEDGED
      payload: MarkCancelledOrderAsAcknowledged
    }
  | {
      type: typeof MARK_COURIER_NEARBY_AS_NOTIFIED
      payload: MarkCourierNearbyAsNotifiedPayload
    }
  | {
      type: typeof MARK_IS_GROCERIES_CHECKBOX_CHECKED
      payload: MarkIsGroceriesCheckboxCheckedPayload
    }
  | {
      type: typeof SET_IS_ORDER_READY
      payload: SetIsOrderReadyPayload
    }
