import { ProductDetails } from '@/modules/store/data/resources/order-details'
import { Product } from '@/modules/store/data/resources/product'

export function parseProducts(product: Product): ProductDetails {
  return {
    id: product.id,
    productId: product.productId,
    imageUrl: product.imageUrl || '',
    imageServiceId: product.imageServiceId || '',
    name: product.name,
    quantity: product.quantity,
    price: product.price,
    note: product.note,
    customizations: product.customizations,
    originalCustomerProductRequest: product.originalCustomerProductRequest
      ? parseProducts(product.originalCustomerProductRequest)
      : null
  }
}
