
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { Getter } from 'vuex-class'
import { stockManagement } from '@/modules/stock/store/namespace'
import {
  GET_CONTENT,
  GetContentPayload
} from '@/modules/stock/store/action-types'
import { StoreAction } from '@/modules/store/types/store'
import Icon from 'common/Icon.vue'
import { GlovoLink } from 'common/ui/link'
import { ProductDetails } from '@/modules/store/data/resources/order-details'
import { GET_PRODUCTS_GETTER } from '@/modules/store/data/getters'
import { Product, ProductId } from '@/modules/store/data/resources'
import { IS_GET_CONTENT_LOADING_GETTER } from '@/modules/stock/store/getters'

@Component({
  name: 'OrderProductEdit',
  components: {
    Icon,
    GlovoLink
  }
})
export default class OrderProductEdit extends Vue {
  @Prop() productDetails!: ProductDetails
  @Prop() storeAddressId!: number

  @Getter(GET_PRODUCTS_GETTER)
  storeProducts!: Record<ProductId, Product>

  @stockManagement.Getter(IS_GET_CONTENT_LOADING_GETTER)
  isContentLoading!: boolean

  @stockManagement.Action(GET_CONTENT)
  getContent!: StoreAction<GetContentPayload>

  mounted() {
    if (!this.product) {
      this.getContent(this.storeAddressId)
    }
  }

  get product() {
    return this.storeProducts[this.productDetails.productId]
  }

  get productHasAttributeGroups(): boolean {
    return !!this.product?.attributeGroups?.length
  }

  get isEditAttributesOptionVisible(): boolean {
    return this.isContentLoading || this.productHasAttributeGroups
  }

  handleEditQuantity() {
    this.$emit('edit-quantity')
  }

  handleReplaceProduct() {
    this.$emit('replace')
  }

  handleEditAttributes() {
    if (this.productHasAttributeGroups) {
      this.$emit('edit-attributes')
    }
  }

  handleRemoveProduct() {
    this.$emit('remove')
  }
}
