
import Component from 'vue-class-component'
import Vue from 'vue'
import Icon from 'common/Icon.vue'
import moment from 'moment'
import times from 'lodash/times'
import { Prop } from 'vue-property-decorator'
export interface MonthOption {
  label: string
  value: string
}
@Component({
  name: 'GlovoMonthSelector',
  components: {
    Icon
  }
})
export default class GlovoMonthSelector extends Vue {
  @Prop({ required: true })
  defaultValue!: string

  selectedMonth: string = this.defaultValue

  get months(): MonthOption[] {
    return times(12, monthsBeforeCurrent => {
      const month = moment().subtract(monthsBeforeCurrent, 'months')
      const value = moment(month).locale('es').format('YYYY-MM')
      const label = moment(month).locale(this.$i18n.locale).format('MMMM, YYYY')
      return {
        value,
        label
      }
    })
  }
}
