
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import Icon from 'common/Icon.vue'
import { Size } from '../types'

// TODO: https://glovoapp.atlassian.net/browse/PAR-1744 Replace all buttons in our app with GlovoBtn components
@Component({
  name: 'GlovoBtn',
  components: {
    Icon
  }
})
export default class GlovoBtn extends Vue {
  // Design reference:
  // https://app.zeplin.io/project/5e3bf9b4d2f5e18cd30ffb84/screen/5ea1535247a54e51f50a9729
  @Prop({
    default: ''
  })
  text!: string
  @Prop({
    default: ''
  })
  type!: string
  @Prop() nativeType!: string
  @Prop({
    default: ''
  })
  size!: Size
  @Prop({
    default: false
  })
  uppercase!: boolean
  @Prop({
    default: false
  })
  bold!: boolean
  @Prop({
    default: false
  })
  shadow!: boolean
  @Prop({
    default: ''
  })
  color!: string
  @Prop({
    default: false
  })
  loading!: boolean
  @Prop({
    default: false
  })
  disabled!: boolean
  @Prop({
    default: ''
  })
  icon!: string

  // Gradually move all glovo-btn usages to this styling (aligns to zeplin above)
  // I don't want to change all buttons in one go
  @Prop({
    default: false
  })
  useNewStyling!: boolean

  click(e: Event) {
    if (!this.isDisabled) {
      this.$emit('clack', e)
    }
  }

  get isDisabled() {
    return this.disabled || this.loading
  }

  get buttonText() {
    return this.loading ? this.$t('common.loading.text') : this.text
  }
}
