import { RouteConfig } from 'vue-router'

const Orders = () =>
  import(/* webpackPrefetch: true */ './components/list/Orders.vue')
const OrderDetails = () =>
  import(/* webpackPrefetch: true */ './components/details/OrderDetails.vue')

export const ORDERS_ROUTE = 'orders'

const routes: RouteConfig[] = [
  {
    path: ORDERS_ROUTE,
    name: 'orders',
    component: Orders
  },
  {
    path: '/:id/details',
    name: 'details',
    component: OrderDetails
  }
]

export default routes
