import { Vue as _Vue } from 'vue/types/vue'
import VuePortal from '@linusborg/vue-simple-portal'

const SIMPLE_PORTAL_SELECTOR = 'partners-simple-portal-id'

const getPortalElement = () => document.getElementById(SIMPLE_PORTAL_SELECTOR)

export const clearPortalElement = () => {
  const portalElement = getPortalElement()
  if (!portalElement) {
    return
  }

  if (portalElement.hasChildNodes()) {
    portalElement.innerHTML = ''
  }
}

export default function install(Vue: typeof _Vue) {
  Vue.use(VuePortal, {
    name: 'VuePortal',
    defaultSelector: SIMPLE_PORTAL_SELECTOR
  })
}
