import { PollingService } from '@/modules/shared'
import { ONE_MINUTE_IN_MILLIS } from '@/modules/core/helpers'
import { TypedDispatch, GlobalGetters } from 'glovo'
import { Action } from '@/modules/store-management/store/action-types'
import {
  GET_STATUS_FAILURE,
  GET_STATUS_SUCCESS
} from '@/modules/store-management/store/action-types'
import { StoreAddressId } from '@/modules/store/data/resources'
import { StatusResponse } from '@/api/user'
import { Subscription } from 'rxjs'

let statusPollingSubscription: Subscription | null

export function setupStatusPolling(
  storeAddressId: StoreAddressId,
  getters: GlobalGetters,
  dispatch: TypedDispatch<Action>,
  getStatusFn: (payload: StoreAddressId) => Promise<StatusResponse>
) {
  const statusPolling$ = new PollingService({
    interval: ONE_MINUTE_IN_MILLIS,
    attempts: 0
  }).createPolling(() => getStatusFn(storeAddressId))

  statusPollingSubscription = statusPolling$.subscribe(
    response => {
      if (response) {
        dispatch(GET_STATUS_SUCCESS, response)
      } else {
        dispatch(GET_STATUS_FAILURE, `Empty response: ${response}`)
      }
    },
    error => dispatch(GET_STATUS_FAILURE, error)
  )
}

export function stopStatusPolling() {
  if (statusPollingSubscription && !statusPollingSubscription.closed) {
    statusPollingSubscription.unsubscribe()
  }
}
