import { ActionContext } from 'glovo'
import stockApiFactory from '@/api/stock'
import {
  Action,
  ADD_ATTRIBUTE_STOCKOUT_FAILURE,
  ADD_PRODUCT_STOCKOUT_FAILURE,
  AddAttributeStockoutFailurePayload,
  AddProductStockoutFailurePayload,
  GET_ATTRIBUTES,
  GET_ATTRIBUTES_FAILURE,
  GET_ATTRIBUTES_SUCCESS,
  GET_CONTENT,
  GET_CONTENT_FAILURE,
  GET_CONTENT_SUCCESS,
  GET_REQUESTED_PRICE_UPDATES,
  GET_REQUESTED_PRICE_UPDATES_FAILURE,
  GET_REQUESTED_PRICE_UPDATES_SUCCESS,
  GetAttributesFailurePayload,
  GetAttributesPayload,
  GetAttributesSuccessPayload,
  GetContentFailurePayload,
  GetContentPayload,
  GetContentSuccessPayload,
  GetRequestedPriceUpdatesFailurePayload,
  GetRequestedPriceUpdatesPayload,
  GetRequestedPriceUpdatesSuccessPayload,
  REMOVE_ATTRIBUTE_STOCKOUT_FAILURE,
  REMOVE_ATTRIBUTE_STOCKOUT_SUCCESS,
  REMOVE_PRODUCT_STOCKOUT_FAILURE,
  REMOVE_PRODUCT_STOCKOUT_SUCCESS,
  REMOVE_REQUEST_PRICE_UPDATE,
  REMOVE_REQUEST_PRICE_UPDATE_FAILURE,
  REMOVE_REQUEST_PRICE_UPDATE_SUCCESS,
  RemoveAttributeStockoutFailurePayload,
  RemoveAttributeStockoutSuccessPayload,
  RemoveProductStockoutFailurePayload,
  RemoveProductStockoutSuccessPayload,
  RemoveRequestPriceUpdateFailurePayload,
  RemoveRequestPriceUpdatePayload,
  RemoveRequestPriceUpdateSuccessPayload,
  REQUEST_ATTRIBUTE_PRICE_UPDATE,
  REQUEST_ATTRIBUTE_PRICE_UPDATE_FAILURE,
  REQUEST_ATTRIBUTE_PRICE_UPDATE_SUCCESS,
  REQUEST_PRODUCT_PRICE_UPDATE,
  REQUEST_PRODUCT_PRICE_UPDATE_FAILURE,
  REQUEST_PRODUCT_PRICE_UPDATE_SUCCESS,
  RequestAttributePriceUpdateFailurePayload,
  RequestAttributePriceUpdatePayload,
  RequestAttributePriceUpdateSuccessPayload,
  RequestProductPriceUpdateFailurePayload,
  RequestProductPriceUpdatePayload,
  RequestProductPriceUpdateSuccessPayload,
  SET_IS_ATTRIBUTE_SELECTED,
  SetIsAttributeSelectedPayload,
  SET_IS_PRODUCT_SELECTED,
  SetIsProductSelectedPayload,
  ADD_PRODUCT_STOCKOUT,
  AddProductStockoutPayload,
  ADD_ATTRIBUTE_STOCKOUT,
  AddAttributeStockoutPayload,
  ADD_PRODUCTS_STOCKOUT_SUCCESS,
  AddProductsStockoutSuccessPayload,
  ADD_TEMPORARY_PRODUCTS,
  CLEAR_TEMPORARY_PRODUCTS,
  ADD_ATTRIBUTES_STOCKOUT_SUCCESS,
  AddAttributesStockoutSuccessPayload,
  ADD_CUSTOM_ATTRIBUTE_STOCKOUT,
  AddCustomAttributeStockoutPayload,
  ADD_CUSTOM_PRODUCT_STOCKOUT,
  AddCustomProductStockoutPayload,
  RemoveAttributeStockoutNewPayload,
  REMOVE_ATTRIBUTE_STOCKOUT_NEW,
  REMOVE_ATTRIBUTE_STOCKOUT_SUCCESS_NEW,
  RemoveAttributeStockoutSuccessNewPayload,
  REMOVE_PRODUCT_STOCKOUT_SUCCESS_NEW,
  REMOVE_PRODUCT_STOCKOUT_NEW,
  RemoveProductStockoutNewPayload,
  RemoveProductStockoutSuccessNewPayload,
  UNDO_PRODUCTS_STOCKOUT,
  UndoProductsStockoutPayload,
  ADD_TEMPORARY_ATTRIBUTES,
  UndoAttributesStockoutPayload,
  UNDO_ATTRIBUTES_STOCKOUT,
  CLEAR_TEMPORARY_ATTRIBUTES,
  ADD_TEMPORARY_REQUEST_PRICE_UPDATE,
  RESTORE_REQUEST_PRICE_UPDATE,
  CLEAR_TEMPORARY_REQUEST_PRICE_UPDATE,
  SET_IS_GET_CONTENT_LOADING
} from './action-types'
import {
  GET_ATTRIBUTES_GETTER,
  GET_PRODUCTS_GETTER,
  GET_REQUESTED_PRICE_UPDATES_GETTER
} from '@/modules/store/data/getters'
import cloneDeep from 'lodash/cloneDeep'
import {
  ADD_ATTRIBUTES,
  ADD_PRODUCTS,
  ADD_REQUEST_PRICE_UPDATES,
  ADD_STORE_CONTENT,
  REMOVE_REQUEST_PRICE_UPDATE as DATA_REMOVE_REQUEST_PRICE_UPDATE,
  UPDATE_ATTRIBUTES
} from '@/modules/store/data/action-types'
import {
  ProductId,
  AttributeId,
  Attribute,
  Product,
  RequestPriceUpdateType
} from '@/modules/store/data/resources'
import {
  TEMPORARY_PRODUCTS_GETTER,
  TEMPORARY_ATTRIBUTES_GETTER,
  Getters,
  TEMPORARY_REQUEST_PRICE_UPDATE_GETTER
} from './getters'
import { State } from './state'

export function actionsFactory({
  content,
  removeProductStockout,
  requestProductPriceUpdate,
  fetchAttributes,
  removeAttributeStockout,
  requestAttributePriceUpdate,
  getRequestedPriceUpdates,
  removeRequestPriceUpdate,
  addProductStockout,
  addAttributeStockout,
  addCustomProductStockout,
  addCustomAttributeStockout
}: ReturnType<typeof stockApiFactory>) {
  return {
    async [GET_CONTENT](
      { dispatch, commit }: ActionContext<Action>,
      storeAddressId: GetContentPayload
    ) {
      commit(SET_IS_GET_CONTENT_LOADING, true)
      try {
        const storeContent = await content(storeAddressId)
        dispatch(GET_CONTENT_SUCCESS, {
          storeContent,
          storeAddressId
        })
      } catch (err) {
        dispatch(GET_CONTENT_FAILURE, err)
      } finally {
        commit(SET_IS_GET_CONTENT_LOADING, false)
      }
    },
    [GET_CONTENT_SUCCESS](
      { dispatch }: ActionContext<Action>,
      payload: GetContentSuccessPayload
    ) {
      dispatch(ADD_STORE_CONTENT, payload, { root: true })
    },
    [GET_CONTENT_FAILURE](
      _: ActionContext<Action>,
      payload: GetContentFailurePayload
    ) {
      console.error(payload)
    },
    async [GET_ATTRIBUTES](
      { dispatch }: ActionContext<Action>,
      storeAddressId: GetAttributesPayload
    ) {
      try {
        const attributes = await fetchAttributes(storeAddressId)
        dispatch(GET_ATTRIBUTES_SUCCESS, {
          attributes,
          storeAddressId
        })
      } catch (err) {
        dispatch(GET_ATTRIBUTES_FAILURE, err)
      }
    },
    [GET_ATTRIBUTES_SUCCESS](
      { dispatch }: ActionContext<Action>,
      payload: GetAttributesSuccessPayload
    ) {
      dispatch(ADD_ATTRIBUTES, payload, { root: true })
    },
    [GET_ATTRIBUTES_FAILURE](
      _: ActionContext<Action>,
      payload: GetAttributesFailurePayload
    ) {
      console.error(payload)
    },
    async [ADD_PRODUCT_STOCKOUT](
      { dispatch }: ActionContext<Action>,
      { storeAddressId, productIds, until }: AddProductStockoutPayload
    ) {
      const productStockoutPayload = {
        productIds,
        until
      }
      try {
        const { expirationTime } = await addProductStockout(
          storeAddressId,
          productStockoutPayload
        )
        dispatch(ADD_PRODUCTS_STOCKOUT_SUCCESS, {
          productIds,
          expirationTime
        })
      } catch (err) {
        dispatch(ADD_PRODUCT_STOCKOUT_FAILURE, err)
      }
    },
    async [ADD_CUSTOM_PRODUCT_STOCKOUT](
      { dispatch }: ActionContext<Action>,
      {
        storeAddressId,
        productIds,
        expirationTime
      }: AddCustomProductStockoutPayload
    ) {
      const customProductStockoutPayload = {
        productIds,
        expirationTime
      }
      try {
        await addCustomProductStockout(
          storeAddressId,
          customProductStockoutPayload
        )
        dispatch(ADD_PRODUCTS_STOCKOUT_SUCCESS, {
          productIds,
          expirationTime
        })
      } catch (err) {
        dispatch(ADD_PRODUCT_STOCKOUT_FAILURE, err)
      }
    },
    [ADD_PRODUCTS_STOCKOUT_SUCCESS](
      { dispatch, commit, rootGetters }: ActionContext<Action>,
      { productIds, expirationTime }: AddProductsStockoutSuccessPayload
    ) {
      const allProducts = rootGetters[GET_PRODUCTS_GETTER]
      const stockoutProducts: Product[] = []
      const productsBeforeStockout: Product[] = []
      productIds.map((productId: ProductId) => {
        const stockoutProduct = cloneDeep(allProducts[productId])
        if (stockoutProduct) {
          productsBeforeStockout.push({ ...stockoutProduct })
          stockoutProduct.enabled = false
          stockoutProduct.stockout = {
            expirationTime
          }
          stockoutProducts.push(stockoutProduct)
        }
      })
      if (stockoutProducts.length > 0) {
        dispatch(ADD_PRODUCTS, stockoutProducts, { root: true })
        commit(ADD_TEMPORARY_PRODUCTS, productsBeforeStockout)
      }
    },
    [UNDO_PRODUCTS_STOCKOUT](
      { commit, dispatch, getters }: ActionContext<Action, State, Getters>,
      storeAddressId: UndoProductsStockoutPayload
    ) {
      const allTemporaryProducts = getters[TEMPORARY_PRODUCTS_GETTER]
      allTemporaryProducts.forEach(temporaryProduct => {
        if (temporaryProduct.stockout) {
          dispatch(ADD_CUSTOM_PRODUCT_STOCKOUT, {
            storeAddressId,
            productIds: [temporaryProduct.id],
            expirationTime: temporaryProduct.stockout.expirationTime
          })
        } else {
          dispatch(REMOVE_PRODUCT_STOCKOUT_NEW, {
            storeAddressId,
            productIds: [temporaryProduct.id]
          })
        }
      })
      commit(CLEAR_TEMPORARY_PRODUCTS)
    },
    [ADD_PRODUCT_STOCKOUT_FAILURE](
      _: ActionContext<Action>,
      payload: AddProductStockoutFailurePayload
    ) {
      console.error(payload)
    },
    async [REMOVE_PRODUCT_STOCKOUT_NEW](
      { dispatch }: ActionContext<Action>,
      { storeAddressId, productIds }: RemoveProductStockoutNewPayload
    ) {
      try {
        await removeProductStockout(storeAddressId, { productIds })
        dispatch(REMOVE_PRODUCT_STOCKOUT_SUCCESS_NEW, productIds)
      } catch (err) {
        dispatch(REMOVE_PRODUCT_STOCKOUT_FAILURE, err)
      }
    },
    [REMOVE_PRODUCT_STOCKOUT_SUCCESS_NEW](
      { dispatch, commit, rootGetters }: ActionContext<Action>,
      productIds: RemoveProductStockoutSuccessNewPayload
    ) {
      const allProducts = rootGetters[GET_PRODUCTS_GETTER]
      const stockoutProducts: Product[] = []
      const productsBeforeRemoval: Product[] = []
      productIds.map((productId: ProductId) => {
        const stockoutProduct = cloneDeep(allProducts[productId])
        if (stockoutProduct) {
          productsBeforeRemoval.push({ ...stockoutProduct })
          stockoutProduct.enabled = true
          stockoutProduct.stockout = null
          stockoutProducts.push(stockoutProduct)
        }
      })
      if (stockoutProducts.length > 0) {
        dispatch(ADD_PRODUCTS, stockoutProducts, { root: true })
        commit(ADD_TEMPORARY_PRODUCTS, productsBeforeRemoval)
      }
    },
    [REMOVE_PRODUCT_STOCKOUT_SUCCESS](
      { dispatch, rootGetters }: ActionContext<Action>,
      productId: RemoveProductStockoutSuccessPayload
    ) {
      const allProducts = rootGetters[GET_PRODUCTS_GETTER]
      const stockoutProduct = cloneDeep(allProducts[productId])
      if (stockoutProduct) {
        stockoutProduct.enabled = true
        stockoutProduct.stockout = null
        dispatch(ADD_PRODUCTS, [stockoutProduct], { root: true })
      }
    },
    [REMOVE_PRODUCT_STOCKOUT_FAILURE](
      _: ActionContext<Action>,
      payload: RemoveProductStockoutFailurePayload
    ) {
      console.error(payload)
    },
    async [ADD_ATTRIBUTE_STOCKOUT](
      { dispatch }: ActionContext<Action>,
      { storeAddressId, attributeIds, until }: AddAttributeStockoutPayload
    ) {
      try {
        const attributeStockoutPayload = {
          attributeIds,
          until
        }
        const { expirationTime } = await addAttributeStockout(
          storeAddressId,
          attributeStockoutPayload
        )
        dispatch(ADD_ATTRIBUTES_STOCKOUT_SUCCESS, {
          attributeIds,
          expirationTime
        })
      } catch (err) {
        dispatch(ADD_ATTRIBUTE_STOCKOUT_FAILURE, err)
      }
    },
    async [ADD_CUSTOM_ATTRIBUTE_STOCKOUT](
      { dispatch }: ActionContext<Action>,
      {
        storeAddressId,
        attributeIds,
        expirationTime
      }: AddCustomAttributeStockoutPayload
    ) {
      try {
        const customAttributeStockoutPayload = {
          attributeIds,
          expirationTime
        }
        await addCustomAttributeStockout(
          storeAddressId,
          customAttributeStockoutPayload
        )
        dispatch(ADD_ATTRIBUTES_STOCKOUT_SUCCESS, {
          attributeIds,
          expirationTime
        })
      } catch (err) {
        dispatch(ADD_ATTRIBUTE_STOCKOUT_FAILURE, err)
      }
    },
    [ADD_ATTRIBUTES_STOCKOUT_SUCCESS](
      { commit, dispatch, rootGetters }: ActionContext<Action>,
      { attributeIds, expirationTime }: AddAttributesStockoutSuccessPayload
    ) {
      const allAttributes = rootGetters[GET_ATTRIBUTES_GETTER].byId
      const stockoutAttributes: Attribute[] = []
      const beforeStockOutAttributes: Attribute[] = []
      attributeIds.map((attributeId: AttributeId) => {
        const stockoutAttribute = cloneDeep(allAttributes[attributeId])
        if (stockoutAttribute) {
          beforeStockOutAttributes.push({ ...stockoutAttribute })
          stockoutAttribute.stockout = {
            expirationTime
          }
          stockoutAttribute.enabled = false
          stockoutAttributes.push(stockoutAttribute)
        }
      })
      if (stockoutAttributes.length > 0) {
        dispatch(UPDATE_ATTRIBUTES, stockoutAttributes, { root: true })
        commit(ADD_TEMPORARY_ATTRIBUTES, beforeStockOutAttributes)
      }
    },
    [UNDO_ATTRIBUTES_STOCKOUT](
      { commit, dispatch, getters }: ActionContext<Action, State, Getters>,
      storeAddressId: UndoAttributesStockoutPayload
    ) {
      const allTemporaryAttributes = getters[TEMPORARY_ATTRIBUTES_GETTER]
      allTemporaryAttributes.forEach(temporaryAttribute => {
        if (temporaryAttribute.stockout) {
          dispatch(ADD_CUSTOM_ATTRIBUTE_STOCKOUT, {
            storeAddressId,
            attributeIds: [temporaryAttribute.id],
            expirationTime: temporaryAttribute.stockout.expirationTime
          })
        } else {
          dispatch(REMOVE_ATTRIBUTE_STOCKOUT_NEW, {
            storeAddressId,
            attributeIds: [temporaryAttribute.id]
          })
        }
      })
      commit(CLEAR_TEMPORARY_ATTRIBUTES)
    },
    [ADD_ATTRIBUTE_STOCKOUT_FAILURE](
      _: ActionContext<Action>,
      payload: AddAttributeStockoutFailurePayload
    ) {
      console.error(payload)
    },
    [REMOVE_ATTRIBUTE_STOCKOUT_SUCCESS](
      { dispatch, rootGetters }: ActionContext<Action>,
      attributeId: RemoveAttributeStockoutSuccessPayload
    ) {
      const allAttributes = rootGetters[GET_ATTRIBUTES_GETTER].byId
      const stockoutAttribute = cloneDeep(allAttributes[attributeId])
      if (stockoutAttribute) {
        stockoutAttribute.stockout = null
        stockoutAttribute.enabled = true
        dispatch(UPDATE_ATTRIBUTES, [stockoutAttribute], { root: true })
      }
    },
    async [REMOVE_ATTRIBUTE_STOCKOUT_NEW](
      { dispatch }: ActionContext<Action>,
      { storeAddressId, attributeIds }: RemoveAttributeStockoutNewPayload
    ) {
      try {
        await removeAttributeStockout(storeAddressId, { attributeIds })
        dispatch(REMOVE_ATTRIBUTE_STOCKOUT_SUCCESS_NEW, attributeIds)
      } catch (err) {
        dispatch(REMOVE_ATTRIBUTE_STOCKOUT_FAILURE, err)
      }
    },
    [REMOVE_ATTRIBUTE_STOCKOUT_SUCCESS_NEW](
      { dispatch, commit, rootGetters }: ActionContext<Action>,
      attributeIds: RemoveAttributeStockoutSuccessNewPayload
    ) {
      const allAttributes = rootGetters[GET_ATTRIBUTES_GETTER].byId
      const stockoutAttributes: Attribute[] = []
      const attributesBeforeRemoval: Attribute[] = []
      attributeIds.map((attributeId: AttributeId) => {
        const stockoutAttribute = cloneDeep(allAttributes[attributeId])
        if (stockoutAttribute) {
          attributesBeforeRemoval.push({ ...stockoutAttribute })
          stockoutAttribute.stockout = null
          stockoutAttribute.enabled = true
          stockoutAttributes.push(stockoutAttribute)
        }
      })
      if (stockoutAttributes.length > 0) {
        dispatch(UPDATE_ATTRIBUTES, stockoutAttributes, { root: true })
        commit(ADD_TEMPORARY_ATTRIBUTES, attributesBeforeRemoval)
      }
    },
    [REMOVE_ATTRIBUTE_STOCKOUT_FAILURE](
      _: ActionContext<Action>,
      payload: RemoveAttributeStockoutFailurePayload
    ) {
      console.error(payload)
    },
    async [GET_REQUESTED_PRICE_UPDATES](
      { dispatch }: ActionContext<Action>,
      storeAddressId: GetRequestedPriceUpdatesPayload
    ) {
      try {
        const requestedPriceUpdates = await getRequestedPriceUpdates(
          storeAddressId
        )
        dispatch(GET_REQUESTED_PRICE_UPDATES_SUCCESS, requestedPriceUpdates)
      } catch (error) {
        dispatch(GET_REQUESTED_PRICE_UPDATES_FAILURE, error)
      }
    },
    [GET_REQUESTED_PRICE_UPDATES_SUCCESS](
      { dispatch }: ActionContext<Action>,
      payload: GetRequestedPriceUpdatesSuccessPayload
    ) {
      dispatch(ADD_REQUEST_PRICE_UPDATES, payload, { root: true })
    },
    [GET_REQUESTED_PRICE_UPDATES_FAILURE](
      _: ActionContext<Action>,
      payload: GetRequestedPriceUpdatesFailurePayload
    ) {
      console.error(payload)
    },
    async [REQUEST_PRODUCT_PRICE_UPDATE](
      { dispatch }: ActionContext<Action>,
      payload: RequestProductPriceUpdatePayload
    ) {
      try {
        const { storeAddressId, productId, price } = payload
        const requestedPriceUpdate = await requestProductPriceUpdate(
          storeAddressId,
          productId,
          price
        )
        dispatch(REQUEST_PRODUCT_PRICE_UPDATE_SUCCESS, {
          requestedPriceUpdate
        })
      } catch (err) {
        dispatch(REQUEST_PRODUCT_PRICE_UPDATE_FAILURE, err)
      }
    },
    [REQUEST_PRODUCT_PRICE_UPDATE_SUCCESS](
      { dispatch }: ActionContext<Action>,
      { requestedPriceUpdate }: RequestProductPriceUpdateSuccessPayload
    ) {
      dispatch(ADD_REQUEST_PRICE_UPDATES, [requestedPriceUpdate], {
        root: true
      })
    },
    [REQUEST_PRODUCT_PRICE_UPDATE_FAILURE](
      _: ActionContext<Action>,
      payload: RequestProductPriceUpdateFailurePayload
    ) {
      console.error(payload)
    },
    async [REQUEST_ATTRIBUTE_PRICE_UPDATE](
      { dispatch }: ActionContext<Action>,
      payload: RequestAttributePriceUpdatePayload
    ) {
      try {
        const { storeAddressId, attributeId, price } = payload
        const requestedPriceUpdate = await requestAttributePriceUpdate(
          storeAddressId,
          attributeId,
          price
        )
        dispatch(REQUEST_ATTRIBUTE_PRICE_UPDATE_SUCCESS, {
          requestedPriceUpdate
        })
      } catch (err) {
        dispatch(REQUEST_ATTRIBUTE_PRICE_UPDATE_FAILURE, err)
      }
    },
    [REQUEST_ATTRIBUTE_PRICE_UPDATE_SUCCESS](
      { dispatch }: ActionContext<Action>,
      { requestedPriceUpdate }: RequestAttributePriceUpdateSuccessPayload
    ) {
      dispatch(ADD_REQUEST_PRICE_UPDATES, [requestedPriceUpdate], {
        root: true
      })
    },
    [RESTORE_REQUEST_PRICE_UPDATE]({
      commit,
      dispatch,
      getters
    }: ActionContext<Action, State, Getters>) {
      const temporaryRequestPriceUpdate =
        getters[TEMPORARY_REQUEST_PRICE_UPDATE_GETTER]
      if (temporaryRequestPriceUpdate) {
        const {
          storeAddressId,
          itemId,
          newPrice,
          type
        } = temporaryRequestPriceUpdate
        if (type === RequestPriceUpdateType.PRODUCT) {
          dispatch(REQUEST_PRODUCT_PRICE_UPDATE, {
            storeAddressId,
            productId: itemId,
            price: newPrice
          })
        } else {
          dispatch(REQUEST_ATTRIBUTE_PRICE_UPDATE, {
            storeAddressId,
            attributeId: itemId,
            price: newPrice
          })
        }
        commit(CLEAR_TEMPORARY_REQUEST_PRICE_UPDATE)
      }
    },
    [REQUEST_ATTRIBUTE_PRICE_UPDATE_FAILURE](
      _: ActionContext<Action>,
      payload: RequestAttributePriceUpdateFailurePayload
    ) {
      console.error(payload)
    },
    async [REMOVE_REQUEST_PRICE_UPDATE](
      { dispatch }: ActionContext<Action>,
      requestPriceUpdateId: RemoveRequestPriceUpdatePayload
    ) {
      try {
        await removeRequestPriceUpdate(requestPriceUpdateId)
        dispatch(REMOVE_REQUEST_PRICE_UPDATE_SUCCESS, requestPriceUpdateId)
      } catch (err) {
        dispatch(REMOVE_REQUEST_PRICE_UPDATE_FAILURE, err)
      }
    },
    [REMOVE_REQUEST_PRICE_UPDATE_SUCCESS](
      { commit, rootGetters }: ActionContext<Action>,
      requestPriceUpdateId: RemoveRequestPriceUpdateSuccessPayload
    ) {
      const allRequestPriceUpdates =
        rootGetters[GET_REQUESTED_PRICE_UPDATES_GETTER]
      const removedRequestPriceUpdate =
        allRequestPriceUpdates[requestPriceUpdateId]
      commit(ADD_TEMPORARY_REQUEST_PRICE_UPDATE, removedRequestPriceUpdate)
      commit(DATA_REMOVE_REQUEST_PRICE_UPDATE, requestPriceUpdateId, {
        root: true
      })
    },
    [REMOVE_REQUEST_PRICE_UPDATE_FAILURE](
      _: ActionContext<Action>,
      payload: RemoveRequestPriceUpdateFailurePayload
    ) {
      console.error(payload)
    },
    [SET_IS_PRODUCT_SELECTED](
      { commit, rootGetters }: ActionContext<Action>,
      { id, isSelected }: SetIsProductSelectedPayload
    ) {
      const product = rootGetters[GET_PRODUCTS_GETTER][id]
      const payload = [{ ...product, selected: isSelected }]
      commit(ADD_PRODUCTS, payload, { root: true })
    },
    [SET_IS_ATTRIBUTE_SELECTED](
      { commit, rootGetters }: ActionContext<Action>,
      { id, isSelected }: SetIsAttributeSelectedPayload
    ) {
      const attribute = rootGetters[GET_ATTRIBUTES_GETTER].byId[id]
      const payload = [{ ...attribute, selected: isSelected }]
      commit(UPDATE_ATTRIBUTES, payload, { root: true })
    }
  }
}
