
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import GlovoSearchField from '@/components/common/ui/GlovoSearchField.vue'
import CollectionSelector from '@/components/common/ui/CollectionSelector.vue'
import { MenuItemType } from 'common/ui/collection-list-types'

@Component({
  name: 'products-filter',
  components: {
    GlovoSearchField,
    CollectionSelector
  }
})
export default class ProductsFilter extends Vue {
  @Prop() storeAddressId!: number

  @Prop() searchWord!: string

  get immediateSearchWord() {
    return this.searchWord
  }

  set immediateSearchWord(newValue: string) {
    this.$emit('search-word-changed', newValue)
  }

  onCollectionOrGroupSelected(type: MenuItemType, id: number) {
    this.$emit('selected-collection-or-group-changed', { id, type })
  }
}
