
import ModalWrapper from 'common/ModalWrapper.vue'
import stockApiFactory from '@/api/stock'
import { stockManagement } from '@/modules/stock/store/namespace'
import {
  ADD_ATTRIBUTE_STOCKOUT,
  AddAttributeStockoutPayload,
  ADD_PRODUCT_STOCKOUT,
  AddProductStockoutPayload,
  AddCustomProductStockoutPayload,
  AddCustomAttributeStockoutPayload,
  ADD_CUSTOM_PRODUCT_STOCKOUT,
  ADD_CUSTOM_ATTRIBUTE_STOCKOUT,
  UNDO_PRODUCTS_STOCKOUT,
  UNDO_ATTRIBUTES_STOCKOUT,
  UndoProductsStockoutPayload,
  UndoAttributesStockoutPayload
} from '@/modules/stock/store/action-types'
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop, Watch } from 'vue-property-decorator'
import {
  StoreAddressId,
  ProductId,
  AttributeId
} from '../../../modules/store/data/resources'
import { StockoutOption } from '@/modules/store/data/resources'
import { StoreAction } from '@/modules/store/types/store'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { FeatureToggle } from '@/modules/feature-toggles/store/state'
import { Getter } from 'vuex-class'
import { HttpModule } from '@/modules/http'
import { ClosingOption } from '@/modules/store-management/types'
import { NonEmptyArrayOf } from 'types/NonEmptyArrayOf'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { authentication } from '@/modules/authentication/store/namespace'
import { StoreAdmin } from '@/modules/store/data/resources'
import mParticle from '@/modules/mparticle'
import { BusinessEventNames } from 'models/BusinessEvents'

interface PickerOptions {
  disabledDate: boolean
}
export const MAXIMUM_AMOUNT_OF_CHARACTERS = 195
const SUNMI_MAXIMUM_NORMAL_HEIGHT = 650

@Component({
  name: 'Stockout',
  components: {
    ModalWrapper
  }
})
export default class Stockout extends Vue {
  @Prop({
    default: 'product',
    validator: value => {
      return ['product', 'attribute'].indexOf(value) !== -1
    }
  })
  mode!: string
  @Prop() id!: number
  @Prop() name!: string
  @Prop() ids!: number[]
  @Prop() names!: string[]
  @Prop() storeAddressId!: StoreAddressId
  @Prop({ default: true }) displayUndoBanner!: Boolean
  closedUntil: StockoutOption = null
  closedUntilCustom: string | number = ''
  loading = false
  pickerOptions: PickerOptions = {
    disabledDate: false
  }
  disablingTimeOptions: ClosingOption[] = []
  CUSTOM_CLOSING_TIME = 'custom'

  @Getter(GET_TOGGLES_GETTER) toggles!: Record<FeatureToggle, boolean>

  @authentication.Getter(GET_CURRENT_STORE_ADMIN_GETTER)
  storeAdmin?: StoreAdmin

  @stockManagement.Action(ADD_ATTRIBUTE_STOCKOUT)
  addAttributeStockout!: StoreAction<AddAttributeStockoutPayload>

  @stockManagement.Action(ADD_PRODUCT_STOCKOUT)
  addProductStockout!: StoreAction<AddProductStockoutPayload>

  @stockManagement.Action(ADD_CUSTOM_PRODUCT_STOCKOUT)
  addCustomProductStockout!: StoreAction<AddCustomProductStockoutPayload>

  @stockManagement.Action(UNDO_PRODUCTS_STOCKOUT)
  undoProductsStockout!: StoreAction<UndoProductsStockoutPayload>

  @stockManagement.Action(UNDO_ATTRIBUTES_STOCKOUT)
  undoAttributesStockout!: StoreAction<UndoAttributesStockoutPayload>

  @stockManagement.Action(ADD_CUSTOM_ATTRIBUTE_STOCKOUT)
  addCustomAttributeStockout!: StoreAction<AddCustomAttributeStockoutPayload>

  @Watch('closedUntilCustom')
  onClosedUntilCustomChange() {
    if (this.closedUntilCustom) {
      this.closedUntil = this.CUSTOM_CLOSING_TIME
    }
  }

  get title() {
    if (this.toggles.FE_PARTNERS_MENU_MANAGEMENT_CHANGES_ENABLED) {
      return this.mode === 'product'
        ? this.$t('stockout.disable.products')
        : this.$t('stockout.disable.attributes')
    }
    return this.$t('stockout.title')
  }

  get filteredItemNames() {
    const joinedItemNames = [...this.names]
    if (this.toggles.FE_PARTNERS_MENU_MANAGEMENT_CHANGES_ENABLED) {
      while (joinedItemNames.join(', ').length > MAXIMUM_AMOUNT_OF_CHARACTERS) {
        joinedItemNames.pop()
      }
    }
    return joinedItemNames
  }

  get omittedItemsCount() {
    return this.names.length - this.filteredItemNames.length
  }

  get hasOmittedItems() {
    return this.omittedItemsCount > 0
  }

  get translationPath() {
    return this.hasOmittedItems && this.menuManagementChangesEnabled
      ? 'stockout.subtitle.omitted'
      : 'stockout.subtitle.whole'
  }

  get isSunmiInKioskMode() {
    return window.innerHeight > SUNMI_MAXIMUM_NORMAL_HEIGHT && !!window.Android
  }

  get popperClass() {
    return this.isSunmiInKioskMode
      ? 'stockout-picker-fullscreen'
      : 'stockout-picker'
  }

  get menuManagementChangesEnabled() {
    return this.toggles.FE_PARTNERS_MENU_MANAGEMENT_CHANGES_ENABLED
  }

  mounted() {
    this.retrieveDisablingOptions()
  }

  async retrieveDisablingOptions() {
    if (this.mode === 'product') {
      await this.retrieveProductDisablingOptions()
    } else {
      await this.retrieveAttributeDisablingOptions()
    }
    this.disablingTimeOptions!.push({
      value: this.CUSTOM_CLOSING_TIME,
      displayText: this.$i18n.t('time.options.custom').toString()
    })
  }

  async retrieveProductDisablingOptions() {
    try {
      const { getProductsDisablingOptions } = stockApiFactory(
        HttpModule.fetchService
      )
      const result = await getProductsDisablingOptions()
      this.disablingTimeOptions = result.untilOptions
    } catch (error) {
      console.error(`getProductsDisablingOptions failure: ${error}`)
    }
  }

  async retrieveAttributeDisablingOptions() {
    try {
      const { getAttributesDisablingOptions } = stockApiFactory(
        HttpModule.fetchService
      )
      const result = await getAttributesDisablingOptions()
      this.disablingTimeOptions = result.untilOptions
    } catch (error) {
      console.error(`getAttributesDisablingOptions failure: ${error}`)
    }
  }

  change(value: string) {
    const stockoutPickerElement: HTMLElement | null = document.getElementById(
      'stockoutPicker'
    )
    if (value === this.CUSTOM_CLOSING_TIME && stockoutPickerElement) {
      const firstIconTimeElement = stockoutPickerElement.getElementsByClassName(
        'el-icon-time'
      )[0] as HTMLElement
      firstIconTimeElement.click()
    }
    this.$forceUpdate()
  }

  async acceptStockout() {
    this.loading = true
    await this.updateStockout()
    this.loading = false
    this.$emit('close')
    if (this.menuManagementChangesEnabled && this.displayUndoBanner) {
      this.showUndoBanner()
    }
  }

  showUndoBanner() {
    if (this.mode === 'product') {
      this.$toast.show({
        message: this.$tc('stockout.products.disabled', this.ids.length),
        actionLabel: this.$t('common.undo').toString(),
        onActionClick: () => this.undoProductsStockout(this.storeAddressId)
      })
    } else {
      this.$toast.show({
        message: this.$tc('stockout.attributes.disabled', this.ids.length),
        actionLabel: this.$t('common.undo').toString(),
        onActionClick: () => this.undoAttributesStockout(this.storeAddressId)
      })
    }
  }

  async updateStockout() {
    if (this.mode === 'product') {
      this.updateProductStockout()
    } else {
      this.updateAttributeStockout()
    }
  }

  async updateProductStockout() {
    let productIds = this.ids
    if (this.id) {
      productIds = [this.id]
    }

    this.logMParticleStockout()

    if (this.closedUntil === this.CUSTOM_CLOSING_TIME) {
      await this.addCustomProductStockout({
        productIds: productIds as NonEmptyArrayOf<ProductId>,
        expirationTime: this.closedUntilCustom.valueOf(),
        storeAddressId: this.storeAddressId
      })
    } else {
      await this.addProductStockout({
        productIds: productIds as NonEmptyArrayOf<ProductId>,
        until: this.closedUntil,
        storeAddressId: this.storeAddressId
      })
    }
  }

  async updateAttributeStockout() {
    let attributeIds = this.ids
    if (this.id) {
      attributeIds = [this.id]
    }

    this.logMParticleStockout()

    if (this.closedUntil === this.CUSTOM_CLOSING_TIME) {
      await this.addCustomAttributeStockout({
        attributeIds: attributeIds as NonEmptyArrayOf<ProductId>,
        expirationTime: this.closedUntilCustom.valueOf(),
        storeAddressId: this.storeAddressId
      })
    } else {
      await this.addAttributeStockout({
        attributeIds: attributeIds as NonEmptyArrayOf<AttributeId>,
        until: this.closedUntil,
        storeAddressId: this.storeAddressId
      })
    }
  }

  logMParticleStockout() {
    const storeAdminId = this.storeAdmin ? this.storeAdmin.id : 0
    const disableTimeFrame =
      this.closedUntil === this.CUSTOM_CLOSING_TIME
        ? this.closedUntilCustom.valueOf()
        : this.closedUntil

    if (this.mode === 'product') {
      mParticle.logEvent(
        BusinessEventNames.productDisableAccept,
        mParticle.EventType.UserContent,
        {
          productIds: this.ids,
          storeAddressId: this.storeAddressId,
          storeAdminId,
          disableTimeFrame
        }
      )
    } else {
      mParticle.logEvent(
        BusinessEventNames.attributeDisableAccept,
        mParticle.EventType.UserContent,
        {
          attributeIds: this.ids,
          storeAddressId: this.storeAddressId,
          storeAdminId,
          disableTimeFrame
        }
      )
    }
  }
}
