
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop, Watch } from 'vue-property-decorator'
import { currency } from '@/modules/core/filters'
import QuantitySelector from './QuantitySelector.vue'
import Icon from 'common/Icon.vue'
import GlovoBtn from '@/components/common/GlovoBtn.vue'
import GlovoCheckbox from '@/components/common/ui/checkbox/GlovoCheckbox.vue'
import ZoomedImageView from './ZoomedImageView.vue'
import { orderManagement } from '@/modules/orders/store/namespace'
import {
  SET_IS_GROCERIES_CHECKBOX_CHECKED,
  SetIsGroceriesCheckboxCheckedPayload,
  OPEN_ORDER_PRODUCT_EDIT_MODAL,
  OpenOrderProductEditModalPayload
} from '@/modules/orders/store'
import { GET_IS_GROCERIES_PRODUCT_CHECKED } from '@/modules/orders/store/getters'
import OrderProduct from 'models/OrderProduct'

import type {
  Product,
  ProductCustomization
} from '@/modules/store/data/resources/product'
import type { BoughtProductId } from '@/modules/store/data/resources/order-details'

@Component({
  name: 'OrderProductItem',
  components: {
    Icon,
    GlovoBtn,
    GlovoCheckbox,
    QuantitySelector,
    ZoomedImageView
  }
})
export default class OrderProductItem extends Vue {
  @Prop({ default: null }) product!: Product
  @Prop({ default: null }) error!: string
  @Prop() shouldShowGroceriesCheckbox!: boolean

  private tmpProduct = new OrderProduct()
  private productInZoomedView: { image: string; title: string } | null = null

  @orderManagement.Getter(GET_IS_GROCERIES_PRODUCT_CHECKED)
  getIsGroceriesProductChecked!: (id: BoughtProductId) => boolean

  get isGroceriesCheckboxChecked() {
    const { isGroceriesCheckboxChecked, id } = this.product
    const valueFromOldState = isGroceriesCheckboxChecked
    const valueFromNewState = this.getIsGroceriesProductChecked(id)
    return valueFromOldState || valueFromNewState
  }
  get initialQuantity() {
    if (!this.product) {
      return 1
    }
    return this.tmpProduct.hasReference()
      ? this.tmpProduct.referenceQuantity()
      : this.product.quantity
  }
  get shouldShowZoomedView() {
    return Boolean(
      this.productInZoomedView &&
        this.productInZoomedView.image &&
        this.productInZoomedView.title
    )
  }

  get isProductRemoved() {
    return this.tmpProduct.quantity === 0
  }

  @Watch('product')
  watchProduct() {
    this.initProduct()
  }
  @Watch('product.mode')
  watchProductMode() {
    this.initProduct()
  }

  mounted() {
    this.initProduct()
  }

  @orderManagement.Action(OPEN_ORDER_PRODUCT_EDIT_MODAL)
  openOrderProductEditModal!: (
    payload: OpenOrderProductEditModalPayload
  ) => void

  @orderManagement.Action(SET_IS_GROCERIES_CHECKBOX_CHECKED)
  setIsGroceriesCheckboxChecked!: (
    payload: SetIsGroceriesCheckboxCheckedPayload
  ) => void

  onGroceriesCheckboxChange(isChecked: boolean, id: number) {
    this.setIsGroceriesCheckboxChecked({ isChecked, id })
  }

  initProduct() {
    const orderProduct = OrderProduct.parse(this.product)
    this.tmpProduct.parse(this.product)
    this.tmpProduct.setReference(
      orderProduct.hasReference() && orderProduct.originalCustomerProductRequest
        ? orderProduct.originalCustomerProductRequest
        : orderProduct
    )
  }

  alterQuantity(add: boolean) {
    let quantity = this.tmpProduct.quantity
    if (add) {
      quantity++
    } else if (this.tmpProduct.quantity > 0) {
      quantity--
    }
    this.tmpProduct.quantity = quantity
  }

  revert() {
    this.$emit('revert')
  }

  getCurrency(val: string) {
    return currency(val)
  }

  enableZoomedViewForProduct(image: string, title: string) {
    this.productInZoomedView = {
      image,
      title
    }
  }

  disableZoomedViewForProduct() {
    this.productInZoomedView = null
  }

  shouldShowPriceImpact(customization: ProductCustomization) {
    return Boolean(customization.priceImpact)
  }

  onClickEditProductButton() {
    this.openOrderProductEditModal({
      productId: this.product.id
    })
  }
}
