import {
  Store,
  StoreAddressDTO,
  StoresMap
} from '@/modules/store/data/resources'
import { PromotionDTO, PromotionsListDTO, PromotionStoreDTO } from '../api/dtos'
import { Promotion } from './promotion'

export class PromotionsList implements PromotionsListDTO {
  private readonly promotionsArray: Promotion[]

  constructor(promotionsListDTO: PromotionsListDTO, allStores: StoresMap) {
    const { promotions = [] } = promotionsListDTO || {}
    this.promotionsArray = promotions.map(promotion => {
      const promotionStores = this.getFilteredStoreMap(promotion, allStores)
      return mapPromotion(promotion, promotionStores)
    })
  }

  private getFilteredStoreMap(
    promotion: PromotionDTO,
    allStores: StoresMap
  ): StoresMap {
    const promotionStoresFromDTO = (dto: PromotionStoreDTO) => {
      const storeId = dto.id
      const store = allStores[storeId]

      const filteredStoreAddresses = this.getFilteredStoreAddresses(store, dto)

      return {
        ...store,
        storeAddresses: filteredStoreAddresses,
        storeAddressIds: filteredStoreAddresses.map(address => address.id)
      }
    }

    const { stores } = promotion
    return stores
      .map(promotionStoresFromDTO)
      .reduce((storesMap, store) => ({ ...storesMap, [store.id]: store }), {})
  }

  private getFilteredStoreAddresses(
    store: Store,
    promotionStoreDTO: PromotionStoreDTO
  ): StoreAddressDTO[] {
    return store.storeAddresses.filter(storeAddressDTO => {
      const foundStoreAddress = promotionStoreDTO.addresses.find(addressId => {
        return storeAddressDTO.id === addressId
      })
      return foundStoreAddress != undefined
    })
  }

  get promotions() {
    return this.promotionsArray || []
  }

  get hasPromotions() {
    return this.promotions.length > 0
  }
}

const mapPromotion = (
  promotionDto: PromotionDTO,
  stores: StoresMap
): Promotion => {
  return new Promotion(promotionDto, stores)
}
