import {
  Alignment,
  LineType,
  TextSize,
  Typeface
} from '@/services/printer/enums'

export interface Element {
  type: string
}

export class Bitmap implements Element {
  type = 'Bitmap'

  constructor(public base64: string) {}

  toString() {
    return '[BMP]\n'
  }
}

export class QrBarcode implements Element {
  type = 'QR'

  constructor(public data: string) {}

  toString() {
    return `[QR-${this.data}]\n`
  }
}

export class Barcode implements Element {
  type = 'Barcode'

  constructor(public data: string) {}

  toString() {
    return `[BC-${this.data}]\n`
  }
}

export class Logo implements Element {
  type = 'Logo'

  toString() {
    return '[Glovo]\n'
  }
}

export class Text implements Element {
  type = 'Text'

  constructor(
    public size: TextSize | number = TextSize.MEDIUM,
    public alignment: Alignment = Alignment.CENTER,
    public text: string = '',
    public typeface = Typeface.REGULAR
  ) {}

  toString() {
    if (this.alignment == Alignment.RIGHT) {
      return `    ${this.text}`
    }
    return `${this.text}`
  }
}

export class TextField implements Element {
  type = 'Field'
  public typeface = Typeface.REGULAR

  constructor(
    public fieldName: string,
    public text: string,
    public size: TextSize | number = TextSize.MEDIUM,
    public alignment: Alignment = Alignment.CENTER
  ) {}

  toString() {
    return `${this.fieldName}: ${this.text}`
  }
}

export class Line extends Text {
  type = 'Line'

  constructor(
    public lineType: LineType = LineType.ASTERISK,
    public size: TextSize | number = TextSize.MEDIUM,
    public alignment: Alignment = Alignment.CENTER
  ) {
    super(size, alignment)
  }

  toString() {
    switch (this.lineType) {
      case LineType.DOT:
        return '. . . . . . . . .\n'
      case LineType.DOUBLE_DOT:
        return ': : : : : : : : :\n'
      case LineType.EQUALS:
        return '= = = = = = = = =\n'
      case LineType.ASTERISK:
        return '* * * * * * * * *\n'
      case LineType.DASH:
      default:
        return '- - - - - - - - -\n'
    }
  }
}

export class LineBreak implements Element {
  type = 'LineBreak'

  constructor(public lines = 1) {}

  toString() {
    return '\n'.repeat(this.lines)
  }
}

export class TextBox extends Text {
  type = 'Box'

  constructor(
    size?: TextSize | number,
    alignment?: Alignment,
    text?: string,
    public height?: number,
    public width?: number
  ) {
    super(size, alignment, text)
  }

  toString() {
    return `[[${this.text}]]\n`
  }
}
