
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { FeatureName, LegalDocumentType, loadLegalDocument } from '../services'

@Component({ name: 'LegalDocument' })
export default class LegalDocument extends Vue {
  private termsText = ''
  @Prop({ required: true }) featureName!: FeatureName
  @Prop() documentType!: LegalDocumentType

  created() {
    this.loadTermsText()
  }

  get language() {
    return this.$i18n.locale
  }

  async loadTermsText() {
    const termsDocument = await this.getTermsDocument()
    this.termsText =
      termsDocument?.body || (this.$t('common.table.has.no.data') as string)
  }

  getTermsDocument() {
    const { documentType, featureName, language } = this
    return loadLegalDocument({
      documentType,
      featureName,
      language
    })
  }
}
