import { Vue as _Vue } from 'vue/types/vue'

type PhoneFormatter = (inputString: string) => string

export default function install(Vue: typeof _Vue) {
  Vue.prototype.$phone = (inputString: string | unknown): string | unknown => {
    if (isInputNumberInvalid(inputString)) {
      return inputString
    }

    let { countryCode, postfix } = splitNumberToCountryCodeAndPostfix(
      inputString as string
    )

    if (countryCode && postfix) {
      if (postfix.length % 3 === 0) {
        // countryCode ### ### ###
        postfix = postfix.replace(/(\d{3})/g, ' $1')
      } else if (postfixHasIrregularLength(postfix)) {
        // countryCode ### ### ##
        postfix = postfix.replace(/(\d{2,3})/g, ' $1')
      } else if (postfix.length === 10) {
        // countryCode ### ### ####
        postfix = postfix.replace(/(\d{3})(\d{3})(\d{4})/, ' $1 $2 $3')
      } else if (postfix.length === 8) {
        // countryCode ## ### ###
        postfix = postfix.replace(/(\d{2})(\d{3})(\d{3})/, ' $1 $2 $3')
      } else if (postfix.length % 2 === 0) {
        // countryCode ## ## ##
        postfix = postfix.replace(/(\d{2})/g, ' $1')
      }
      return `${countryCode}${postfix}`
    }
    return inputString
  }
}

const MIN_INTERNATIONAL_NUMBER_LENGTH = 7
const MAX_INTERNATIONAL_NUMBER_LENGTH = 15
const OPTIONAL_PREFIX_LENGTH = 1

function isInputNumberInvalid(inputNumber: string | unknown): boolean {
  return (
    typeof inputNumber !== 'string' ||
    inputNumber.length < MIN_INTERNATIONAL_NUMBER_LENGTH ||
    inputNumber.length >
      MAX_INTERNATIONAL_NUMBER_LENGTH + OPTIONAL_PREFIX_LENGTH
  )
}

function postfixHasIrregularLength(postfix: string): boolean {
  return (
    postfix.length === 14 ||
    postfix.length === 13 ||
    postfix.length === 11 ||
    postfix.length === 7 ||
    postfix.length === 5
  )
}

function splitNumberToCountryCodeAndPostfix(
  inputNumber: string
): {
  countryCode: string
  postfix: string
} {
  // 1st optional group is '+', 2nd group is country code, 3rd groups is region and postfix
  const regexForCountryCodes = /(\D*)(1|7|20|27|30|31|32|33|34|36|39|40|41|43|44|45|46|47|48|49|51|52|53|54|55|56|57|58|60|61|62|63|64|65|66|81|82|84|86|90|91|92|93|94|95|98|211|212|213|216|218|220|221|222|223|224|225|226|227|228|229|230|231|232|233|234|235|236|237|238|239|240|241|242|243|244|245|246|248|249|250|251|252|253|254|255|256|257|258|260|261|262|263|264|265|266|267|268|269|290|291|297|298|299|350|351|352|353|354|355|356|357|358|359|370|371|372|373|374|375|376|377|378|380|381|382|383|385|386|387|389|420|421|423|500|501|502|503|504|505|506|507|508|509|590|591|592|593|594|595|596|597|598|599|670|672|673|674|675|676|677|678|679|680|681|682|683|685|686|687|688|689|690|691|692|850|852|853|855|856|880|886|960|961|962|963|964|965|966|967|968|970|971|972|973|974|975|976|977|992|993|994|995|996|998)(.*)/

  const preFormattedNumber = inputNumber
    .trim()
    .replace(regexForCountryCodes, '$1$2 $3')
  const [countryCode, postfix] = preFormattedNumber.split(' ')
  return { countryCode, postfix }
}

declare module 'vue/types/vue' {
  interface Vue {
    $phone: PhoneFormatter
  }
}
