import { Vue as _Vue } from 'vue/types/vue'
import ErrorTrackerFactory from './error-tracker-factory'
import ErrorTrackerPluginOptions from './error-tracker-plugin-options'

function install(Vue: typeof _Vue, options: ErrorTrackerPluginOptions) {
  assertRequiredOptions(options, ['sentryDSN'])
  const instance = ErrorTrackerFactory.createErrorTracker(options)
  Vue.prototype.$glvTracker = Vue.glvTracker = instance

  // Start capturing errors
  instance.captureUncaughtErrors()
}

function assertRequiredOptions(
  options: ErrorTrackerPluginOptions,
  required: string[]
) {
  required.forEach(opt => {
    if (!(opt in options)) {
      throw new Error(
        `You must provide ${opt} option to initialize ErrorTrackerPlugin`
      )
    }
  })
}

const ErrorTrackerPlugin = { install }
export default ErrorTrackerPlugin
