import { Instant } from 'types/dates'
import { CityCode } from '@/modules/store/data/resources'
import {
  PromotionStoreDTO,
  CreatePromotionStoreDTO
} from './promotion-stores-dtos'
import { PromotionQualityTier } from './promotion-quality-dtos'

export type PromotionId = number

export enum PromotionType {
  FREE_DELIVERY = 'FREE_DELIVERY',
  FLAT_DELIVERY = 'FLAT_DELIVERY',
  PERCENTAGE_DISCOUNT = 'PERCENTAGE_DISCOUNT',
  TWO_FOR_ONE = 'TWO_FOR_ONE'
}

export enum PromotionStatusName {
  ENABLED = 'ENABLED',
  DISABLED = 'DISABLED',
  COMPLETED = 'COMPLETED'
}

export interface PromotionStatus {
  name: PromotionStatusName
  description?: string
}

export interface PromotionBudget {
  amount: number
}

export interface PromotionSpendingSummary {
  currentSpend: number
}

interface PromotionDTOBase {
  name: string
  type: PromotionType
  cityCode: CityCode
  startDate: Instant
  endDate: Instant
}

export interface CreatePromotionDTO extends PromotionDTOBase {
  stores: CreatePromotionStoreDTO[]
  enabled: boolean
  budget?: number
  percentage?: number
}

export interface PromotionDTO extends PromotionDTOBase {
  id: PromotionId
  qualityTier: PromotionQualityTier
  stores: PromotionStoreDTO[]
  status: PromotionStatus
  budget: PromotionBudget
  spendingSummary: PromotionSpendingSummary
  percentage?: string
}

export interface PromotionsListDTO {
  promotions: PromotionDTO[]
}

export interface UpdatePromotionRequestDTO {
  enabled: boolean
}
