import { FIVE_MINUTES_IN_SECONDS } from '@/modules/core/helpers'

export const EXPIRATION_GUARD = FIVE_MINUTES_IN_SECONDS

export class JWTHelper {
  static tokenIsGoingToExpire(expirationTime: number) {
    const now = new Date()
    return addSecondsToDate(EXPIRATION_GUARD, now).getTime() > expirationTime
  }
  static hasTokenExpired(
    expirationTime: number,
    now: number = Date.now()
  ): boolean {
    return now > expirationTime
  }
}

export const addSecondsToDate = (seconds: number, date = new Date()) => {
  date.setSeconds(date.getSeconds() + seconds)
  return date
}
