import { GlvModule } from 'glovo'
import { Vue as _Vue } from 'vue/types/vue'
import { installVueI18n } from './plugins'
import VueI18n from 'vue-i18n'
import { vueI18nFactory } from './services'
import moment from 'moment-timezone'
class I18nModule implements GlvModule {
  i18n!: VueI18n
  install(Vue: typeof _Vue) {
    installVueI18n(Vue)
    this.i18n = vueI18nFactory()
    moment.locale(this.i18n.locale)
  }
}

const glvModule = new I18nModule()

export default glvModule
