import { OrdersRequest } from './orders-request'
import { OrdersResponse } from './orders-response'

export abstract class AbstractLiveOrdersService {
  notify?: (orders: OrdersResponse) => void
  constructor(protected requests: OrdersRequest) {}
  abstract start(): void
  abstract stop(): void
  abstract resume(): void
  abstract pause(): void
  abstract subscribe(subscriptionFn: (orders: OrdersResponse) => void): void
  abstract isPolling(): boolean
}
