import Storage from 'models/Storage'

export function getDeviceVendor(): string {
  // TODO : Define the vendor names (+model) to track  and implement the
  // logic to identify them
  return isSunmiDevice() ? 'sunmi' : 'unknown'
}

export function isSunmiDevice(): boolean {
  // example agent sections for V1: V1s-G Build/MRA58K and for V2: V2 Build/N6F26Q
  return navigator.userAgent.match(/V1|V2/i) ? true : false
}

export function getDeviceId(): string {
  const device = Storage.get(Storage.items.DEVICE)
  return (device && device.id) || 'unknown'
}

const trackingIdSeed = new Date()
export function getDeviceSessionTrackingId(): string {
  const hour = String(trackingIdSeed.getHours()).padStart(2, '0')
  const minute = String(trackingIdSeed.getMinutes()).padStart(2, '0')
  const second = String(trackingIdSeed.getSeconds()).padStart(2, '0')
  const milli = String(trackingIdSeed.getMilliseconds())
    .padStart(2, '0')
    .slice(0, 2)
  return `${hour}${minute}-${second}${milli}`
}
