import moment from 'moment-timezone'
import CurrencyService from '@/services/currency/CurrencyService'
import I18n from '@/modules/i18n'
import DateTimeService from '@/services/datetime/DateTimeService'

export const formatHours = (value?: string): string => {
  if (!value) {
    return '-'
  }

  if (typeof value === 'string') {
    return value
  }

  return moment(value).format('HH:mm')
}

export const formatDate = (value?: string): string => {
  if (!value) {
    return '-'
  }
  return moment(value).calendar(undefined, {
    sameDay: `[${I18n.i18n!.t('common.today')}]`,
    nextDay: `[${I18n.i18n!.t('common.tomorrow')}]`,
    nextWeek: 'dddd',
    lastDay: `[${I18n.i18n!.t('common.yesterday')}]`,
    lastWeek: 'DD/MM/YYYY',
    sameElse: 'DD/MM/YYYY'
  })
}

export const dateFilter = (value?: string | number): string => {
  if (!value) {
    return '-'
  }
  return moment(value).calendar()
}

export const formatMinutes = (value?: number): string | null => {
  if (!value) return ''
  return DateTimeService.formatDurationInMinutes(value)
}

export const currency = (value?: number | string): string => {
  if (value || value === 0 || value === '0') {
    return CurrencyService.format(value)
  }
  return ''
}
