import { GlvModule } from 'glovo'
import { Store } from 'vuex'
import { AbstractFetchService } from '@/modules/http'
import { State as RootState } from '@/modules/store'
import { storeFactory } from './store'

export class InvoicesModule implements GlvModule {
  readonly name = 'invoices'
  constructor(
    private store: Store<RootState>,
    private fetchService: AbstractFetchService
  ) {}

  install() {
    const invoicesStore = storeFactory(this.fetchService)
    this.store.registerModule([this.name], invoicesStore)
  }
}
