import { State } from './state'

export const GET_ORDERS_GETTER = 'GET_ORDERS_GETTER'
export const GET_CITIES_GETTER = 'GET_CITIES_GETTER'
export const GET_STORES_GETTER = 'GET_STORES_GETTER'
export const GET_STORE_ADDRESSES_GETTER = 'GET_STORE_ADDRESSES_GETTER'
export const GET_PRODUCTS_GETTER = 'GET_PRODUCTS_GETTER'
export const GET_ATTRIBUTES_GETTER = 'GET_ATTRIBUTES_GETTER'
export const GET_STORE_ADMINS_GETTER = 'GET_STORE_ADMINS_GETTER'
export const GET_REQUESTED_PRICE_UPDATES_GETTER =
  'GET_REQUESTED_PRICE_UPDATES_GETTER'
export const GET_STORE_COLLECTION_GROUPS_GETTER =
  'GET_STORE_COLLECTION_GROUPS_GETTER'
export const GET_STORE_COLLECTIONS_GETTER = 'GET_STORE_COLLECTIONS_GETTER'
export const GET_STORE_COLLECTION_SECTIONS_GETTER =
  'GET_STORE_COLLECTION_SECTIONS_GETTER'

export interface DataGetters {
  [GET_ORDERS_GETTER]: ReturnType<typeof getters.GET_ORDERS_GETTER>
  [GET_CITIES_GETTER]: ReturnType<typeof getters.GET_CITIES_GETTER>
  [GET_STORES_GETTER]: ReturnType<typeof getters.GET_STORES_GETTER>
  [GET_STORE_ADDRESSES_GETTER]: ReturnType<
    typeof getters.GET_STORE_ADDRESSES_GETTER
  >
  [GET_PRODUCTS_GETTER]: ReturnType<typeof getters.GET_PRODUCTS_GETTER>
  [GET_ATTRIBUTES_GETTER]: ReturnType<typeof getters.GET_ATTRIBUTES_GETTER>
  [GET_STORE_ADMINS_GETTER]: ReturnType<typeof getters.GET_STORE_ADMINS_GETTER>
  [GET_REQUESTED_PRICE_UPDATES_GETTER]: ReturnType<
    typeof getters.GET_REQUESTED_PRICE_UPDATES_GETTER
  >
  [GET_STORE_COLLECTION_GROUPS_GETTER]: ReturnType<
    typeof getters.GET_STORE_COLLECTION_GROUPS_GETTER
  >
  [GET_STORE_COLLECTIONS_GETTER]: ReturnType<
    typeof getters.GET_STORE_COLLECTIONS_GETTER
  >
  [GET_STORE_COLLECTION_SECTIONS_GETTER]: ReturnType<
    typeof getters.GET_STORE_COLLECTIONS_GETTER
  >
}

export const getters = {
  [GET_ORDERS_GETTER]: (state: State) => state.orders,
  [GET_CITIES_GETTER]: (state: State) => state.cities,
  [GET_STORES_GETTER]: (state: State) => state.stores,
  [GET_STORE_ADDRESSES_GETTER]: (state: State) => state.storeAddresses,
  [GET_PRODUCTS_GETTER]: (state: State) => state.products,
  [GET_ATTRIBUTES_GETTER]: (state: State) => state.attributes,
  [GET_STORE_ADMINS_GETTER]: (state: State) => state.storeAdmins,
  [GET_REQUESTED_PRICE_UPDATES_GETTER]: (state: State) =>
    state.requestPriceUpdates,
  [GET_STORE_COLLECTION_GROUPS_GETTER]: (state: State) =>
    state.storeCollectionGroups,
  [GET_STORE_COLLECTIONS_GETTER]: (state: State) => state.storeCollections,
  [GET_STORE_COLLECTION_SECTIONS_GETTER]: (state: State) =>
    state.storeCollectionSections
}
