import Butter, { ButterStatic } from 'buttercms'
import { butterCmsToken } from '@/api/localConfig'
import telemetry from '@/modules/telemetry/telemetry'

export interface CmsPageResponse {
  body: string
  name: string
  page_type: string
  slug: string
}

const CMS_SERVICE_ERROR = 'cms_service_error'

class CmsService {
  private cms!: ButterStatic

  constructor() {
    try {
      this.initButterCms()
    } catch (e) {
      logError(e)
    }
  }

  async page(slug: string) {
    return safeCall(async () => {
      const cmsPage = await this.cms.page.retrieve('*', slug)
      return parseCmsResponse(cmsPage)
    })
  }

  private initButterCms() {
    if (!butterCmsToken) {
      throw new Error('buttercms needs a token to initialise')
    }

    this.cms = Butter(butterCmsToken)
  }
}

const parseCmsResponse = (response: Butter.Response): CmsPageResponse => {
  const { fields, ...restData } = response.data?.data
  return {
    ...restData,
    body: fields?.body
  }
}

const logError = (error: Error) => {
  telemetry.Log.error(CMS_SERVICE_ERROR, `buttercms failed: ${error?.message}`)
}

const safeCall = async <T>(callback: () => Promise<T>): Promise<T | null> => {
  try {
    return await callback()
  } catch (e) {
    logError(e)
    return null
  }
}

const cmsService = new CmsService()
export default cmsService
