import { OrdersListDTO } from '@/api/dtos/orders/list/OrdersListDTO'
import { OrdersList } from '@/modules/store/data/resources/orders-list/orders-list'
import { PreparationStage } from '@/modules/orders/constants'

export function mapOrdersList(orders: OrdersListDTO): OrdersList {
  return {
    served: orders.served.map(servedOrder => ({
      ...servedOrder,
      statusDetails: {
        preparationStage: PreparationStage.SERVED,
        isReady: false // since order ready button is only present for accepted orders
      }
    })),
    cancelled: orders.cancelled.map(cancelledOrder => ({
      ...cancelledOrder,
      statusDetails: {
        preparationStage: PreparationStage.UNKNOWN,
        isReady: false // since order ready button is only present for accepted orders
      }
    })),
    accepted: orders.accepted,
    pending: orders.pending
  }
}
