import { State } from './state'
import {
  AddTemporaryProductsPayload,
  CLEAR_TEMPORARY_PRODUCTS,
  ADD_TEMPORARY_PRODUCTS,
  ADD_TEMPORARY_ATTRIBUTES,
  CLEAR_TEMPORARY_ATTRIBUTES,
  AddTemporaryAttributesPayload,
  ADD_TEMPORARY_REQUEST_PRICE_UPDATE,
  CLEAR_TEMPORARY_REQUEST_PRICE_UPDATE,
  AddTemporaryRequestPriceUpdatePayload,
  SET_IS_GET_CONTENT_LOADING,
  SetIsGetContentLoading
} from './action-types'

export const mutations = {
  [ADD_TEMPORARY_PRODUCTS](
    { dataState }: State,
    payload: AddTemporaryProductsPayload
  ) {
    dataState.temporaryProducts = [...payload]
  },
  [CLEAR_TEMPORARY_PRODUCTS]({ dataState }: State) {
    dataState.temporaryProducts = []
  },
  [ADD_TEMPORARY_ATTRIBUTES](
    { dataState }: State,
    payload: AddTemporaryAttributesPayload
  ) {
    dataState.temporaryAttributes = [...payload]
  },
  [CLEAR_TEMPORARY_ATTRIBUTES]({ dataState }: State) {
    dataState.temporaryAttributes = []
  },
  [ADD_TEMPORARY_REQUEST_PRICE_UPDATE](
    { dataState }: State,
    payload: AddTemporaryRequestPriceUpdatePayload
  ) {
    dataState.temporaryRequestPriceUpdate = { ...payload }
  },
  [CLEAR_TEMPORARY_REQUEST_PRICE_UPDATE]({ dataState }: State) {
    dataState.temporaryRequestPriceUpdate = null
  },
  [SET_IS_GET_CONTENT_LOADING](
    { uiState }: State,
    payload: SetIsGetContentLoading
  ) {
    uiState.isGetContentLoading = payload
  }
}
