import { Store } from 'vuex'
import { State } from '@/modules/store'
import telemetry from '@/modules/telemetry/telemetry'
import mParticle from '@/modules/mparticle'
import { getDeviceId } from '@/modules/shared/helpers'
import { GET_CURRENT_STORE_ADMIN_GETTER } from '@/modules/authentication'
import { PerformanceMemory } from 'types/performance'
import { BusinessEventNames } from 'models/BusinessEvents'
import { FIVE_MINUTES_IN_MILLIS } from '@/modules/core/helpers'

export const LOG_INTERVAL_IN_MILLIS = FIVE_MINUTES_IN_MILLIS
export const MAX_ALLOWED_SAMPLES = 100

/**
 * **Memory Monitoring Service** : Performs intervallic checks on the consumed
 * memory, and emits metrics in each check cycle. Only a limited amount of
 * samples are allowed in order to keep the metrics granularity low. Once the
 * limit is reached no more metrics are emitted.
 */
export default class MemoryMonitoringService {
  constructor(store: Store<State>) {
    this.store = store
    this.tick = this.tick.bind(this)
  }

  private store: Store<State>
  private isRunning = false
  private timer: number | null = null
  private sampleIndex = 0
  private lastUsedMemory = 0
  private isInitialReadingSent = false

  public start(): void {
    if (!window.performance.memory || this.isRunning) return
    this.isRunning = true
    this.timer = window.setInterval(this.tick, LOG_INTERVAL_IN_MILLIS)
  }

  public stop(): void {
    if (!this.isRunning) return
    this.isRunning = false
    if (this.timer) window.clearInterval(this.timer)
  }

  private tick(): void {
    if (this.sampleIndex === MAX_ALLOWED_SAMPLES) return
    const memoryAPI = performance.memory as PerformanceMemory
    const usedMemoryAbs = memoryAPI.usedJSHeapSize / 1024 / 1024
    const usedMemoryRel = Math.round(
      (memoryAPI.usedJSHeapSize * 100) / memoryAPI.jsHeapSizeLimit
    )
    // Some browsers don't update the usedMemory value in real time as a privacy
    // measure. Don't log memory usage when the value is static, however send
    // always at least one measurement
    const doesMemoryValueChangeOverTime = usedMemoryAbs !== this.lastUsedMemory
    if (!this.isInitialReadingSent || doesMemoryValueChangeOverTime) {
      this.logUsedMemory(usedMemoryAbs, usedMemoryRel)

      this.lastUsedMemory = usedMemoryAbs
      this.isInitialReadingSent = true
    }
    this.sampleIndex++
  }

  private getStoreAdminId() {
    const admin = this.store.getters[
      `authentication/${GET_CURRENT_STORE_ADMIN_GETTER}`
    ]
    return (admin && admin.id) || ''
  }

  private logUsedMemory(usedMemoryAbs = 0, usedMemoryRel = 0) {
    this.logMParticle(BusinessEventNames.usedMemoryAbsolute, { usedMemoryAbs })
    this.logMParticle(BusinessEventNames.usedMemoryRelative, { usedMemoryRel })
    this.logTelemetry('used_memory_absolute', usedMemoryAbs)
    this.logTelemetry('used_memory_relative', usedMemoryRel)
  }

  private logMParticle(businessEventName: BusinessEventNames, info: object) {
    mParticle.logEvent(businessEventName, mParticle.EventType.UserContent, {
      storeAdminId: this.getStoreAdminId(),
      deviceId: getDeviceId(),
      timeIndex: this.sampleIndex,
      ...info
    })
  }

  private logTelemetry(name: string, value: number) {
    telemetry.Metric.gauge(name, value, {
      tags: { 'time-index': this.sampleIndex }
    })
  }
}
