import { Vue as _Vue } from 'vue/types/vue'
import VueRouter from 'vue-router'
import { GlvModule } from 'glovo'
import { installVueRouter } from './plugins'
import { vueRouterFactory } from './services'

class RouterModule implements GlvModule {
  router!: VueRouter
  install(Vue: typeof _Vue) {
    installVueRouter(Vue)
    this.router = vueRouterFactory()
  }
}
const glvModule = new RouterModule()

export default glvModule
