import OrderProduct from '@/models/OrderProduct'
import { Product } from '@/modules/store'
import { ProductDetails } from '@/modules/store/data/resources/order-details'
import { parseProducts } from './mappers'

export const getInitialProductID = (product: ProductDetails | OrderProduct) => {
  const originalCustomerProductRequestId = product.originalCustomerProductRequest
    ? product.originalCustomerProductRequest.id
    : product.id
  return originalCustomerProductRequestId
}

export function formatServedProductsPayload(
  product: ProductDetails | OrderProduct
) {
  const { quantity, productId, customizations } = product
  return {
    servedProducts: [
      {
        replacedBougthProductId: getInitialProductID(product),
        servedProduct: {
          quantity,
          id: productId,
          customizations
        }
      }
    ]
  }
}

export function parseProductsFromResponse(
  products: Product[]
): ProductDetails[] {
  return products.map(product => parseProducts(product))
}

export { missingTranslationLogger } from './missing-translation-logger'
