import {
  StoreCollection,
  StoreCollectionGroup,
  StoreCollectionSection
} from '@/modules/store/data/resources'

export type CollectorSelectorItem =
  | CollectionGroupMenuItem
  | CollectionMenuItem
  | SectionMenuItem

export type CollectorSelectorParentItem =
  | CollectionGroupMenuItem
  | CollectionMenuItem
export type CollectorSelectorChildrenItem = CollectionMenuItem | SectionMenuItem

export interface CollectionMenuItem extends StoreCollection {
  type: MenuItemType.COLLECTION
  children: CollectorSelectorChildrenItem[]
}

export interface CollectionGroupMenuItem extends StoreCollectionGroup {
  type: MenuItemType.COLLECTION_GROUP
  children: CollectorSelectorChildrenItem[]
}

export interface SectionMenuItem extends StoreCollectionSection {
  type: MenuItemType.SECTION
}

export enum MenuItemType {
  SECTION = 'SECTION',
  COLLECTION = 'COLLECTION',
  COLLECTION_GROUP = 'COLLECTION_GROUP'
}

export type UndefinableMenuItemType = any

export type CollectionList = Array<CollectionGroupMenuItem | CollectionMenuItem>
