import { State } from '@/modules/invoices/store/state'

export const GET_INVOICES_GETTER = 'GET_INVOICES_GETTER'
export const GET_INVOICES_LOADING_GETTER = 'GET_INVOICES_LOADING_GETTER'

export const getters = {
  [GET_INVOICES_GETTER](state: State) {
    return state.dataState.invoices
  },
  [GET_INVOICES_LOADING_GETTER](state: State) {
    return state.uiState.loading
  }
}
