import { State } from '@/modules/benchmarking/store/state'
import {
  SET_IS_IN_BENCHMARKING_MODE,
  SET_NUMBER_OF_ORDERS_REQUESTED,
  SetIsInBenchmarkingModePayload,
  SetNumberOfOrdersRequestedPayload
} from '@/modules/benchmarking/store/action-types'

export const mutations = {
  [SET_IS_IN_BENCHMARKING_MODE](
    state: State,
    payload: SetIsInBenchmarkingModePayload
  ) {
    state.isInBenchmarkingMode = payload
  },
  [SET_NUMBER_OF_ORDERS_REQUESTED](
    state: State,
    payload: SetNumberOfOrdersRequestedPayload
  ) {
    state.numberOfOrdersRequested = payload
  }
}
