import { GlvModule } from 'glovo'
import { Store } from 'vuex'
import { State } from '@/modules/store'
import { AbstractFetchService } from '@/modules/http'
import { storeFactory } from './store'

export class FeatureTogglesModule implements GlvModule {
  readonly name = 'featureToggles'
  constructor(
    private store: Store<State>,
    private fetchService: AbstractFetchService
  ) {
    const featureTogglesModule = storeFactory(this.fetchService)
    this.store.registerModule([this.name], featureTogglesModule)
  }
  install() {}
}
