import { Invoice } from '@/modules/invoices/types/Invoice'
import { UndefinableError } from 'types/any'
import { StoreAddressId } from '@/modules/store/data/resources'

export const GET_INVOICES = 'GET_INVOICES'
export interface GetInvoicesPayload {
  storeAddressIds: StoreAddressId[]
  yearMonth: string
}

export const UPDATE_INVOICES_LOADING = 'UPDATE_INVOICES_LOADING'
export type UpdateInvoicesLoadingPayload = boolean

export const GET_INVOICES_SUCCESS = 'GET_INVOICES_SUCCESS'
export type GetInvoicesSuccessPayload = Invoice[]

export const GET_INVOICES_FAILURE = 'GET_INVOICES_FAILURE'
export type GetInvoicesFailurePayload = UndefinableError

export type Action =
  | {
      type: typeof GET_INVOICES
      payload: GetInvoicesPayload
    }
  | {
      type: typeof GET_INVOICES_SUCCESS
      payload: GetInvoicesSuccessPayload
    }
  | {
      type: typeof GET_INVOICES_FAILURE
      payload: GetInvoicesFailurePayload
    }
  | {
      type: typeof UPDATE_INVOICES_LOADING
      payload: UpdateInvoicesLoadingPayload
    }
