/* eslint-disable @typescript-eslint/no-unused-vars */
import GlvError from './glv-error'
import GlvEvent from './glv-event'
import AbstractErrorTrackerOptions from './abstract-error-tracker-options'

/**
 * This class has the responsability to report anything that may help
 * debugging and improving our webapp statiblity.
 */
export default class AbstractErrorTracker<
  T extends AbstractErrorTrackerOptions = AbstractErrorTrackerOptions
> {
  // tslint:disable-next-line: no-empty
  constructor(options: T) {}
  /**
   * Starts capturing all errors that are not catched explicitly
   */
  captureUncaughtErrors() {
    throw new Error('captureUncaughtErrors must be implemented')
  }

  /**
   * Manually captures an error
   * @param error to be tracked
   * @param context for the error
   */
  captureError(error: GlvError, context: Record<string, any>) {
    throw new Error('captureError must be implemented')
  }

  /**
   * Manually captures an event (not an error) for debugging purposes
   * @param event to be tracked
   */
  captureEvent(event: GlvEvent) {
    // TODO: This should be implemented for debugging purposes
    throw new Error('captureEvent must be implemented')
  }

  /**
   * TODO: user type should be more specific (and customizable is possible)
   * Sets a user in the tracking context. The context is reported as well
   * @param user to be set in the context
   */
  setUser(user: Record<string, any>) {
    throw new Error('setUser must be implemented')
  }
  unsetUser() {
    throw new Error('unsetUser must be implemented')
  }

  /**
   * TODO: locality type should be more specific (and customizable is possible)
   * Serts the locality to the context. The context is reported as well
   * @param locality to be set in the context
   */
  setHyperLocal(locality: Record<string, any>) {
    throw new Error('setHyperLocal must be implemented')
  }
  unsetHyperLocal() {
    throw new Error('unsetHyperLocal must be implemented')
  }

  /**
   * Sets an specific key-value pair to the context
   * @param key to set
   * @param value to set
   */
  setContext(key: string, value: string) {
    throw new Error('setContext must be implemented')
  }
  unsetContext(key: string) {
    throw new Error('unsetContext must be implemented')
  }

  setTag(key: string, value: string) {
    throw new Error('setTag must be implemented')
  }

  /**
   * Checks if an error is whitelisted
   * @param errorStr to check
   */
  isErrorWhitelisted(errorStr: string) {
    throw new Error('isErrorWhiteListed must be implemented')
  }

  _assertGlvError(error: any) {
    if (!(error instanceof GlvError)) {
      const warn =
        'It is recommended to use GlvError. It may provide a better context for debugging:'
      // tslint:disable-next-line: no-console
      console.warn(warn, error.stack)
    }
  }
}
