
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'

import {
  CollectionList,
  CollectorSelectorItem,
  CollectorSelectorParentItem,
  MenuItemType
} from './collection-list-types'

@Component({
  name: 'CollectionSelectorDesktop'
})
export default class CollectionSelectorDesktop extends Vue {
  @Prop({ required: true }) collectionList!: CollectionList
  @Prop({ default: '' }) selectedItemId!: string
  @Prop({ default: null }) selectedItemName!: string | null

  isDropdownOpen = false

  showDropdown() {
    this.isDropdownOpen = true
  }

  closeDropdown() {
    this.isDropdownOpen = false
  }

  toggleDropdown() {
    this.isDropdownOpen ? this.closeDropdown() : this.showDropdown()
  }

  allItemsClicked() {
    this.$emit('clear')
    this.closeDropdown()
  }

  itemClicked(item: CollectorSelectorItem) {
    const title =
      item.type === MenuItemType.COLLECTION_GROUP ? item.title : item.name

    this.$emit('update', item.type, title, item.id.toString())
    this.closeDropdown()
  }

  hasChildrenSelected(item: CollectorSelectorParentItem) {
    return (
      item.children.filter(i => i.id.toString() === this.selectedItemId)
        .length > 0
    )
  }
}
