
import Vue from 'vue'
import Component from 'vue-class-component'
import { Getter } from 'vuex-class'
import {
  GET_STORE_COLLECTION_GROUPS_GETTER,
  GET_STORE_COLLECTION_SECTIONS_GETTER,
  GET_STORE_COLLECTIONS_GETTER
} from '@/modules/store/data/getters'
import {
  StoreAddressId,
  StoreCollection,
  StoreCollectionGroup,
  StoreCollectionGroupId,
  StoreCollectionId,
  StoreCollectionSection,
  StoreCollectionSectionId
} from '@/modules/store/data/resources'
import {
  CollectionMenuItem,
  CollectionGroupMenuItem,
  SectionMenuItem,
  MenuItemType,
  CollectionList
} from './collection-list-types'
import { Prop } from 'vue-property-decorator'
import CollectionDialog from '@/components/common/ui/CollectionDialog.vue'
import CollectionSelectorDesktop from './CollectionSelectorDesktop.vue'

@Component({
  name: 'CollectionSelector',
  components: {
    CollectionSelectorDesktop,
    CollectionDialog
  }
})
export default class CollectionSelector extends Vue {
  @Prop({ required: true }) storeAddressId!: StoreAddressId

  selectedItemName: string | null = null
  selectedItemId = ''
  shouldShowCollectionListPopup = false

  @Getter(GET_STORE_COLLECTION_GROUPS_GETTER) storeCollectionGroups!: Record<
    StoreCollectionGroupId,
    StoreCollectionGroup
  >

  @Getter(GET_STORE_COLLECTIONS_GETTER) storeCollections!: Record<
    StoreCollectionId,
    StoreCollection
  >

  @Getter(GET_STORE_COLLECTION_SECTIONS_GETTER)
  storeCollectionSections!: Record<
    StoreCollectionSectionId,
    StoreCollectionSection
  >

  @Getter('isDesktop') isDesktop!: boolean

  get hasDefaultCollectionGroup() {
    // the default collection group has the id of 0
    return this.storeCollectionGroups[0]
  }

  get hasCollectionGroups() {
    const collectionGroupIdsArray = Object.keys(this.storeCollectionGroups)
    return collectionGroupIdsArray.length === 1
      ? !this.hasDefaultCollectionGroup
      : collectionGroupIdsArray.length > 1
  }

  get collectionList() {
    const collectionsList: CollectionList = []
    if (this.hasCollectionGroups) {
      Object.keys(this.storeCollectionGroups).forEach(groupId => {
        collectionsList.push(this.mapCollectionGroup(+groupId))
      })
    } else {
      Object.keys(this.storeCollections).forEach(collectionId => {
        collectionsList.push(this.mapCollections(+collectionId))
      })
    }
    return collectionsList.filter(
      (collection: CollectionMenuItem | CollectionGroupMenuItem) => {
        return collection.storeAddressId === this.storeAddressId
      }
    )
  }

  mapCollectionGroup(groupId: StoreCollectionGroupId): CollectionGroupMenuItem {
    const group = this.storeCollectionGroups[groupId]
    const children = group.collections.map(this.mapCollections)
    return { ...group, children, type: MenuItemType.COLLECTION_GROUP }
  }

  mapCollections(collectionId: StoreCollectionId): CollectionMenuItem {
    const collection = this.storeCollections[collectionId]
    const children = collection.sections.map(this.mapSections)
    return { ...collection, children, type: MenuItemType.COLLECTION }
  }

  mapSections(sectionId: StoreCollectionSectionId): SectionMenuItem {
    return {
      ...this.storeCollectionSections[sectionId],
      type: MenuItemType.SECTION
    }
  }

  updateSelection(
    menuItemType: MenuItemType,
    selectedItemName: string,
    itemId: string
  ) {
    this.closeCollectionDialog()
    this.selectedItemName = selectedItemName
    this.selectedItemId = itemId
    this.$emit('update-products', menuItemType, parseInt(itemId))
  }

  clearSelection() {
    this.closeCollectionDialog()
    this.selectedItemName = null
    this.selectedItemId = ''
    this.$emit('update-products', null, null)
  }

  showCollectionDialog() {
    this.shouldShowCollectionListPopup = true
  }

  closeCollectionDialog() {
    this.shouldShowCollectionListPopup = false
  }
}
