import {
  GET_INVOICES_SUCCESS,
  GetInvoicesSuccessPayload,
  UPDATE_INVOICES_LOADING,
  UpdateInvoicesLoadingPayload
} from '@/modules/invoices/store/action-types'
import { State } from '@/modules/invoices/store/state'

export const mutations = {
  [GET_INVOICES_SUCCESS](state: State, payload: GetInvoicesSuccessPayload) {
    state.dataState.invoices = payload
  },
  [UPDATE_INVOICES_LOADING](
    state: State,
    payload: UpdateInvoicesLoadingPayload
  ) {
    state.uiState.loading = payload
  }
}
