import Vue from 'vue'
import { ErrorTrackerPlugin } from '../errors'
import AbstractErrorTrackerOptions from '../errors/abstract-error-tracker-options'

export default function install({
  errorWhitelist,
  sentryDSN
}: AbstractErrorTrackerOptions = {}) {
  Vue.use(ErrorTrackerPlugin, {
    sentryDSN,
    errorWhitelist
  })
}
