
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import GlovoBtn from '@/components/common/GlovoBtn.vue'
import { removeElement } from '@/modules/shared'
import { FIFTY_MILLIS, FIVE_SECONDS_IN_MILLIS } from '@/modules/core/helpers'
import { ToastProps } from './types'

@Component({ name: 'GlovoToast', components: { GlovoBtn } })
export default class GlovoToast extends Vue {
  private isVisible = true
  private visibilityTimeout!: NodeJS.Timeout

  @Prop({ required: true }) message!: ToastProps['message']
  @Prop({ default: undefined }) actionLabel!: ToastProps['actionLabel']
  @Prop({ default: undefined }) onActionClick!: ToastProps['onActionClick']

  get transitionDurations() {
    return { enter: 200, leave: 800 }
  }

  get messageLength() {
    return (this.message || '').length
  }

  get toastVisibilityDuration() {
    return Math.max(FIVE_SECONDS_IN_MILLIS, this.messageLength * FIFTY_MILLIS)
  }

  onBeforeEnter() {
    this.visibilityTimeout = setTimeout(
      this.hideToast,
      this.toastVisibilityDuration
    )
  }

  afterLeave(el: HTMLElement) {
    removeElement(el)
  }

  beforeDestroy() {
    clearTimeout(this.visibilityTimeout)
  }

  handleActionClick(e: Event) {
    if (this.onActionClick) {
      this.onActionClick(e)
    }

    this.$emit('action-click', e)
    this.hideToast()
  }

  hideToast() {
    this.isVisible = false
  }
}
