import invoicesApiFactory from '@/api/invoices'
import {
  Action,
  GET_INVOICES,
  GET_INVOICES_FAILURE,
  GET_INVOICES_SUCCESS,
  GetInvoicesFailurePayload,
  GetInvoicesPayload,
  GetInvoicesSuccessPayload,
  UPDATE_INVOICES_LOADING
} from '@/modules/invoices/store/action-types'
import { ActionContext } from 'glovo'

export function actionsFactory({
  fetchInvoices
}: ReturnType<typeof invoicesApiFactory>) {
  return {
    async [GET_INVOICES](
      { commit, dispatch }: ActionContext<Action>,
      payload: GetInvoicesPayload
    ) {
      try {
        commit(UPDATE_INVOICES_LOADING, true)
        const invoices = await fetchInvoices(payload)
        dispatch(GET_INVOICES_SUCCESS, invoices)
      } catch (error) {
        dispatch(GET_INVOICES_FAILURE, error)
      } finally {
        commit(UPDATE_INVOICES_LOADING, false)
      }
    },
    [GET_INVOICES_SUCCESS](
      { commit }: ActionContext<Action>,
      payload: GetInvoicesSuccessPayload
    ) {
      commit(GET_INVOICES_SUCCESS, payload)
    },
    [GET_INVOICES_FAILURE](
      _: ActionContext<Action>,
      error: GetInvoicesFailurePayload
    ) {
      console.error(`getInvoices failure: ${error}`)
    }
  }
}
