import { AbstractFetchService } from '@/modules/http'
import { Module } from 'vuex'
import { State as RootState } from '@/modules/store'
import { initialState, State } from './state'
import {
  getters,
  GET_IS_OPEN_GETTER,
  GET_CLOSING_DAYS_GETTER,
  GET_IS_CHECK_IN_PENDING_GETTER,
  GET_IS_ALLOWED_TO_CHOOSE_CLOSING_TYPE_GETTER
} from './getters'
import { storeManagement } from './namespace'
import { actionsFactory } from './actions'
import userApiFactory from '@/api/user'
import { mutations } from './mutations'

export function storeFactory(
  fetchService: AbstractFetchService
): Module<State, RootState> {
  const userApi = userApiFactory(fetchService)
  return {
    namespaced: true,
    state: initialState,
    actions: actionsFactory(userApi),
    mutations,
    getters
  }
}

export {
  GET_IS_OPEN_GETTER,
  GET_IS_CHECK_IN_PENDING_GETTER,
  GET_CLOSING_DAYS_GETTER,
  GET_IS_ALLOWED_TO_CHOOSE_CLOSING_TYPE_GETTER,
  storeManagement
}
export * from './action-types'
