import I18n from '@/modules/i18n'
import { GlvHttpError, GlvFormattedHttpError, HttpStatusCode } from '../types'

export const formatHttpError = (
  error: GlvHttpError,
  customServerErrorMessage?: string
): GlvFormattedHttpError => {
  const { response, ...rest } = error || {}
  const status = response && response.status
  const message =
    customServerErrorMessage && isServerErrorStatus(status)
      ? customServerErrorMessage
      : response?.data?.error?.message || ''

  return { ...rest, message, status }
}

export const isServerErrorStatus = (status: HttpStatusCode) => {
  const statusNumber = Number(status)
  if (isNaN(statusNumber)) {
    return false
  }

  return statusNumber >= 500 && statusNumber <= 599
}

export const formatHttpErrorWithGenericMessage = (error: GlvHttpError) => {
  const serverErrorMessage = I18n.i18n.t('generic.error.tryagain') as string
  return formatHttpError(error, serverErrorMessage) || {}
}

export const getHttpErrorMessage = (error: GlvHttpError) => {
  const { message } = formatHttpErrorWithGenericMessage(error)
  return message || ''
}
