
import Vue from 'vue'
import Component from 'vue-class-component'
import { Prop } from 'vue-property-decorator'
import { Getter } from 'vuex-class'
import Pair from 'common/Pair.vue'
import Order from 'models/Order'

import type { OrderId } from '@/modules/store'
import type { OrderDetails } from '@/modules/store/data/resources/order-details'

@Component({
  name: 'OrderDetailsCourier',
  components: {
    Pair
  }
})
export default class OrderDetailsCourier extends Vue {
  @Prop() orderDetails!: OrderDetails

  @Getter reportedOrders?: OrderId[]

  get isAndroidInterfaceEnabled() {
    return window.Android
  }

  get orderFinished() {
    return Order.isFinished(this.orderDetails)
  }

  get courierImageUrl() {
    return `https://res.cloudinary.com/glovoapp/image/upload/c_fill,h_160,w_160/${
      this.orderDetails.courierDetails
        ? this.orderDetails.courierDetails.picture
        : ''
    }`
  }

  get shouldShowOrderDetailsCourier() {
    return (
      this.orderDetails.courierDetails ||
      this.orderDetails.statusDetails.isStacked
    )
  }

  get shouldShowOrderStatus() {
    return this.orderFinished && this.isAndroidInterfaceEnabled
  }

  get shouldShowReportCourierButton() {
    if (!this.orderDetails.statusDetails.isPickedUp) {
      return false
    }
    if (!this.reportedOrders) {
      return true
    }
    return !this.reportedOrders.includes(this.orderDetails.id)
  }
}
