import { StoreProductsList } from '../models'
import { GetProductsPayload } from '../api/stores-api'
import { StoreProductsListDTO } from '../api/dtos'
import { UndefinableError } from 'types/any'

export type LoadingPayload = boolean

export const GET_STORE_PRODUCTS = 'GET_STORE_PRODUCTS'

export const SET_STORE_PRODUCTS_LOADING = 'SET_STORE_PRODUCTS_LOADING'

export const GET_STORE_PRODUCTS_SUCCESS = 'GET_STORE_PRODUCTS_SUCCESS'
export type GetStoreProductsSuccessPayload = StoreProductsListDTO | null
export type GetStoreProductsSuccessMutationPayload = StoreProductsList

export const GET_STORE_PRODUCTS_FAILURE = 'GET_STORE_PRODUCTS_FAILURE'
export type GetStoreProductsFailurePayload = UndefinableError

export type Action =
  | {
      type: typeof GET_STORE_PRODUCTS
      payload: GetProductsPayload
    }
  | {
      type: typeof GET_STORE_PRODUCTS_SUCCESS
      payload: GetStoreProductsSuccessPayload
    }
  | {
      type: typeof GET_STORE_PRODUCTS_FAILURE
      payload: GetStoreProductsFailurePayload
    }
  | {
      type: typeof SET_STORE_PRODUCTS_LOADING
      payload: LoadingPayload
    }
