
import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'
import GlovoTableRow from 'common/ui/GlovoTableRow'
import GlovoTableColumn from 'common/ui/GlovoTableColumn'
import GlovoTableRowAnimationConfig from 'common/ui/GlovoTableRowAnimationConfig'

const durationOfTableRowAnimation = 3500

@Component({ name: 'GlovoTable' })
export default class GlovoTable extends Vue {
  @Prop({ default: () => [] })
  rows!: GlovoTableRow[]

  @Prop({ default: null })
  rowToHighlight?: GlovoTableRowAnimationConfig

  @Prop({ default: () => [] })
  columns!: GlovoTableColumn[]

  @Prop({ default: '' })
  noDataTranslationKey!: string

  @Prop({ default: false })
  loading!: boolean

  @Prop({ default: undefined })
  tableRowClassName?: (row: GlovoTableRow, index: number) => string

  mounted() {
    this.handleRowHighlightAnimationCompletion()
  }

  private handleRowHighlightAnimationCompletion() {
    if (this.rowToHighlight) {
      setTimeout(() => {
        this.rowToHighlight?.completion?.call(this)
      }, durationOfTableRowAnimation)
    }
  }

  get noData() {
    return !this.rows || !this.rows.length
  }

  get cssVars() {
    return { '--columns-count': this.columns.length }
  }

  getHeaderClassName(column: GlovoTableColumn) {
    let cssClassNames = `header cell header-${column.name}`
    if (column === this.columns[this.columns.length - 1]) {
      cssClassNames += ' last-header'
    }
    return cssClassNames
  }

  getCssClassName(column: GlovoTableColumn, rowIndex: number, rowId: number) {
    const cssClassNames = [
      `cell row-${rowIndex}  col-${column.name}`,
      {
        highlighted: this.shouldHighlightRow(rowId),
        'first-row': rowIndex === 0,
        'last-row': rowIndex === this.rows.length - 1,
        'last-col': column === this.columns[this.columns.length - 1],
        'first-col': column === this.columns[0]
      }
    ]
    if (this.tableRowClassName) {
      cssClassNames.push(this.tableRowClassName(this.rows[rowIndex], rowIndex))
    }
    return cssClassNames
  }

  private shouldHighlightRow(rowId: number) {
    return this.rowToHighlight?.rowId === rowId
  }

  onCellClick(record: GlovoTableRow, column: string, row: number) {
    this.$emit('cell-click', { record, column, row })
  }
}
