import mParticleSDK from '@mparticle/web-sdk'
import { mParticleKey } from '@/api/localConfig'
import telemetry from '@/modules/telemetry/telemetry'
import Storage from 'models/Storage'
import { getNextSequenceIndex, BusinessEventNames } from 'models/BusinessEvents'
import Store from '@/modules/store'
import { GET_TOGGLES_GETTER } from '@/modules/feature-toggles'
import { ONE_MINUTE_IN_MILLIS, TWO_SECONDS_IN_MILLIS } from '../core/helpers'

const BATCH_INTERVAL_MILLIS = ONE_MINUTE_IN_MILLIS
const TELEMETRY_EVENT_NAME = 'MPARTICLE_SDK'
const isDevelopmentMode = process.env.VUE_APP_MODE === 'development'

function isMParticleFeatureToggleEnabled() {
  return Store?.store?.getters[GET_TOGGLES_GETTER]
    ?.FE_PARTNERS_MPARTICLE_LOGGING_ENABLED
}

type EventType = number

export interface EventTracker {
  logEvent(
    eventName: BusinessEventNames,
    eventType: EventType,
    eventInfo: Record<any, any>
  ): void

  EventType: Record<string, EventType>
}

class MParticleEventTracker implements EventTracker {
  // add logLevel: 'verbose', for debugging purposes
  constructor() {
    mParticleSDK.init(mParticleKey, {
      isDevelopmentMode,
      timeout: TWO_SECONDS_IN_MILLIS,
      flags: {
        eventBatchingIntervalMillis: BATCH_INTERVAL_MILLIS,
        eventsV3: 100
      },
      logger: {
        error: (message: string) => {
          const configs = MParticleEventTracker.buildLogConfigs()
          telemetry.Log.error(TELEMETRY_EVENT_NAME, message, configs)
          console.error(message)
        },
        warn: (message: string) => {
          const configs = MParticleEventTracker.buildLogConfigs()
          telemetry.Log.warn(TELEMETRY_EVENT_NAME, message, configs)
          console.warn(message)
        },
        verbose: (message: string) => {
          console.log(message)
        }
      }
    })
  }

  private static buildLogConfigs() {
    const device = Storage.get(Storage.items.DEVICE)
    return {
      tags: {
        deviceId: (device || {}).id || ''
      }
    }
  }

  get EventType() {
    return mParticleSDK.EventType
  }

  logEvent(
    eventName: BusinessEventNames,
    eventType: EventType,
    eventInfo: Record<any, any>
  ): void {
    if (isMParticleFeatureToggleEnabled()) {
      eventInfo.sequenceNum = getNextSequenceIndex(eventName)
      mParticleSDK.logEvent(eventName, eventType, eventInfo)
    }
  }
}

class NullEventTracker implements EventTracker {
  get EventType() {
    return mParticleSDK.EventType
  }

  logEvent(): void {}
}

export default isDevelopmentMode
  ? new NullEventTracker()
  : new MParticleEventTracker()
