import { Route, RawLocation, NavigationGuard } from 'vue-router'
import Vue from 'vue'
import { Store } from 'vuex'
import { State } from '@/modules/store'
import {
  IS_ADMIN_MODE_GETTER,
  IS_AUTHENTICATED_GETTER,
  IS_STORE_ADMIN_LOADED_GETTER,
  GET_ADMIN_ROUTES_GETTER
} from '../store/getters'

type NextFunction<V = Vue> = (
  to?: RawLocation | false | ((vm: V) => any) | void
) => void

export const permissionsName = 'permissions'

const authorization: (store: Store<State>) => NavigationGuard = (
  store: Store<State>
) => {
  return (to: Route, from: Route, next: NextFunction) => {
    const isAuthenticated = () =>
      store.getters[`authentication/${IS_AUTHENTICATED_GETTER}`]
    const isAdmin = () =>
      store.getters[`authentication/${IS_ADMIN_MODE_GETTER}`]
    const isAdminRoute = () => {
      return store.getters[
        `authentication/${GET_ADMIN_ROUTES_GETTER}`
      ].includes(to.name!)
    }
    const isStoreAdminLoaded = () =>
      store.getters[`authentication/${IS_STORE_ADMIN_LOADED_GETTER}`]
    const isFirstPageLoad = () => from.name === null

    if (isFirstPageLoad()) {
      if (!isAuthenticated()) {
        handlePermissionsRoute()
      } else {
        handleAuthenticatedRoutes()
      }
    } else {
      handleAuthenticatedRoutes()
    }

    function handleAuthenticatedRoutes() {
      if (isStoreAdminLoaded()) {
        handlePermissionsRoute()
      } else {
        store.watch(
          (_state, getters) =>
            getters[`authentication/${IS_STORE_ADMIN_LOADED_GETTER}`],
          isStoreAdminLoadedValue => {
            if (isStoreAdminLoadedValue) {
              handlePermissionsRoute()
            }
          }
        )
      }
    }

    function handlePermissionsRoute() {
      if (!isAdminRoute() || isAdmin()) {
        next()
      } else {
        const permissionsRoute = {
          name: permissionsName,
          query: { redirect: to.path }
        }
        next(permissionsRoute)
      }
    }
  }
}

export default authorization
