
import Vue from 'vue'
import Component from 'vue-class-component'
import { DetailsList } from 'common/ui/details-list'
import { Prop } from 'vue-property-decorator'
import { ProductId } from '@/modules/store/data/resources'
import { StoreProduct } from '../product'
import { storeCatalogs } from '../../store/namespace'
import { GET_STORE_PRODUCTS_BY_ID } from '../../store/getters'
import { ProductsMapById, StoreProductVM } from '../../models'

@Component({
  name: 'ProductsSummary',
  components: { DetailsList, StoreProduct }
})
export default class ProductsSummary extends Vue {
  @Prop({ default: false }) showAllProducts!: boolean
  @Prop({ required: true }) productIds!: ProductId[]

  @storeCatalogs.Getter(GET_STORE_PRODUCTS_BY_ID)
  productsById!: ProductsMapById

  get computedProductIds() {
    return this.productIds ?? []
  }

  get products(): StoreProductVM[] {
    return this.computedProductIds.map(this.mapProduct)
  }

  get allProductsIndication() {
    return [
      {
        title: this.$t('promotions.products.all-products.text') as string,
        message: this.$t(
          'promotions.products.new-products-participate.text'
        ) as string
      }
    ]
  }

  get items() {
    return this.showAllProducts ? this.allProductsIndication : this.products
  }

  private mapProduct(id: ProductId): StoreProductVM {
    return this.productsById[id] ?? {}
  }
}
