/**
 * Provides a way to safely call a method that can potentially throw a type error
 * To be used to safely access nested properties until optional chaining is introduced in the project
 * https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Optional_chaining
 *
 * @example
 * obj?.prop?.nestedProp becomes safeCall(() => obj.prop.nestedProp)
 *
 * @param callable a function that can potentially throw a type error when called
 */
export const safeCall = <T>(callable: () => T): T | null => {
  try {
    return callable()
  } catch (e) {
    // we don't log errors as they are expected in this context
    return null
  }
}
