import TelemetryClient from '@glovo/telemetry-client-web'
import { glovoEndpoints } from '@glovo/glovo-uri-catalog'
import { root } from '@/api/localConfig'
import { version } from '@/../version'
import {
  getNetworkType,
  getConnectionType,
  isSunmiDevice,
  getDeviceVendor,
  getDeviceId
} from '@/modules/shared/helpers'

export default function initTelemetryClient(): TelemetryClient {
  return new TelemetryClient({
    scope: 'glovoapp',
    service: 'partners_web',
    api: {
      log: `${root}/frontend_metrics/logs`,
      metric: `${root}/frontend_metrics/metrics`
    },
    env: process.env.VUE_APP_MODE === 'production' ? 'production' : 'stage',
    queueLimit: 200,
    interval: 30,
    retryDelay: 60,
    debug: false,
    tags: {
      'app-version': version,
      'app-name': 'partners_web',
      'app-env':
        process.env.VUE_APP_MODE === 'production' ? 'production' : 'stage',
      'app-context': window.Android ? 'webview' : 'browser',
      'device-memory': navigator.deviceMemory || 'unknown',
      'device-vendor': getDeviceVendor(),
      'device-connection-speed': getNetworkType(),
      'device-connection-type': getConnectionType()
      /* 
        Other default tags:
        'route-name'    ( Set and kept in sync in the Vue Router module )
        'user-country'  ( Set and kept in sync by the Vuex login & logout actions )
      */
    },
    attr: {
      'user-agent': navigator.userAgent,
      'is-sunmi-device': isSunmiDevice(),
      'device-id': getDeviceId()
      /*
        Other default attributes:
        'user-id'  ( Set and kept in sync by the Vuex login & logout actions )
      */
    },
    networkResources: glovoEndpoints,
    downsample: {
      polling_orders: 0.99,
      network_request_latency: 0.99,
      network_request_event: 0.99,
      network_request_unknown_resource: 0.99,
      orders_polling_response_content_length: 0.99,
      orders_response: 0.99
    }
  })
}
