import {
  LoadingPayload,
  SET_STORE_PRODUCTS_LOADING,
  GET_STORE_PRODUCTS_SUCCESS,
  GET_STORE_PRODUCTS_FAILURE,
  GetStoreProductsFailurePayload,
  GetStoreProductsSuccessPayload
} from './action-types'
import { State } from './state'
import { mapStoreProductsList, mapStoreProducts } from '../mappers'

export const mutations = {
  [GET_STORE_PRODUCTS_SUCCESS](
    state: State,
    payload: GetStoreProductsSuccessPayload
  ) {
    const { storeId, products = [] } = payload || {}
    if (!storeId) {
      return
    }

    state.dataState.products.byStoreId = {
      ...state.dataState.products.byStoreId,
      [storeId]: mapStoreProductsList(payload)
    }

    state.dataState.products.byId = {
      ...state.dataState.products.byId,
      ...mapStoreProducts(products)
    }

    state.uiState = { ...state.uiState, getStoreProductsApiError: null }
  },
  [GET_STORE_PRODUCTS_FAILURE](
    state: State,
    error: GetStoreProductsFailurePayload
  ) {
    state.uiState = { ...state.uiState, getStoreProductsApiError: error }
  },
  [SET_STORE_PRODUCTS_LOADING](state: State, payload: LoadingPayload) {
    state.uiState = { ...state.uiState, storeProductsLoading: payload }
  }
}
