import { analyticsEvents } from '../analytics-library'
import { StoreAddressId, StoreAdminId } from '@/modules/store/data/resources'
import { OrderId } from '@/modules/store'
import { ScaocOptions } from '../types/ScaocOptions'

// TD Task added to fix ts-ignore - https://glovoapp.atlassian.net/browse/PAR-2254

export const storeClosureEvents = {
  onStoreClosureWarningPressed(
    pendingOrderIds: OrderId[],
    storeAddressIds: StoreAddressId[],
    storeAdminId: StoreAdminId,
    scaocOption: ScaocOptions
  ): void {
    const { ScaocWarningClicked } = analyticsEvents
    // @ts-ignore
    this.trackMParticleEvent(
      new ScaocWarningClicked(
        pendingOrderIds,
        storeAddressIds,
        storeAdminId,
        scaocOption
      )
    )
  }
}
