import telemetry from '@/modules/telemetry/telemetry'
import Router, { RouteConfig, Route, RawLocation } from 'vue-router'

import permissions from '@/components/permissions/permissions.routes'
import main from '@/components/main/main.routes'

export default function factory(): Router {
  const fallbackRoute: RouteConfig = {
    path: '*',
    redirect: '/'
  }
  const router = new Router({
    mode: 'history',
    routes: [main, permissions, fallbackRoute],
    scrollBehavior(to, from, savedPosition) {
      if (savedPosition) {
        return savedPosition
      } else {
        return { x: 0, y: 0 }
      }
    }
  })
  const originalPush = Router.prototype.push
  Router.prototype.push = function push(location: RawLocation): Promise<Route> {
    const originalPushCall: Promise<Route> = (originalPush.call(
      router,
      location
    ) as unknown) as Promise<Route>
    return originalPushCall.catch(err => err)
  }
  router.beforeEach((to, from, next) => {
    const targetRouteName =
      typeof to?.name === 'string' && to.name.length ? to.name : '_unknown_'
    telemetry.setDefaultTag('route-name', to.name || 'unnamed')
    telemetry.setDefaultTag('app-route', targetRouteName)
    telemetry.setDefaultAttribute('app-route', targetRouteName)
    next()
  })
  return router
}
