import * as types from '@/modules/store/services/mutation-types'
import moment from 'moment-timezone'
import Vue from 'vue'
import flatMap from 'lodash/flatMap'
import stockApiFactory from '@/api/stock'

const state = {
  content: {},
  attributes: [],
  replacements: {},
  currentGroup: {},
  productRequestPriceUpdates: {},
  attributeRequestPriceUpdates: {}
}

const getters = {
  content: state => state.content,
  attributes: state => state.attributes,
  replacements: state => state.replacements,
  currentGroup: state => state.currentGroup,
  storeAddressCollections: state => {
    if (!state.content.collections) return {}
    const allCollections = flatMap(
      state.content.collections.map(group => group.collections)
    )
    return allCollections.reduce((accum, currentValue) => {
      accum[currentValue.id] = currentValue
      return accum
    }, {})
  },
  isSingleCollection: state => {
    if (!state.content || !state.content.collections) {
      return false
    }
    return (
      state.content.collections.length === 1 &&
      state.content.collections[0].collections.length === 1
    )
  },
  allProducts: state => {
    if (!state.currentGroup.collections) return []
    return flatMap(
      flatMap(
        state.currentGroup.collections,
        collection => collection.sections
      ),
      section => section.products
    )
  },
  productRequestPriceUpdates: state => state.productRequestPriceUpdates,
  attributeRequestPriceUpdates: state => state.attributeRequestPriceUpdates
}

export function actionsFactory({
  content,
  fetchAttributes,
  addProductStockout,
  removeProductStockout,
  addAttributeStockout,
  removeAttributeStockout,
  getRequestedPriceUpdates,
  requestAttributePriceUpdate,
  requestProductPriceUpdate,
  removeRequestPriceUpdate
}) {
  return {
    getContent({ commit }, storeAddressId) {
      return content(storeAddressId).then(response => {
        commit('GET_CONTENT', {
          content: response,
          storeAddressId: storeAddressId
        })
      })
    },
    async addProductStockout(
      { commit, getters },
      { productId, stockout, storeAddressId }
    ) {
      const stockoutObject = { expirationTime: stockout }
      await addProductStockout(storeAddressId, productId, stockoutObject)
      const product = getters.allProducts.find(
        product => product.id === productId
      )
      commit('ADD_PRODUCT_STOCKOUT', { product, stockoutObject })
    },
    async removeProductStockout(
      { commit, getters },
      { productId, storeAddressId }
    ) {
      await removeProductStockout(storeAddressId, productId)
      const product = getters.allProducts.find(
        product => product.id === productId
      )
      commit('REMOVE_PRODUCT_STOCKOUT', product)
    },
    async addAttributeStockout(
      { commit },
      { storeAddressId, attributeId, expirationTime }
    ) {
      await addAttributeStockout(storeAddressId, attributeId, {
        expirationTime
      })
      const attributes = await fetchAttributes(storeAddressId)
      commit('ADD_ATTRIBUTE_STOCKOUT', attributes)
    },
    async removeAttributeStockout({ commit }, { attributeId, storeAddressId }) {
      await removeAttributeStockout(storeAddressId, attributeId)
      const attributes = await fetchAttributes(storeAddressId)
      commit('REMOVE_ATTRIBUTE_STOCKOUT', attributes)
    },
    async getRequestedPriceUpdates({ commit }, storeAddressId) {
      const updates = await getRequestedPriceUpdates(storeAddressId)
      commit(types.GET_REQUESTED_PRICE_UPDATES, updates)
    },
    async requestAttributePriceUpdate(
      { dispatch },
      { storeAddressId, attributeId, price }
    ) {
      const savedRequest = await requestAttributePriceUpdate(
        storeAddressId,
        attributeId,
        price
      )
      dispatch('getRequestedPriceUpdates', storeAddressId)
      return savedRequest
    },
    async requestProductPriceUpdate(
      { dispatch },
      { storeAddressId, productId, price }
    ) {
      const savedRequest = await requestProductPriceUpdate(
        storeAddressId,
        productId,
        price
      )
      dispatch('getRequestedPriceUpdates', storeAddressId)
      return savedRequest
    },
    async removeRequestPriceUpdate({ commit }, id) {
      await removeRequestPriceUpdate(id)
      commit(types.DELETE_REQUEST_PRICE_UPDATES, id)
    }
  }
}

const mutations = {
  [types.GET_CONTENT](state, { content }) {
    state.content = content

    const allCollections = flatMap(
      state.content.collections.map(group => group.collections)
    )
    state.normalizedCollections = allCollections.reduce(
      (accum, currentValue) => {
        accum[currentValue.id] = currentValue
        return accum
      },
      {}
    )
    if (content.collections[0]) {
      state.currentGroup = content.collections[0]
    } else {
      state.currentGroup = []
    }
  },
  [types.SET_COLLECTION_GROUP](state, group) {
    state.currentGroup = group
  },
  [types.ADD_PRODUCT_STOCKOUT](state, { product, stockoutObject }) {
    product.enabled = false
    product.stockout = stockoutObject
  },
  [types.REMOVE_PRODUCT_STOCKOUT](state, product) {
    product.enabled = true
    product.stockout = null
  },
  [types.ADD_ATTRIBUTE_STOCKOUT](state, attributes) {
    state.attributes = attributes
  },
  [types.REMOVE_ATTRIBUTE_STOCKOUT](state, attributes) {
    state.attributes = attributes
  },
  [types.REPLACEMENTS_RETRIEVED](state, { items, collectionId }) {
    Vue.set(state.replacements, collectionId, {
      items,
      timestamp: moment()
    })
  },
  [types.GET_REQUESTED_PRICE_UPDATES](state, requests) {
    state.attributeRequestPriceUpdates = {}
    state.productRequestPriceUpdates = {}
    requests
      .filter(r => r.type === 'PRODUCT')
      .forEach(r => Vue.set(state.productRequestPriceUpdates, r.itemId, r))
    requests
      .filter(r => r.type === 'ATTRIBUTE')
      .forEach(r => Vue.set(state.attributeRequestPriceUpdates, r.itemId, r))
  },
  [types.DELETE_REQUEST_PRICE_UPDATES](state, id) {
    for (const i in state.productRequestPriceUpdates) {
      if (state.productRequestPriceUpdates[i].id === id) {
        Vue.delete(state.productRequestPriceUpdates, i)
        break
      }
    }
    for (const i in state.attributeRequestPriceUpdates) {
      if (state.attributeRequestPriceUpdates[i].id === id) {
        Vue.delete(state.attributeRequestPriceUpdates, i)
        break
      }
    }
  }
}

export default function (fetchService) {
  const stockApi = stockApiFactory(fetchService)
  return {
    state,
    getters,
    actions: actionsFactory(stockApi),
    mutations
  }
}
