import { host } from './localConfig'
import { AbstractFetchService } from '@/modules/http'

export default function (fetch: AbstractFetchService) {
  return {
    sales: (storeAddressId: number, params: Record<string, any>) =>
      fetch.get(
        `${host}/store_addresses/${storeAddressId}/orders/statistics/sales`,
        params
      ),
    getStoreAddressesSales: (params: Record<string, any>) =>
      fetch.get(`${host}/store_addresses/orders/statistics/sales`, params),
    history: (storeAddressId: number, params: Record<string, any>) =>
      fetch.get(
        `${host}/store_addresses/${storeAddressId}/orders/completed/filtered`,
        params
      ),
    getStoreAddressesHistory: (params: Record<string, any>) =>
      fetch.get(`${host}/store_addresses/orders/completed/filtered`, params),
    getStoreAddressesExcel: (params: Record<string, any>) =>
      fetch.download(
        `${host}/store_addresses/orders/completed/filtered/as-xls`,
        params,
        60000,
        {}
      ),
    getStoreAddressesSearchByCode: (storeAddressIds: number[], code: string) =>
      fetch.get(`${host}/store_addresses/orders/search/${code}/code`, {
        storeAddressIds
      }),
    getStoreAddressesStatisticsByWeeks: (storeAddressIds: number[]) =>
      fetch.get(`${host}/store_addresses/orders/statistics`, {
        storeAddressIds
      }),
    getStoreAddressessBestSellers: (storeAddressIds: number[]) =>
      fetch.get(`${host}/store_addresses/orders/statistics/top_products`, {
        storeAddressIds
      }),
    lookerUrl: (storeAddressId: number) =>
      fetch.get(`${host}/store_addresses/${storeAddressId}/reports/looker`),
    sendSALogs: (storeAddressId: number, data: Record<string, any>) =>
      fetch.post(
        `${host}/store_addresses/${storeAddressId}/webapp/tracking`,
        null,
        data
      )
  }
}
