
import Component from 'vue-class-component'
import { Mixins, Prop, Watch } from 'vue-property-decorator'
import { WatchedModel } from '@/components/mixins'
import { GlovoCheckboxGroup } from 'common/ui/checkbox'
import GlovoSearchField from 'common/ui/GlovoSearchField.vue'
import Placeholder from '@/components/common/Placeholder.vue'
import GlovoErrorPage from 'common/ui/GlovoErrorPage.vue'
import { ProductId, StoreId } from '@/modules/store/data/resources'
import { StoreAction } from '@/modules/store/types/store'
import { StoreProduct } from '../product'
import { storeCatalogs } from '../../store/namespace'
import {
  GET_STORE_PRODUCTS_BY_STORE_ID,
  GET_STORE_PRODUCTS_ERROR_GETTER,
  GET_STORE_PRODUCTS_LOADING_GETTER
} from '../../store/getters'
import { ProductsMapByStoreId } from '../../models'
import {
  GET_STORE_PRODUCTS,
  GetStoreProductsFailurePayload
} from '../../store/action-types'
import { GetProductsPayload } from '../../api/stores-api'
import EmptyCollection from 'common/ui/empty-collection/EmptyCollection.vue'
import { filterProductsByQuery } from './StoreProducts.helpers'

@Component({
  name: 'StoreProducts',
  components: {
    GlovoSearchField,
    GlovoCheckboxGroup,
    Placeholder,
    GlovoErrorPage,
    EmptyCollection,
    StoreProduct
  }
})
export default class StoreProducts extends Mixins(WatchedModel) {
  private searchQueryInternal = ''
  @Prop({ required: true }) storeId!: StoreId
  @Prop() searchEnabled!: boolean

  @storeCatalogs.Action(GET_STORE_PRODUCTS)
  getStoreProducts!: StoreAction<GetProductsPayload>

  @storeCatalogs.Getter(GET_STORE_PRODUCTS_BY_STORE_ID)
  productsByStoreId!: ProductsMapByStoreId

  @storeCatalogs.Getter(GET_STORE_PRODUCTS_LOADING_GETTER)
  productsLoading!: boolean

  @storeCatalogs.Getter(GET_STORE_PRODUCTS_ERROR_GETTER)
  getProductsApiError!: GetStoreProductsFailurePayload

  async created() {
    this.loadProducts()
  }

  get searchQuery(): string {
    return this.searchQueryInternal
  }

  set searchQuery(value) {
    this.searchQueryInternal = typeof value === 'string' ? value.trim() : ''
  }

  get selectedProductIds(): ProductId[] {
    return this.internalValue
  }

  set selectedProductIds(ids: ProductId[]) {
    this.internalValue = ids
  }

  get allProductsSelected() {
    return (
      this.storeHasProducts &&
      this.allProductsCount === this.selectedProductIds?.length
    )
  }

  get allProducts() {
    return this.productsByStoreId[this.storeId]
  }

  get allProductsCount() {
    return (this.allProducts ?? []).length
  }

  get products() {
    if (!this.searchEnabled) {
      return this.allProducts
    }

    return filterProductsByQuery(this.allProducts, this.searchQuery)
  }

  get productsCount() {
    return (this.products ?? []).length
  }

  get storeHasProducts() {
    return this.allProductsCount > 0
  }

  get productListHasResults() {
    return this.productsCount > 0
  }

  get productsListVisible() {
    return (
      !this.productsLoading &&
      !this.getProductsApiError &&
      this.productListHasResults
    )
  }

  get isSearchQueryEmpty() {
    return this.searchQuery.length === 0
  }

  loadProducts() {
    if (!this.storeHasProducts && this.storeId) {
      this.getStoreProducts({ storeId: this.storeId })
    }
  }

  @Watch('storeId')
  handleStoreIdChange() {
    this.loadProducts()
  }

  @Watch('allProductsSelected')
  handleAllProductsSelectedChange() {
    this.$emit('update:allProductsSelected', this.allProductsSelected)
  }
}
