import axios, {
  AxiosInstance,
  AxiosError,
  AxiosResponse,
  CancelTokenSource,
  CancelToken
} from 'axios'
import { apiVersion } from '@/api/localConfig'
import { version } from '@/../version'
import { HTTP_METHOD, CONTENT_TYPE, ERROR_CODE, HTTP_CODES } from './constants'
import { RequestHeader } from './request-header'
import {
  AbstractFetchService,
  RequestInterceptor
} from '../abstract-fetch-service/abstract-fetch-service'
import { beforeEnterServiceFactory } from '@/modules/authentication/services/before-enter/before-enter-service-factory'
import telemetry from '@/modules/telemetry/telemetry'
import Store from '@/modules/store'
import Timeout = NodeJS.Timeout
import { LOG_REQUEST, LoggedRequest } from '@/modules/core/store/action-types'
import { TWENTY_SECONDS_IN_MILLIS } from '@/modules/core/helpers'
import { exponentialDelay } from 'axios-retry'
import { HttpStatusCode, RequestConfiguration } from '@/modules/http'
import { ORDERS_POLLING_URL } from '@/api/orders'

export const DEFAULT_RETRIES = 0
export const DEFAULT_TIMEOUT = TWENTY_SECONDS_IN_MILLIS

export class NetworkFetchService implements AbstractFetchService {
  client: AxiosInstance
  requestInterceptors: RequestInterceptor[] = []

  get headers() {
    const headers: Partial<RequestHeader> = {
      'Content-Type': CONTENT_TYPE.JSON,
      'Glovo-API-Version': apiVersion,
      'Glovo-App-Platform': 'web',
      'Glovo-App-Version': version || '',
      'Glovo-App-Type': 'partners-webapp'
    }
    const beforeEnterService = beforeEnterServiceFactory()
    const locale = beforeEnterService.getLocale()
    if (locale) {
      headers['Glovo-Language-Code'] = locale
    }
    const device = beforeEnterService.getDevice()
    if (device && device.id) {
      headers['Glovo-Partner-Device-Id'] = device.id
    }
    return headers
  }

  constructor(client: AxiosInstance) {
    this.client = client
  }

  get = <T = any>(...args: any[]): Promise<T> =>
    this.request<T>(HTTP_METHOD.GET, ...args)
  post = <T = any>(...args: any[]): Promise<T> =>
    this.request<T>(HTTP_METHOD.POST, ...args)
  put = <T = any>(...args: any[]): Promise<T> =>
    this.request<T>(HTTP_METHOD.PUT, ...args)
  remove = <T = any>(...args: any[]): Promise<T> =>
    this.request<T>(HTTP_METHOD.DELETE, ...args)
  download = (
    url: string,
    params: Record<string, any>,
    timeout = DEFAULT_TIMEOUT,
    data: Record<string, any>
  ): Promise<Blob> =>
    this.request(
      HTTP_METHOD.GET,
      url,
      params,
      data,
      timeout,
      DEFAULT_RETRIES,
      { 'Content-Type': CONTENT_TYPE.FILE },
      { responseType: 'blob' }
    ).then(response => {
      this.downloadAsFile(response)
      return response
    })

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  request<T = any>(method: HTTP_METHOD, ...args: any[]): Promise<any>
  async request<T = any>(
    method: HTTP_METHOD,
    url: string,
    params: Record<string, any>,
    data: Record<string, any>,
    timeout: number = DEFAULT_TIMEOUT,
    retries = DEFAULT_RETRIES,
    extraHeaders = {},
    extraConfig = {},
    retryConditionFn: (error: AxiosError) => boolean = () => false,
    isExponentialBackoffEnabled = false,
    shouldOmitCancelToken = false
  ): Promise<T> {
    const headers = { ...this.headers, ...extraHeaders }
    let requestConfig: RequestConfiguration = {
      url,
      headers,
      params,
      data
    }
    if (!shouldOmitCancelToken) {
      requestConfig = {
        ...requestConfig,
        cancelToken: axios.CancelToken.source()
      }
    }
    try {
      requestConfig = await this.requestInterceptors.reduce(
        async (accumulator, interceptor) => {
          const oldConfig: RequestConfiguration = await accumulator
          return interceptor(oldConfig)
        },
        Promise.resolve(requestConfig)
      )
    } catch (err) {
      return Promise.reject(err)
    }

    const config = {
      ...requestConfig,
      method,
      timeout, // this key has to be above the `data` key to avoid it being ignored. Yes, really - https://github.com/axios/axios/issues/2073
      data: requestConfig.data ? JSON.stringify(requestConfig.data) : {},
      ...extraConfig,
      'axios-retry': {
        retries,
        shouldResetTimeout: true,
        retryCondition: (error: AxiosError) =>
          this.isTimeoutError(error) || retryConditionFn(error), // if true, request will be retried
        retryDelay: isExponentialBackoffEnabled ? exponentialDelay : () => 0 // () => 0 is the default value for retry delay: https://github.com/softonic/axios-retry#options
      }
    }

    return await this.performRequest<T>(config)
  }

  private async performRequest<T = any>(config: RequestConfig): Promise<T> {
    const { timeout, cancelToken } = config
    let setTimeoutId: Timeout | undefined = undefined
    let token: CancelToken | undefined = undefined
    if (cancelToken) {
      setTimeoutId = this.configureCancelTokenTimeout(timeout, cancelToken)
        .setTimeoutId
      token = cancelToken.token
    }
    const configWithToken = {
      ...config,
      cancelToken: token
    }
    let error: AxiosError | undefined
    let response: AxiosResponse | undefined
    let statusCode: number
    const requestTime = new Date()

    try {
      response = await this.client.request<T>(configWithToken)
      statusCode = response.status
    } catch (e) {
      error = e
      if (typeof e.response === 'object' && e.response.status) {
        statusCode = e.response.status
      } else if (this.isTimeoutError(e)) {
        statusCode = HTTP_CODES.REQUEST_TIMEOUT_CODE
      } else {
        statusCode = HTTP_CODES.CONNECTION_CLOSED_CODE
      }
    }

    this.logRequestInformation(response, config, statusCode, requestTime)
    this.logRequestToStore(!error)

    if (setTimeoutId) clearTimeout(setTimeoutId)
    if (error) throw error
    return response ? response.data : {}
  }

  private isTimeoutError(e: AxiosError) {
    if (e && e.code && e.message) {
      return Boolean(
        e.code === ERROR_CODE.CONNECTION_ABORTED &&
          e.message.includes('timeout')
      )
    }
    return false
  }

  beforeEach(fn: RequestInterceptor): void {
    this.requestInterceptors.push(fn)
  }

  private configureCancelTokenTimeout(
    timeout: number,
    cancelToken: CancelTokenSource
  ): {
    setTimeoutId?: Timeout
  } {
    // Use cancel tokens instead of timeout field for more reliable results
    // https://github.com/axios/axios/issues/647#issuecomment-459517694
    const setTimeoutId = setTimeout(
      () => cancelToken.cancel(`CancelToken timeout at ${timeout}`),
      timeout
    )
    return {
      setTimeoutId
    }
  }

  private downloadAsFile(data: BlobPart) {
    const blob = new Blob([data], {
      type: CONTENT_TYPE.FILE
    })
    const link = document.createElement('a')
    link.href = window.URL.createObjectURL(blob)
    link.download = 'orders-summary.xlsx'
    link.click()
  }

  private logRequestToStore(success: boolean) {
    const logRequest: LoggedRequest = {
      timestamp: Date.now(),
      success: success
    }
    Store.store.dispatch(`core/${LOG_REQUEST}`, logRequest)
  }

  private logRequestInformation(
    response: AxiosResponse | undefined,
    config: RequestConfig,
    statusCode: HttpStatusCode,
    requestTime: Date
  ) {
    telemetry.Event.networkRequest({
      time: requestTime,
      method: config.method,
      status: statusCode,
      address: config.url
    })
    const isOrdersPollingEndpoint = config.url.includes(ORDERS_POLLING_URL)
    const responseSize = response?.headers?.['content-length']
    if (isOrdersPollingEndpoint && responseSize) {
      telemetry.Metric.gauge(
        'orders_polling_response_content_length',
        responseSize
      ) // downsampled in src/modules/telemetry/helpers/init-telemetry-client.ts
    }
  }
}

interface RequestConfig {
  method: HTTP_METHOD
  url: string
  data?: any
  timeout: number
  headers?: Partial<RequestHeader>
  params?: Record<string, any>
  cancelToken?: CancelTokenSource
  'axios-retry'?: {
    retries: number
    shouldResetTimeout: boolean
    retryCondition: (error: AxiosError) => boolean
  }
}
