import { State as RootState } from '@/modules/store'
import { GlvModule } from 'glovo'
import { Module, Store } from 'vuex'
import {
  liveOrdersServiceFactory,
  liveOrdersServiceHandler
} from './services/live-orders'
import { INIT_STORE, State, storeFactory } from './store'
import { AbstractFetchService } from '@/modules/http'

export class OrdersModule implements GlvModule {
  readonly name = 'orderManagement'
  constructor(
    private store: Store<RootState>,
    private fetchService: AbstractFetchService
  ) {}

  install() {
    const liveOrdersService = liveOrdersServiceFactory(this.store)
    const ordersModule = storeFactory(liveOrdersService, this.fetchService)
    this.initStore(this.store, ordersModule)
  }

  initStore(store: Store<RootState>, storeModule: Module<State, RootState>) {
    store.registerModule([this.name], storeModule)
    store.dispatch(`${this.name}/${INIT_STORE}`)
    store.subscribeAction(liveOrdersServiceHandler(this.name, store))
  }
}
