import { RouteConfig } from 'vue-router'
import { PromotionType } from './api/dtos'

const PromotionsView = () =>
  import(
    /* webpackChunkName: "promotions"*/ './components/promotions-view/PromotionsView.vue'
  )

const CreatePromotionView = () =>
  import(
    /* webpackChunkName: "promotions-create"*/ './components/create-promotion-view/CreatePromotionView.vue'
  )

export const PROMOTION_ROUTES = {
  getPromos: 'promotions'
}

export const CREATE_PROMO_ROUTES: Partial<Record<PromotionType, string>> = {
  [PromotionType.FREE_DELIVERY]: 'createFreeDeliveryPromo',
  [PromotionType.PERCENTAGE_DISCOUNT]: 'createPercentageDiscountPromo'
}

const routes: RouteConfig[] = [
  {
    path: 'promotions',
    name: PROMOTION_ROUTES.getPromos,
    component: PromotionsView
  },
  {
    path: 'promotions/create-free',
    name: CREATE_PROMO_ROUTES.FREE_DELIVERY,
    component: CreatePromotionView,
    props: { promotionType: PromotionType.FREE_DELIVERY }
  },
  {
    path: 'promotions/create-discount',
    name: CREATE_PROMO_ROUTES.PERCENTAGE_DISCOUNT,
    component: CreatePromotionView,
    props: { promotionType: PromotionType.PERCENTAGE_DISCOUNT }
  }
]

export default routes
