import { Module } from 'vuex'
import { AbstractFetchService } from '@/modules/http'
import featureTogglesApiFactory from '../api/feature-toggles'
import { initialState } from './state'
import { getters } from './getters'
import { actionsFactory } from './actions'
import { mutations } from './mutations'
import type { State as RootState } from '@/modules/store'
import type { State } from './state'

export type { Getters as FeatureToggleGetters } from './getters'
export { GET_TOGGLES_GETTER, TOGGLES_LOADED_GETTER } from './getters'

export function storeFactory(
  fetchService: AbstractFetchService
): Module<State, RootState> {
  const featureTogglesApi = featureTogglesApiFactory(fetchService)
  return {
    state: initialState,
    getters,
    actions: actionsFactory(featureTogglesApi),
    mutations
  }
}
