import {
  OrderId,
  StoreAdmin,
  StoreAdminId,
  Product,
  ProductId,
  StoreCollectionId,
  StoreCollectionGroupId,
  StoreCollectionSectionId,
  StoreCollectionGroup,
  StoreCollection,
  StoreCollectionSection,
  RequestPriceUpdate,
  RequestPriceUpdateId,
  AttributeId,
  Attribute,
  StoreAddressId,
  StoresAddressesMap,
  CitiesMap,
  StoresMap
} from './resources'
import {
  PendingOrder,
  AcceptedOrder,
  CancelledOrder,
  ServedOrder
} from './resources/orders-list/order-types'

export type OrderTypes =
  | PendingOrder
  | AcceptedOrder
  | CancelledOrder
  | ServedOrder

export interface State {
  orders: Record<OrderId, OrderTypes>
  cities: CitiesMap
  stores: StoresMap
  storeAddresses: StoresAddressesMap
  storeAdmins: Record<StoreAdminId, StoreAdmin>
  products: Record<ProductId, Product>
  storeCollectionGroups: Record<StoreCollectionGroupId, StoreCollectionGroup>
  storeCollections: Record<StoreCollectionId, StoreCollection>
  storeCollectionSections: Record<
    StoreCollectionSectionId,
    StoreCollectionSection
  >
  requestPriceUpdates: Record<RequestPriceUpdateId, RequestPriceUpdate>
  attributes: AttributesDataState
}

export type AttributesDataState = {
  byId: Record<AttributeId, Attribute>
  byStoreAddressId: Record<StoreAddressId, AttributeId[]>
}

export const initialState: State = {
  orders: {},
  cities: {},
  stores: {},
  storeAddresses: {},
  storeAdmins: {},
  products: {},
  storeCollectionGroups: {},
  storeCollections: {},
  storeCollectionSections: {},
  requestPriceUpdates: {},
  attributes: {
    byId: {},
    byStoreAddressId: {}
  }
}
